<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title">Edit {{selectedStudent.fullname}}'s Details</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                  <router-link 
                    :to="{
                      name: 'InstitutionStudentDetails',
                      params: {
                        institutionID: institutionID,
                        studentID: studentID
                      }
                    }" 
                    class="btn btn-outline-danger"
                  >
                    <span class="mb-2"><i class="fas fa-times mr-2"></i></span>Cancel
                  </router-link>               
              </div>
          </div>
            <div class="row">
              <div class="col-lg-8 offset-lg-2">
                <form>
									<div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Student ID</label>
                        <input @input="updateErrorStatusOnInput('studentID')" type="text" class="form-control text-center" readonly v-model="form.studentID">
                        <small v-if="errors.studentID" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.studentID}}</small>
                      </div>
                    </div>
                  </div>         
                  <div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Fullname <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('fullname')" class="form-control" type="text" v-model="form.fullname">
                        <small v-if="errors.fullname" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.fullname}}</small>
                      </div>
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Date of Birth <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('dob')" class="form-control" type="date" v-model="form.dob">
                        <small v-if="errors.dob" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.dob}}</small>
                      </div>
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Email <span class="text-danger"></span></label>
                        <input readonly @input="updateErrorStatusOnInput('email')" class="form-control" type="email" v-model="form.email">
                        <small v-if="errors.email" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.email}}</small>
                      </div>
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Gender</label>
                        <select @change="updateErrorStatusOnInput('gender')" class="form-control select" v-model="form.gender">
                          <option value="">Select Gender</option>
                          <option value="male">Male</option>
                          <option value="female">Female</option>
                        </select>
                        <small v-if="errors.gender" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.gender}}</small>
                      </div>                      
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Phone</label>
                        <input @input="updateErrorStatusOnInput('phone')" class="form-control" type="text" v-model="form.phone">
                        <small v-if="errors.phone" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.phone}}</small>
                      </div>
                    </div>                    
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Address</label>
                        <textarea @input="updateErrorStatusOnInput('address')" class="form-control" v-model="form.address"></textarea>
                        <small v-if="errors.address" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.address}}</small>
                      </div>
                    </div>
                  </div>
                  <hr class="mb-3">
                  <div class="form-group text-center">
                    <button v-if="!isLoading" @click.prevent="attemptStudentRegister" class="btn btn-lg btn-success btn-block">Update Student Record</button>
                    <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                      <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                      Loading...      
                    </button>
                  </div>
                </form>
              </div>
            </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'

  export default {
    name: "RegisterStudent",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'registered institutions',
        institutionID: null,
        studentID: null,
        institution: {},
        selectedStudent: {},
        isLoading: false,
        selectedFile: null,
        form: {
            studentID: '',
            fullname: '',
            email: '',
            gender: '',
            phone: '',
            avatar: '',
            dob: '',
            address: '',
            userRole: 'student',
            accountType: 'student account',
        },
        errors: {
            studentID: '',
            fullname: '',
            email: '',
            phone: '',
            avatar: '',
            dob: '',
            address: '',
            gender: '',
        }
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      validateForm() {
        if (!this.form.studentID) {
            this.errors.studentID = "Student ID is required!"
        }
        if (!this.form.fullname) {
            this.errors.fullname = "Fullname is required!"
        }
        if (!this.form.email) {
            this.errors.email = "Email is required!"
        }
        if (!this.form.phone) {
            this.errors.phone = "Phone number is required!"
        }
        if (!this.form.address) {
            this.errors.address = "Address is required!"
        }
        if (!this.form.gender) {
            this.errors.gender = "Gender is required!"
        }
        if (!this.form.dob) {
            this.errors.dob = "Date of Birth is required!"
        }
        if(this.form.studentID && this.form.fullname && this.form.email && this.form.gender && this.form.phone  && this.form.address && this.form.dob) {
            return true
        } else {
            return false;
        }
      },

      updateErrorStatusOnInput(fieldName) {
        if(this.form[fieldName].toString().length > 0){
          this.errors[fieldName] = ""
          this.errors.message = ""
        }
      },

      attemptStudentRegister() {
        this.isLoading = true
        if (!this.validateForm()) {
          this.isLoading = false; 
          return;
        }

        if (confirm(`Do you want proceed with ${this.selectedStudent.fullname} record update?`)) {
          db.collection("students").where('studentID', '==', this.studentID).get()
            .then( querySnapshot => {
              querySnapshot.forEach( doc => {
                doc.ref.update({
                  'fullname': this.form.fullname,
                  'dob': new Date(this.form.dob).getTime(),
                  'phone': this.form.phone,
                  'address': this.form.address,
                  'gender': this.form.gender,
                  'userRole': this.form.userRole,
                  'accountType': this.form.accountType,
                })
                    this.isLoading = false
                    this.$router.push({
                        name: 'InstitutionStudentDetails',
                        params: {
                            institutionID: this.institutionID,
                            studentID: this.studentID
                        }
                    })
                })
            }).catch(error => {
            this.isLoading = false
            alert(error)
            })
         }
      },

      setDate(date){
        let day = ("0" + date.getDate()).slice(-2);
        let month = ("0" + (date.getMonth() + 1)).slice(-2);
        return date.getFullYear()+"-"+(month)+"-"+(day) ;
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    created() {
      this.institutionID = this.$route.params.institutionID
      this.studentID = this.$route.params.studentID

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

        db.collection("students").where("studentID", "==", this.studentID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              this.form.studentID = doc.data().studentID
              this.selectedStudent.studentID = doc.data().studentID
              this.form.institutionID =  doc.data().institutionID
              this.selectedStudent.institutionID =  doc.data().institutionID
              this.form.fullname = doc.data().fullname,
              this.selectedStudent.fullname = doc.data().fullname,
              this.form.dob =  this.setDate(new Date(doc.data().dob)),
              this.selectedStudent.dob =  doc.data().dob,
              this.form.email = doc.data().email,
              this.selectedStudent.email = doc.data().email,
              this.form.phone = doc.data().phone,
              this.selectedStudent.phone = doc.data().phone,
              this.form.gender = doc.data().gender,
              this.selectedStudent.gender = doc.data().gender,
              this.form.address = doc.data().address,
              this.selectedStudent.address = doc.data().address,
              this.form.userRole = doc.data().userRole,
              this.selectedStudent.userRole = doc.data().userRole,
              this.form.accountType = doc.data().accountType,
              this.selectedStudent.accountType = doc.data().accountType,
              this.form.createdOn = doc.data().createdOn
              this.selectedStudent.createdOn = doc.data().createdOn                                                                                                                                                 
            })
          })
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
