<template>
    <div class="row">
        <div class="col-md-6 offset-md-3">
            <form>
                <div class="row">
                <div class="col-sm-12">
                    <h3 class="h4 text-center pt-3 pb-2">Add Student</h3>
                    <div class="form-group">
                    <label></label>
                    <select @change="updateErrorStatusOnInput('student')" class="form-control select" v-model="form.student">
                        <option value="">Select Student</option>
                        <option v-for="student in students" :key="student.studentID" :value="student.studentID">{{student.fullname}}</option>
                    </select>
                    <small v-if="errors.student" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.student}}</small>
                    </div>                      
                </div>
                </div>
                <hr class="mb-3">
                <div class="form-group text-center">
                <button v-if="!isLoading" @click.prevent="attemptAddStudentToClassroom" class="btn btn-lg btn-success btn-block">Add Student</button>
                <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                    <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                    Loading...      
                </button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
  import { db } from '../../firebase/firebaseInit'

  export default {
    name: "AddStudentToClassroom",
    props: {
        students: {
            type: Array,
            required: true
        }
    },

    data() {
      return {
          isLoading: false,
          institutionID: "",
          classroomID: "",
          form: {
            student: ""
          },
          errors: {
              
          }
      }
    },

    methods: {
      validateForm() {
        if (!this.form.student) {
            this.errors.student = "Student record MUST be selected!"
        }
        if(this.form.student ){
            return true
        } else {
            return false
        }
      },

      async attemptAddStudentToClassroom() {
        this.isLoading = true
        if (!this.validateForm()) {
          this.isLoading = false; 
          return;
        }
        let canAddStudentToClassroom = await this.getStudentClassroomStatus(this.form.student)
        if (canAddStudentToClassroom) {
          const student = {
            'classroomID': this.classroomID,
            'studentID': this.form.student,
            'createdOn': new Date().getTime()
          }
            db.collection("classroom_students").add(student).then((res) => {
              console.log(res)
              this.isLoading = false
              window.EventBus.emit('student-added-to-classroom', student);
            }).catch(error => {
              this.isLoading = false
              alert(error)
            });
        } else {
            window.EventBus.emit('student-added-to-classroom', {});
        }
      },

      updateErrorStatusOnInput(fieldName) {
        if(this.form[fieldName].toString().length > 0){
          this.errors[fieldName] = ""
          this.errors.message = ""
        }
      },

      async getStudentClassroomStatus(studentID) {
        const temp = []
        await db.collection("classroom_students").where("studentID", "==", studentID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              const data = {
                'id': doc.id,
                'classroomID': doc.data().classroomID,
                'studentID': doc.data().studentID,
              }                                                                                                           
              temp.push(data)
            })
          })

        if (temp.length <= 0) {
            return true
        } else {
            return false
        }
      }
    },

    created() {
        this.institutionID = this.$route.params.institutionID
        this.classroomID = this.$route.params.classroomID   
    }
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");
</style>
