<template>
    <div class="row">
        <div class="col-md-6 offset-md-3">
            <form>
                <div class="row">
                <div class="col-sm-12">
                    <h3 class="h4 text-center pt-3 pb-2">Add Course</h3>
                    <div class="form-group">
                    <label></label>
                    <select @change="updateErrorStatusOnInput('course')" class="form-control select" v-model="form.course">
                        <option value="">Select Course</option>
                        <option v-for="course in courses" :key="course.courseID" :value="course.courseID">{{course.title}} ({{course.code}})</option>
                    </select>
                    <small v-if="errors.course" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.course}}</small>
                    </div>                      
                </div>
                </div>
                <hr class="mb-3">
                <div class="form-group text-center">
                <button v-if="!isLoading" @click.prevent="attemptAddCourseToClassroom" class="btn btn-lg btn-success btn-block">Add Course</button>
                <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                    <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                    Loading...      
                </button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
  import { db } from '../../firebase/firebaseInit'

  export default {
    name: "AddCourseToClassroom",
    props: {
        courses: {
            type: Array,
            required: true
        }
    },

    data() {
      return {
          isLoading: false,
          institutionID: "",
          classroomID: "",
          form: {
            course: ""
          },
          errors: {

          }
      }
    },

    methods: {
      validateForm() {
        if (!this.form.course) {
            this.errors.course = "Course record MUST be selected!"
        }
        if(this.form.course ){
            return true
        } else {
            return false
        }
      },

      async attemptAddCourseToClassroom() {
        this.isLoading = true
        if (!this.validateForm()) {
          this.isLoading = false; 
          return;
        }
        let canAddStudentToClassroom = await this.getCourseClassroomStatus(this.form.course)
        if (canAddStudentToClassroom) {
          const course = {
            'classroomID': this.classroomID,
            'courseID': this.form.course,
            'createdOn': new Date().getTime()
          }

          db.collection("classroom_courses").add(course).then((res) => {
            console.log(res)
            this.isLoading = false
            window.EventBus.emit('course-added-to-classroom', course);
          }).catch(error => {
            this.isLoading = false
            alert(error)
          });
        } else {
          window.EventBus.emit('course-added-to-classroom', {});
        }
      },

      updateErrorStatusOnInput(fieldName) {
        if(this.form[fieldName].toString().length > 0){
          this.errors[fieldName] = ""
          this.errors.message = ""
        }
      },

      async getCourseClassroomStatus(courseID) {
        const temp = []
        await db.collection("classroom_courses").where("courseID", "==", courseID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              const data = {
                'id': doc.id,
                'classroomID': doc.data().classroomID,
                'courseID': doc.data().courseID,
              }                                                                                                           
              temp.push(data)
            })
          })

        if (temp.length <= 0) {
            return true
        } else {
            return false
        }
      }
    },

    created() {
        this.institutionID = this.$route.params.institutionID
        this.classroomID = this.$route.params.classroomID
    }
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");
</style>
