<template>
<div>
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-6 col-3">
                  <h4 class="page-title">{{course.title}} ({{course.code}})'s Examination Questions</h4>
              </div>
              <div class="col-sm-6 text-right m-b-20">
                  <router-link
                    class="btn btn-outline-success float-right"
                    :to="{
                      name: 'InstitutionAddExaminationQuestion',
                      params: {
                        institutionID: institutionID,
                        classroomID: classroomID,
                        examinationID: examinationID,
                        timetableID: timetableID,
                        courseID: courseID
                      }                                                
                    }"
                  ><i class="fas fa-plus mr-2"></i>Add Question</router-link>     

                  <router-link
                    class="btn btn-outline-danger float-right mr-2"
                    :to="{
                      name: 'InstitutionClassroomDetails',
                      params: {
                        institutionID: institutionID,
                        classroomID: classroomID
                      }                                                
                    }"
                  ><i class="fas fa-times mr-2"></i>Back to Course Details</router-link>   

                  <router-link
                    class="btn btn-outline-secondary float-right mr-2"
                    :to="{
                      name: 'InstitutionExaminationCourseQuestionInstructions',
                      params: {
                        institutionID: institutionID,
                        classroomID: classroomID,
                        examinationID: examinationID,
                        timetableID: timetableID,
                        courseID: courseID
                      }                                                
                    }"
                  ><i class="fas fa-chevron-left mr-2"></i>Back</router-link>
              </div>
          </div>
          <div v-if="examinationQuestions.length <= 0" class="row">
              <div class="col-sm-8 offset-sm-2 mt-5">
                <div class="jumbotron text-center">
                    <h3 class="h3">{{emptyMessage}}</h3>
                </div>
              </div>
          </div>
          <div v-else class="container-fluid">
            <div class="row filter-row">
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Examination Question ID</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.classroomID">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Question</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.name">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Options</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.grade">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                  <a @click.prevent="searchClassrooms" class="btn btn-success btn-block text-white"> Search </a>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12">
                <div v-for="(question, index) in examinationQuestions"  :key="index" class="card">
                  <div class="row">
                    <div class="card-body col-sm-1">
                      <div class="serial-number item-font"><strong>{{index+1}}</strong></div>
                    </div>
                    <div class="card-body col-sm-10">
                      <div class="row pr-3 pl-3">
                        <div class="">
                          {{question.examQuestion}}
                        </div>
                      </div>
                      <div class="row">
                        <form class="col-12 mt-5">
                          <div class="row">
                            <div class="col-sm-6">
                              <div class="form-group">
                                <input :value="' A :  ' + question['A']" class="form-control bg-white" type="text" readonly>
                              </div>
                            </div>
                            <div class="col-sm-6">
                              <div class="form-group">
                                <input :value="' B :  ' + question['B']" class="form-control bg-white" type="text" readonly>
                              </div>
                            </div>
                            <div class="col-sm-6">
                              <div class="form-group">
                                <input :value="' C :  ' + question['C']" class="form-control bg-white" type="text" readonly>
                              </div>
                            </div>
                            <div class="col-sm-6">
                              <div class="form-group">
                                <input :value="' D :  ' + question['D']" class="form-control bg-white" type="text" readonly>
                              </div>
                            </div>
                          </div>
                        </form>
                      </div>
                      <div class="row">
                        <div class="answer-box mt-3">
                          <span>Answer</span><span class="item-font"><strong>{{question.answer}}</strong></span>
                        </div>
                      </div>
                    </div>
                    <div class="card-body col-sm-1">
                     <div class="action-box">
                       <button @click.prevent="attemptEditingExaminationQuestion(question.examinationQuestionID)" class="btn btn-warning mb-3"><i class="fa fa-edit fa-2x"></i></button>
                       <button @click.prevent="attemptDeleteExaminationQuestion(question.examinationQuestionID)" class="btn btn-danger mt-3"><i class="fa fa-trash fa-2x"></i></button>
                     </div>
                    </div>
                  </div>
                </div>                
              </div>
            </div>
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'
  import { isEmptyObject } from '../../helpers/isEmptyObject'
  import CryptoJS from 'crypto-js'

  export default {
    name: "ExaminationQuestions",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        isLoading: false,
        active: 'registered institutions',
        institutionID: null,
        classroomID: null,
        courseID: null,
        examinationID: null,
        timetableID: null,
        institution: {},
        course: {},
        question: {},
        examinationQuestions: [],

        search: {

        },
        emptyMessage: "Examination Question list is empty."
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      attemptEditingExaminationQuestion(examinationQuestionID) {
        this.$router.push({
          name: "InstitutionEditExaminationCourseQuestion",
          params: {
            institutionID: this.institutionID,
            classroomID: this.classroomID,
            examinationID: this.examinationID,
            timetableID: this.timetableID,
            courseID: this.courseID,
            examinationQuestionID: examinationQuestionID
          }
        })
      },

      attemptDeleteExaminationQuestion(examinationQuestionID) {
        if (confirm(`Delete examination question?`)) {
          db.collection("examination_questions").where('examinationQuestionID', '==', examinationQuestionID).get()
            .then( querySnapshot => {
              querySnapshot.forEach( doc => {
                  doc.ref.delete()
                  this.examinationQuestions.map(question => {
                    if (question.examinationQuestionID === examinationQuestionID) {
                      const index = this.examinationQuestions.indexOf(question)
                      this.examinationQuestions.splice(index, 1)
                    }
                  })
              })
            })          
        }
      },      
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    created() {
      this.institutionID = this.$route.params.institutionID
      this.classroomID = this.$route.params.classroomID
      this.courseID = this.$route.params.courseID
      this.examinationID = this.$route.params.examinationID
      this.timetableID = this.$route.params.timetableID

      db.collection("courses").where("courseID", "==", this.courseID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              this.course.id = doc.id,
              this.course.courseID = doc.data().courseID,
              this.course.institutionID = doc.data().institutionID,
              this.course.title = doc.data().title,
              this.course.grade = doc.data().grade,
              this.course.code = doc.data().code,
              this.course.duration = doc.data().duration,
              this.course.durationUnit = doc.data().durationUnit,
              this.course.instructor = doc.data().instructor,
              this.course.description = doc.data().description,
              this.course.createdOn = doc.data().createdOn                                                                                                         
            })
          })

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

        db.collection("questions").where("timetableID", "==", this.timetableID).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              if (doc.data().institutionID === this.institutionID && this.classroomID === doc.data().classroomID && doc.data().examinationID === this.examinationID && doc.data().courseID === this.courseID) {
                this.question.questionID = doc.data().questionID
                this.question.institutionID = doc.data().institutionID
                this.question.classroomID = doc.data().classroomID
                this.question.courseID = doc.data().courseID
                this.question.examinationID = doc.data().examinationID
                this.question.timetableID = doc.data().timetableID
                this.question.createdOn = doc.data().createdOn
              }
            })
            if (!isEmptyObject(this.question)) {
              db.collection("examination_questions").where("questionID", "==", this.question.questionID).get()
              .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                      'questionID': doc.data().questionID,
                      'examinationQuestionID': doc.data().examinationQuestionID,
                      'examQuestion': CryptoJS.enc.Base64.parse(doc.data().examQuestion).toString(CryptoJS.enc.Utf8),
                      'A': CryptoJS.enc.Base64.parse(doc.data()['A']).toString(CryptoJS.enc.Utf8),
                      'B': CryptoJS.enc.Base64.parse(doc.data()['B']).toString(CryptoJS.enc.Utf8),
                      'C': CryptoJS.enc.Base64.parse(doc.data()['C']).toString(CryptoJS.enc.Utf8),
                      'D': CryptoJS.enc.Base64.parse(doc.data()['D']).toString(CryptoJS.enc.Utf8),
                      'answer': CryptoJS.enc.Base64.parse(doc.data().answer).toString(CryptoJS.enc.Utf8),
                      'selectedOption': CryptoJS.enc.Base64.parse(doc.data().selectedOption).toString(CryptoJS.enc.Utf8),
                      'extraInformation': doc.data().extraInformation,
                      'createdOn': doc.data().createdOn
                    }                                                                                                                 
                    this.examinationQuestions.push(data)
                  })
                })
            }
          })
    }
  }
</script>

<style>
    .dashboard {
      width: 100%;
    }

    .action-box {
      width: 100%;
      height: 100%;
      display:flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    .answer-box {
      width: 100%;
      height: 100%;
      display:flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    .serial-number{
      width: 100%;
      height: 100%;
      display:flex;
      align-items: center;
      justify-content: center;     
    }

    .item-font {
      font-size: 25px;
    }
</style>
