<template>
  <div class="main-wrapper account-wrapper">
    <div class="account-page">
      <div class="account-center">
        <div class="account-box">
        <form  class="form-signin">
            <div class="account-logo">
              <router-link 
                :to="{ name: 'RegisterRootAdministratorAccount' }"
              >
                <div class="mb-2"><i class="fas fa-user-cog fa-5x"></i></div>
                <div class="h4">Root Administrator Account</div>
              </router-link>
            </div>
            <div class="form-group">
                <label>Fullname</label>
                <input type="text" class="form-control" v-model="fullname" readonly>
            </div>
            <div class="form-group">
                <label>Email Address</label>
                <input type="text" class="form-control" v-model="email" readonly>
            </div>
            <div class="form-group">
                <label>Password</label>
                <input type="text" class="form-control" v-model="password" readonly>
            </div>
            <div class="form-group">
                <label>Role</label>
                <input type="text" class="form-control" v-model="userRole" readonly>
            </div>
            <div class="form-group">
                <label>Account Type</label>
                <input type="text" class="form-control" v-model="accountType" readonly>
            </div>
            <div class="form-group text-center">
                <button v-if="!isLoading" @click.prevent="attemptRegisteration" class="btn btn-lg btn-success btn-block">Register</button>
                <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                Loading...      
                </button>
            </div>
            <div class="text-center login-link">
                Account already exist? <router-link :to="{name: 'AdministratorLogin'}">Login</router-link>
            </div>
        </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import { db, auth } from '../../firebase/firebaseInit'
    import { isEmptyObject } from '../../helpers/isEmptyObject'

    import { uuid } from 'vue-uuid';

    export default {
      name: "RegisterRootAdministratorAccount",

      data() {
        return {
          isLoading: false,

          fullname: "System Administrator",
          email: "root@obeausers.com",
          password: "P@$$w0rd",
          userRole: 'super user',
          accountType: 'system administrator',
          
          root: {}
        }
      },

      methods: {
        attemptRegisteration() {
            this.isLoading = true
            if (isEmptyObject(this.root)) {
                auth.createUserWithEmailAndPassword(this.email, this.password)
                    .then(() => {
                        const userID = uuid.v1()
                        db.collection("system_administrators").add({
                            'userID': userID,
                            'fullname': this.fullname,
                            'email': this.email,
                            'userRole': this.userRole,
                            'accountType': this.accountType,
                            'phone': '---',
                            'createdOn': new Date().getTime()
                        }).then((res) => {
                          console.log(res);
                          
                          const accountID = uuid.v4()
                          db.collection("chat_accounts").add({
                              'accountID': accountID,
                              'userID': userID,
                              'accountType': 'private',
                              'userType': this.userRole,
                              'referenced_db': 'system_administrators',
                              'referenceName': ''
                          }).then((res) => {
                            console.log(res);
                          }).catch(error => {
                              this.isLoading = false
                              alert(error.message)
                          })

    
                          const userID2 = uuid.v1()
                          db.collection("system_administrators").add({
                            'userID': userID2,
                            'fullname': "OBEA General",
                            'email': 'general@obeausers.com',
                            'userRole': 'obea chat channel',
                            'accountType': this.accountType,
                            'phone': '---',
                            'createdOn': new Date().getTime()
                          }).then((res) => {
                            console.log(res);
                          }).catch(error => {
                              this.isLoading = false
                              alert(error.message)
                          })

                          const accountID2 = uuid.v4()
                          db.collection("chat_accounts").add({
                              'accountID': accountID2,
                              'userID': userID2,
                              'accountType': 'general',
                              'userType': 'obea chat channel',
                              'referenced_db': 'system_administrators',
                              'referenceName': 'OBEA#General'
                          }).then((res) => {
                            console.log(res);
                            this.isLoading = false
                            this.$router.push({name: 'AdministratorLogin'})
                          }).catch(error => {
                              this.isLoading = false
                              alert(error.message)
                          })
                        }).catch(error => {
                            this.isLoading = false
                            alert(error.message)
                        })
                    },
                    
                    error => {
                        this.isLoading = false
                        alert(error.message)
                    })
            } else {
                alert(`Welcome ${this.root.fullname}, please login with the email and password showing on the form.`)
            }
        }
      },

      created() {
          db.collection("system_administrators").where("email", "==", this.email).get()
            .then( querySnapshot => {
                querySnapshot.forEach( doc => {
                    this.root.userID = doc.data().userID
                    this.root.fullname = doc.data().fullname
                    this.root.email = doc.data().email
                    this.root.userRole = doc.data().userRole
                    this.root.accountType = doc.data().accountType
                })
            }).catch()
      }
    }
</script>

<style lang="scss" scoped>
    @import url("../../assets/css/style.css");
    @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");
</style>
