<template>
    <div class="container-fluid mb-4">
        <div class="row">
            <div class="col-sm-8 offset-md-2 mb-3">
                <h5 class="h4 text-center">Add Paragraph</h5>
                <textarea @input="updateErrorStatusOnInput('paragraphString')" class="form-control" required v-model="form.paragraphString"></textarea>
                <small v-if="errors.paragraphString" class="form-text text-danger d-flex justify-content-center mb-0">{{errors.paragraphString}}</small>
            </div>
            <div class="col-sm-6 offset-md-3 mb-2">
                <button @click.prevent="attemptAddingUIParagraph" class="btn btn-block btn-success">Add UI Paragraph</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "addinstruction-paragraph",

        props: {
            tagName: {
                type: String,
                required: true
            }
        },

        data() {
            return {
                form: {
                    paragraphString: "",
                }, 
                errors: {
                    paragraphString: "",
                }
            }
        },

        methods: {
            attemptAddingUIParagraph() {
                if (!this.validateForm()) {
                  return;
                }

                const elementObject = {}
                elementObject.tagName = this.tagName
                elementObject.value = this.form.paragraphString
                elementObject.elementString = `<${this.tagName}>${this.form.paragraphString}</${this.tagName}>`
                window.EventBus.emit('instruction-ui-element-added', elementObject)
            },

            validateForm() {
                if (!this.form.paragraphString) {
                    this.errors.paragraphString = "Paragraph details is required!"
                }
                if(this.form.paragraphString) {
                    return true
                } else {
                    return false
                }
            },

            updateErrorStatusOnInput(fieldName) {
              if(this.form[fieldName].toString().length > 0){
                this.errors[fieldName] = ""
                this.errors.message = ""
              }
            }            
        }
    }
</script>