<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div v-if="showUI" class="col-sm-4 col-3">
                  <h4 class="h3 mt-3">{{course.title}} ({{course.code}}) Examination</h4>
              </div>
              <div v-if="showUI" class="col-sm-8 col-9 text-right m-b-20">
                <div class="row">
                  <div class="col-sm-6 text-center">
                    <button v-if="examinationState.examinationStarted && !examinationState.examinationEnded" @click="attemptSubmittingExamination" class="btn btn-lg btn-danger">
                      <span class="h3">Submit</span>
                    </button>                    
                  </div>
                  <div class="col-sm-6">
                    <small class="text-muted"><strong>Examination Timer</strong></small>
                    <h3 class="h3 mt-2">{{ getTimeInformationBaseOnExaminationState() }}</h3>
                  </div>
                </div>
              </div>
          </div>
          <div v-if="!examinationState.examinationStarted && !examinationState.examinationEnded" class="row">
            <h3 v-if="showUI" class="h2 text-center col-12">{{questionInstruction.title}}</h3>
            <h4 v-if="showUI" class="h4 text-center col-12">( {{pageFormatMonthYear(examination.startDate)}} )</h4>
            <div v-for="(uiElement, index) in questionInstruction.details" :key="index" class="container ui-item-container mt-2 b mb-2">
              <div class="mt-0" v-html="uiElement.elementString"></div>
            </div>
            <div v-if="showUI" class="col-12 text-center mb-5 mt-5 pb-5">
              <button @click="startExamination" class="btn btn-lg btn-success pr-5 pl-5"><i class="fas fa-sign-in-alt fa-2x mr-3"></i> <span class="h3">Start Examination</span></button>
            </div>
          </div>
          <div v-if="examinationState.examinationStarted && !examinationState.examinationEnded" class="row">
            <h5 v-if="showUI" class="h4 col-12 mt-5 text-center text-danger">*** <strong>Attempt</strong> all questions ***</h5>
             <small v-if="showUI" class="h5 col-12 text-center text-muted"><strong>{{examinationState.currentViewIdex+1}} of {{getExaminationQuestions.length}}</strong></small> 
              <div
                v-for="(question, index) in getExaminationQuestions"
                :key="question.examinationQuestionID"  
                class="col-md-8 offset-md-2 mt-5 mb-5"
                :style="{'display': index === examinationState.currentViewIdex ? 'block' : 'none'}"           
              >  
                <div 
                  class="card"
                  v-if="index === examinationState.currentViewIdex"
                >
                  <div v-if="showUI" class="card-body mb-5 pb-5">
                    <h5 class="card-title mt-5">Question {{(index+1)}}</h5>
                    <p class="card-text mb-5">{{question.examQuestion}}</p>

                    <div class="col-12">
                      <div class="row">
                        <div class="col-md-6">
                          <div class="form-check">
                            <input @click="attemptAnswerSelectionUpdate(index, 'A')" class="form-check-input" type="radio" :name="question.examinationQuestionID" value="A" v-model="question.selectedOption">
                            <label class="form-check-label">
                              <span class="mr-3">A:</span>{{question['A']}}
                            </label>
                          </div>
                        </div>
                        <div class="col-md-6">
                          <div class="form-check">
                            <input @click="attemptAnswerSelectionUpdate(index, 'B')" class="form-check-input" type="radio" :name="question.examinationQuestionID" value="B" v-model="question.selectedOption">
                            <label class="form-check-label">
                              <span class="mr-3">B:</span>{{question['B']}}
                            </label>
                          </div>
                        </div>
                        <div class="col-md-6 mt-3">
                          <div class="form-check">
                            <input @click="attemptAnswerSelectionUpdate(index, 'C')" class="form-check-input" type="radio" :name="question.examinationQuestionID" value="C" v-model="question.selectedOption">
                            <label class="form-check-label">
                              <span class="mr-3">C:</span>{{question['C']}}
                            </label>
                          </div>
                        </div>
                        <div class="col-md-6 mt-3">
                          <div class="form-check">
                            <input @click="attemptAnswerSelectionUpdate(index, 'D')" class="form-check-input" type="radio" :name="question.examinationQuestionID" value="D" v-model="question.selectedOption">
                            <label class="form-check-label">
                              <span class="mr-3">D:</span>{{question['D']}}
                            </label>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
            </div>
            <div v-if="showUI" class="col-6 text-center">
              <button 
                :disabled="!isPrevEnabled" 
                class="btn btn-lg"
                :class="{
                  'btn-success': isPrevEnabled,
                  'btn-secondary': !isPrevEnabled
                }"
                @click="attemptPrevNextUpdate(-1)"
              >
                <i class="fas fa-chevron-left fa-2x mr-3"></i><span class="h3">Previous</span>
              </button>
            </div>
            <div v-if="showUI" class="col-6 text-center">
              <button 
                :disabled="!isNextEnabled" 
                class="btn btn-lg"
                :class="{
                  'btn-success': isNextEnabled,
                  'btn-secondary': !isNextEnabled
                }"
                @click="attemptPrevNextUpdate(1)"
              >
                <span class="h3">Next</span><i class="fas fa-chevron-right fa-2x ml-3"></i>
              </button>
            </div>
          </div>
          <div v-if="examinationState.examinationStarted && examinationState.examinationEnded" class="col-md-8 offset-md-2">
            <h3 v-if="showUI" class="h2 text-center col-12">Examination Report Summary</h3>
            <div class="row mb-3">
              <div class="col-md-12">
                  <ul class="list-group list-group-flush">
                      <li class="list-group-item transparent-bg">
                          <div class="row">
                              <div class="col-3">Student ID</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{studentID}}</strong></div>
                          </div>
                      </li>
                      <li class="list-group-item transparent-bg">
                          <div class="row">
                              <div class="col-3">Student Fullname</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{loggedUserFullname}}</strong></div>
                          </div>
                      </li>
                      <li class="list-group-item transparent-bg">
                          <div class="row">
                              <div class="col-3">Classroom</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ classroom.name }}</strong></div>
                          </div>
                      </li>
                      <li class="list-group-item transparent-bg">
                          <div class="row">
                              <div class="col-3">Course</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ course.title }} ({{course.code}})</strong></div>
                          </div>
                      </li>
                      <li class="list-group-item transparent-bg">
                          <div class="row">
                              <div class="col-3">Examination</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{examination.title}}</strong></div>
                          </div>
                      </li>
                  </ul>
                  <h3 class="h3 text-center mt-5">Examination Grading System</h3>
                    <table class="table table-light">
                      <thead>
                        <tr>
                          <th scope="col">Metric</th>
                          <th scope="col">Score</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>Correct Answers</td>
                          <td>{{ examinationState.correctAnswer }}</td>
                        </tr>
                        <tr>
                          <td>Wrong Answers</td>
                          <td>{{ examinationState.wrongAnswer }}</td>
                          <td></td>
                        </tr>
                        <tr>
                          <td>Score</td>
                          <td>{{ examinationState.score }}</td>
                        </tr>
                        <tr>
                          <td>Percentage</td>
                          <td>{{ examinationState.percentageScore }} %</td>
                        </tr>
                        <tr>
                          <td>Grade</td>
                          <td>{{ examinationState.grade }}</td>
                        </tr>
                      </tbody>
                    </table>
              </div>              
            </div>
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'
  import { isEmptyObject } from '../../helpers/isEmptyObject'
  import { customizeDate } from '../../helpers/customizeDate'

  import uuid from 'uuid'
  import CryptoJS from 'crypto-js'

  export default {
    name: "StudentSelectedCourseExaminationPage",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'registered institutions',
        institutionID: "",
        studentID: "",
        classroomID: "",
        examinationID: "",
        timetableID: "",
        courseID: "",

        institution: {},
        course: {},
        examination: {},
        examinationQuestions: [],
        questionInstruction: [],
        timetable: {},
        examinationPaperDetails: {},
        targetQuestion: {},
        classroom: {},

        showUI: false,
        countDownClockForExamination: "",
        intervalPlaceholder: null,

        paperID: ""
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },

      examinationState() {
        return this.$store.getters.examinationState
      },

      isPrevEnabled() {
        return this.getCurrentIndex > this.getFirstIndex
      },

      isNextEnabled() {
        return this.getCurrentIndex < (this.getLastIndex-1)
      },

      getFirstIndex() {
        return this.examinationState.firstViewIndex
      },

      getCurrentIndex() {
        return this.examinationState.currentViewIdex
      },

      getLastIndex() {
        return this.examinationState.lastViewIndex
      },

      getExaminationQuestions() {
        return this.examinationState.examinationDetails
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      attemptPrevNextUpdate(prevNextParama) {
        const examinationState = this.examinationState
        examinationState.currentViewIdex += prevNextParama
        if (examinationState.currentViewIdex <= examinationState.firstViewIndex) {
          examinationState.currentViewIdex = examinationState.firstViewIndex
        } else if (examinationState.currentViewIdex >= examinationState.lastViewIndex) {
          examinationState.currentViewIdex = (examinationState.lastViewIndex - 1)
        }
        this.$store.dispatch('UPDATE_EXAMINATION_STATE', examinationState)
        .then(() => {
            console.log("update data", this.examinationState)
        }).catch(() => {
          alert("Examination start attempt failed. Please try again.")
        })
      },

      attemptSubmittingExamination() {
        if (confirm("Do you want submit your examination for grading?")) {
          this.submitExaminationForGrading()
        }
      },

      attemptAnswerSelectionUpdate(index, option) {
        const examinationState = this.examinationState
        examinationState.examinationDetails[index].selectedOption = option
        this.$store.dispatch('UPDATE_EXAMINATION_STATE', examinationState)
        .then(() => {
            console.log(this.examinationState)
        }).catch(() => {
          alert("Examination start attempt failed. Please try again.")
        })        
      },

      submitExaminationForGrading() {
        let correctAnswer = 0
        let wrongAnswer = 0
        this.examinationState.examinationDetails.map((examinationQuestion) => {
            if (examinationQuestion.answer === examinationQuestion.selectedOption) {
              correctAnswer+=1
            } else {
              wrongAnswer+=1
            }
        })
        let score = correctAnswer/(correctAnswer+wrongAnswer)
        let percentageScore = score*100
        let grade = this.gradeStudentPerformance(percentageScore)

        if (this.targetQuestion.questionID) {
            db.collection("student_examination_grades").add({
            'studentExaminationGradeID': uuid.v1(),
            'institutionID': this.institutionID,
            'studentID': this.studentID,
            'classroomID': this.classroomID,
            'examinationID': this.examinationID,
            'timetableID': this.timetableID,
            'courseID': this.courseID,
            'questionID': this.targetQuestion.questionID,
            'examinationDetails': JSON.stringify(this.examinationState.examinationDetails),
            "paperDetails": this.examinationPaperDetails,
            'correctAnswer': correctAnswer,
            'wrongAnswer': wrongAnswer,
            'score': score,
            'percentageScore': percentageScore,
            'grade': grade,
            'createdOn': new Date().getTime()
          }).then((res) => {
            console.log(res)

            const examinationState = this.examinationState
            examinationState.examinationStarted = true
            examinationState.examinationEnded = true
            examinationState.correctAnswer =  correctAnswer
            examinationState.wrongAnswer =  wrongAnswer
            examinationState.score =  score
            examinationState.percentageScore =  percentageScore
            examinationState.grade =  grade
            this.$store.dispatch('UPDATE_EXAMINATION_STATE', examinationState)
            .then(() => {
                console.log(this.examinationState)
            }).catch(() => {
              alert("Examination start attempt failed. Please try again.")
            })        

          }).catch(error => {
            this.isLoading = false
            alert(error)
          });
        } else {
          console.log("invalid Question ID field. Please try again")
        }       
      },

      gradeStudentPerformance(percentageScore) {
        if (percentageScore >= 70 && percentageScore <= 100) {
          return `First Class Honours`
        } else if (percentageScore >= 60 && percentageScore < 70) {
          return `Upper Second Class Honours (2.1)`
        } else if (percentageScore >= 50 && percentageScore < 60) {
          return `Lower Second Class Honours (2.2)`
        } else if (percentageScore >= 40 && percentageScore < 50) {
          return `Class 3 Honours/Pass`
        } else if (percentageScore >= 0 && percentageScore < 40) {
          return `Fail`
        } else {
          return `Invalid Grade`
        }
      },

      isEmptyObject(obj) {
          return isEmptyObject(obj)
      },

      pageFormatMonthYear(timestamp) {
        if (timestamp) {
          let stringDate = new Date(parseInt(timestamp)).toLocaleString('en', { day: 'numeric', month: 'long', year: 'numeric' })
          stringDate = stringDate.split(" ")
          stringDate.splice(1, 1)
          stringDate = stringDate.join(", ").trim().toUpperCase()
          return stringDate
        }
        return ""
      },

      getTimeInformationBaseOnExaminationState() {
        if (!this.examinationState.examinationStarted && !this.examinationState.examinationEnded) {
          let hrs = parseInt((this.examinationState.examinationDurationInMinutes / 60), 10)
          let mins = this.examinationState.examinationDurationInMinutes % 60
          this.countDownClockForExamination = `${hrs}:${mins}:00`
          return this.countDownClockForExamination
        } else if (this.examinationState.examinationStarted && !this.examinationState.examinationEnded) {
          return this.countDownClockForExamination
        } else if (this.examinationState.examinationStarted && this.examinationState.examinationEnded) {
          this.countDownClockForExamination = `00:00:00`
          return this.countDownClockForExamination
        }
      },

      startTimner(vueInstance) {
        this.intervalPlaceholder = setInterval(function() {
          // Get Date from timestamp
          let examinationDate = customizeDate(parseInt(vueInstance.examinationState.examination.paperID.split("-")[0]))
          // Get today's date and time
          let countDownDate = new Date(`${examinationDate} ${vueInstance.examinationState.examination.start}`)
          countDownDate.setMinutes(countDownDate.getMinutes() + parseInt(vueInstance.examinationState.examinationDurationInMinutes, 10))
          let now = new Date().getTime();
          // Find the distance between now and the count down date
          let timeRange = countDownDate - now;
          // Time calculations for days, hours, minutes and seconds
          let days = Math.floor(timeRange / (1000 * 60 * 60 * 24));
          let hours = Math.floor((timeRange % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
          let minutes = Math.floor((timeRange % (1000 * 60 * 60)) / (1000 * 60));
          let seconds = Math.floor((timeRange % (1000 * 60)) / 1000);
          // Display the result in the element with id="demo"
          if (days > 0) {
            vueInstance.countDownClockForExamination = `${days}:${hours < 10 ? '0'+hours : hours}:${minutes < 10 ? '0'+minutes : minutes}:${seconds < 10 ? '0'+seconds : seconds }`
          } else {
            vueInstance.countDownClockForExamination = `${hours}:${minutes < 10 ? '0'+minutes : minutes}:${seconds < 10 ? '0'+seconds : seconds}`
          }
          // If the count down is finished, write some text
          if (timeRange < 0) {
            clearInterval(vueInstance.intervalPlaceholder);
            vueInstance.countDownClockForExamination = `00:00:00`
            vueInstance.submitExaminationForGrading()
          }
        }, 1000)
      },

      startExamination() {
        const examinationState = this.examinationState
        const questionArrayLength = this.examinationQuestions.length
        examinationState.firstViewIndex = 0
        examinationState.currentViewIdex = 0
        examinationState.lastViewIndex = questionArrayLength
        examinationState.examinationStarted = true
        examinationState.examinationStartTime = new Date().getTime()
        examinationState.examinationStatus = 'Started'

        this.$store.dispatch('UPDATE_EXAMINATION_STATE', examinationState)
        .then(() => {
            this.startTimner(this)
        }).catch(() => {
          alert("Examination start attempt failed. Please try again.")
        })
      },

      randomizeExaminationQuestions() {
        const randomizedQuestions = this.examinationQuestions
        // const lengthQuestionArray = randomizedQuestions.length
        for (let i=0; i<2; i++) {
          const randomIndex = Math.floor(Math.random() * 1)
          const itemRemoved = randomizedQuestions.splice(randomIndex, 1)
          console.log(randomizedQuestions, itemRemoved)
        }

        return this.examinationQuestions
      },

      updateExaminationStateStore(examinationState) {
        this.$store.dispatch('UPDATE_EXAMINATION_STATE', examinationState)
        .then(() => {
           console.log("Update was successful ...")
        }).catch((error) => {
         console.log("Failed to Update store: ", error)
        })
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    created() {
        this.institutionID = this.$route.params.institutionID
        this.studentID = this.$route.params.studentID
        this.classroomID = this.$route.params.classroomID
        this.examinationID = this.$route.params.examinationID
        this.timetableID = this.$route.params.timetableID
        this.courseID = this.$route.params.courseID

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

        db.collection("courses").where("courseID", "==", this.courseID).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                  this.course.id = doc.id
                  this.course.courseID = doc.data().courseID
                  this.course.institutionID = doc.data().institutionID
                  this.course.title = doc.data().title
                  this.course.grade = doc.data().grade
                  this.course.code = doc.data().code
                  this.course.duration = doc.data().duration
                  this.course.durationUnit = doc.data().durationUnit
                  this.course.instructor = doc.data().instructor
                  this.course.description = doc.data().description
                  this.course.createdOn = doc.data().createdOn                                                                          
              })
            })

        db.collection("classrooms").where("classroomID", "==", this.classroomID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              this.classroom.id = doc.id
              this.classroom.classroomID = doc.data().classroomID
              this.classroom.institutionID = doc.data().institutionID
              this.classroom.name = doc.data().name
              this.classroom.grade = doc.data().grade
              this.classroom.capacity = parseInt(doc.data().capacity)
              this.classroom.numberOfStudentsAssigned = doc.data().numberOfStudentsAssigned
              this.classroom.overSeenBy = doc.data().overSeenBy
              this.classroom.createdOn = doc.data().createdOn                                                                                                                       
            })
          })

        db.collection("examination_timetables").where("classroomID", "==", this.classroomID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              this.timetable.timetableID = doc.data().timeTableID
              this.timetable.classroomID = doc.data().classroomID
              this.timetable.examinationID = doc.data().examinationID
              this.timetable.details = JSON.parse(doc.data().details)
              this.timetable.startHour = doc.data().startHour
              this.timetable.maxNumExaminationPerDay = doc.data().maxNumExaminationPerDay
              this.timetable.maxExaminationDuration = doc.data().maxExaminationDuration
              this.timetable.firstBreak = doc.data().firstBreak
              this.timetable.secondBreak = doc.data().secondBreak
              this.timetable.timeTableDescription = doc.data().timeTableDescription
              this.timetable.createdOn = doc.data().createdOn                                                        
            })
              if (this.timetable.examinationID) {
                db.collection("examinations").where("examinationID", "==", this.timetable.examinationID).get()
                .then(querySnapshot => {
                  querySnapshot.forEach(doc => {
                      this.examination.examinationID = doc.data().examinationID
                      this.examination.institutionID = doc.data().institutionID
                      this.examination.title = doc.data().title
                      this.examination.academicSession = doc.data().academicSession
                      this.examination.examinationStatus = doc.data().examinationStatus
                      this.examination.startDate = doc.data().startDate
                      this.examination.endDate = doc.data().endDate
                      this.examination.examinationDescription = doc.data().examinationDescription
                      this.examination.createdOn = doc.data().createdOn
                    })
                  })
                this.timetable.details.map((day) => {
                  day.map((examination) => {
                      if (examination.courseID === this.courseID) {
                        this.examinationPaperDetails = examination
                      }
                  })
                })

                this.paperID = this.examinationPaperDetails
                db.collection("questions").where("timetableID", "==", this.timetable.timetableID).get()
                .then(querySnapshot => {
                  querySnapshot.forEach(doc => {
                    if ( doc.data().institutionID === this.institutionID && doc.data().classroomID === this.classroomID && doc.data().courseID === this.courseID && doc.data().examinationID === this.examinationID ) {
                      this.targetQuestion.questionID = doc.data().questionID
                      this.targetQuestion.institutionID = doc.data().institutionID
                      this.targetQuestion.classroomID = doc.data().classroomID
                      this.targetQuestion.courseID = doc.data().courseID
                      this.targetQuestion.examinationID = doc.data().examinationID
                      this.targetQuestion.timetableID = doc.data().timetableID
                      this.targetQuestion.createdOn = doc.data().createdOn
                    }
                  })

                  console.log("=============   TQ   ================>>>>>>>>>>>>>>    ", this.targetQuestion)

                  if (this.targetQuestion.questionID) {
                    db.collection("question_instructions").where("questionID", "==", this.targetQuestion.questionID).get()
                    .then(querySnapshot => {
                      querySnapshot.forEach(doc => {
                          this.questionInstruction.questionInstructionID = doc.data().questionInstructionID
                          this.questionInstruction.questionID = doc.data().questionID
                          this.questionInstruction.title = doc.data().title
                          this.questionInstruction.details = JSON.parse(doc.data().details)
                          this.questionInstruction.description = doc.data().description
                          this.questionInstruction.createdOn = doc.data().createdOn
                        })
                      })
                    db.collection("examination_questions").where("questionID", "==", this.targetQuestion.questionID).get()
                    .then(querySnapshot => {
                      querySnapshot.forEach(doc => {
                          const data = {
                            'questionID': doc.data().questionID,
                            'examinationQuestionID': doc.data().examinationQuestionID,
                            'examQuestion': CryptoJS.enc.Base64.parse(doc.data().examQuestion).toString(CryptoJS.enc.Utf8),
                            'A': CryptoJS.enc.Base64.parse(doc.data()['A']).toString(CryptoJS.enc.Utf8),
                            'B': CryptoJS.enc.Base64.parse(doc.data()['B']).toString(CryptoJS.enc.Utf8),
                            'C': CryptoJS.enc.Base64.parse(doc.data()['C']).toString(CryptoJS.enc.Utf8),
                            'D': CryptoJS.enc.Base64.parse(doc.data()['D']).toString(CryptoJS.enc.Utf8),
                            'answer': CryptoJS.enc.Base64.parse(doc.data().answer).toString(CryptoJS.enc.Utf8),
                            'selectedOption':  CryptoJS.enc.Base64.parse(doc.data().selectedOption).toString(CryptoJS.enc.Utf8),
                            'extraInformation': doc.data().extraInformation,
                            'createdOn': doc.data().createdOn
                          }
                          this.examinationQuestions.push(data)
                        })
                        const examinationState =  this.examinationState
                        examinationState.examinationDetails = this.examinationQuestions
                        this.updateExaminationStateStore(examinationState)
                        this.showUI = true
                      })
                  }
                })


              }
          })

          if (this.examinationState.examinationStatus === "Started") {
            this.startTimner(this)
          }
    },

    beforeRouteLeave (to, from, next) {
      const answer = window.confirm('Do you really want to leave? your examination will be graded anyway!')
      if (this.examinationState.examinationStarted && !this.examinationState.examinationEnded) {
        if (answer) {
          this.submitExaminationForGrading()
          next()
        } else {
          next(false)
        }        
      } else {
        next()
      }
    },

    watch: {
      examinationState() {
        this.$forceUpdate()
      }
    }
  }
</script>

