<template>
    <div class="chat-window">
        <div class="fixed-header">
            <div class="navbar">
                <div class="user-details mr-auto">
                    <div class="float-left user-img m-r-10">
                        <a href="#" :title="getChatMateName()"><img :src="''" alt="" class="w-40 rounded-circle"><span class="status online"></span></a>
                    </div>
                    <div class="user-info float-left">
                        <a href="#"><span class="font-bold">{{getChatMateName()}}</span> <i v-if="isTyping" class="typing-text">Typing...</i></a>
                        <span class="last-seen">Last seen today at 7:50 AM</span>
                    </div>
                </div>
                <div class="search-box">
                    <div class="input-group input-group-sm">
                        <input type="text" class="form-control" placeholder="Search">
                        <span class="input-group-append">
                                <button class="btn" type="button"><i class="fa fa-search"></i></button>
                            </span>
                    </div>
                </div>
            </div>
        </div>
        <div class="chat-contents">
            <div class="chat-content-wrap">
                <div class="chat-wrap-inner">
                    <div class="chat-box" :set="tracker = loggedUserChats.length > 0 ? loggedUserChats[0] : ''">
                        <div class="chats" :set="dateTracker = []">
                            <div v-for="(chat,index) in loggedUserChats" :key="index">
                            <div v-if="tracker !== customizeDate(chat.createdOn)" class="chat-line" :set="dateTracker[index] = tracker">
                                <span class="chat-date">{{customizeDate(chat.createdOn)}}</span>
                            </div>
                            <div
                                :set="tracker = (index > 0) && (tracker == customizeDate(chat.createdOn)) ? tracker: customizeDate(chat.createdOn)" 
                                class="chat" 
                                :class="{
                                    'chat-left': !(loggedUserChatAccount.accountID === chat.sender),
                                    'chat-right': loggedUserChatAccount.accountID === chat.sender,
                                }"  
                            >
                                <div 
                                    class="chat-avatar"
                                    v-if="!(loggedUserChatAccount.accountID === chat.sender)"
                                >
                                    <router-link 
                                        :to="{
                                            name: 'AdministratorChats',
                                            params: {
                                              administratorID: administratorID,
                                              chatMateID: chat.sender
                                            }                                           
                                        }" 
                                        class="avatar"
                                    >
                                        <img alt="Jennifer Robinson" :src="defaultImage" class="img-fluid rounded-circle">
                                    </router-link>
                                </div>
                                <div class="chat-body">
                                    <div class="chat-bubble">
                                        <div class="chat-content">
                                            <p>{{chat.message}}</p>
                                            <span class="chat-time">{{getTimeDiff(chat.createdOn)}}</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <footer-chat></footer-chat>
    </div>
</template>

<script>
    import ChatFooter from './ChatFooter'

    import { db } from '../../firebase/firebaseInit'

    import { customizeTime, customizeDate } from '../../helpers/customizeDate'

    export default {
        name: "ChatWindow",
        components: {
            "footer-chat": ChatFooter
        },

        props: {
            chatMateID: {
                type: String,
                required: true
            }
        },

        data() {
            return {
                isTyping: false,
                administratorID: "",
                chatMate: {},
                loggedUserChatAccount: {},
                loggedUserChats: [],
                setIntervalID: null,
                defaultImage: 'https://www.gravatar.com/avatar/205e460b479e2e5b48aec07710c08d50?f=y',

                users: []
            }
        },

        computed: {
            loggedUserDetails() {
              return this.$store.getters.loggedUser
            },
        },

        methods: {
            getChatMateName() {
                if (this.chatMate.accountType === 'general') {
                    return this.chatMate.referenceName
                }

                return this.getChatUserInstance(this.chatMate.userID) ? this.getChatUserInstance(this.chatMate.userID).fullname : "Unknown User"
            },

            isChatMateAGeneralChannel() {
                return this.chatMate.accountType === 'general' ? true : false
            },

            getTimeDiff(targetDate) {
                return customizeTime(targetDate)
            },

            customizeDate(targetDate) {
                return customizeDate(targetDate)
            },

            updateChatMate(chatMateID) {
                db.collection("chat_accounts").where('accountID', '==', chatMateID).get()
                .then(querySnapshot => {
                  querySnapshot.forEach(doc => {
                    this.chatMate.accountID = doc.data().accountID,
                    this.chatMate.userID = doc.data().userID,
                    this.chatMate.accountType = doc.data().accountType,
                    this.chatMate.userType = doc.data().userType,
                    this.chatMate.referenced_db = doc.data().referenced_db,
                    this.chatMate.referenceName = doc.data().referenceName
                  })
                  window.EventBus.emit('update-chat-mate-account-info', this.chatMate)
                })
            },

            getMessages(vueInstance) {
                const temp = []
                const targetUser = vueInstance.loggedUserChatAccount.accountID ? vueInstance.loggedUserChatAccount.accountID : ""
                db.collection('chats').where('recipient', 'in', [vueInstance.$route.params.chatMateID, targetUser]).orderBy('createdOn').get()
                .then(querySnapshot => {
                  querySnapshot.forEach(doc => {
                    if (vueInstance.isChatMateAGeneralChannel()) {
                        if ( vueInstance.$route.params.chatMateID === doc.data().recipient) {
                          const data = {
                              "chatID": doc.data().chatID,
                              "sender": doc.data().sender,
                              "recipient": doc.data().recipient,
                              "message": doc.data().message,
                              "status": doc.data().status,
                              "createdOn": doc.data().createdOn
                          }
                          temp.push(data)
                        }
                    } else {
                      if (
                          (vueInstance.loggedUserChatAccount.accountID === doc.data().sender && vueInstance.$route.params.chatMateID === doc.data().recipient) ||
                          (vueInstance.loggedUserChatAccount.accountID === doc.data().recipient && vueInstance.$route.params.chatMateID === doc.data().sender)
                      ) {
                        const data = {
                            "chatID": doc.data().chatID,
                            "sender": doc.data().sender,
                            "recipient": doc.data().recipient,
                            "message": doc.data().message,
                            "status": doc.data().status,
                            "createdOn": doc.data().createdOn
                        }
                        temp.push(data)
                      }
                    }
                  })
                  vueInstance.loggedUserChats = temp
                })
            },

            updateChatUIAtInterval() {
                const vueInstance = this;
                this.setIntervalID = setInterval(async function () {
                    await vueInstance.getMessages(vueInstance)
                }, 2000)
            },

            getChatUserInstance(userID) {
                let userDetails = null
              
                this.users.map(user => {
                    if (user.userID === userID) {
                        userDetails = user
                    }
                })

                return userDetails
            }
        },

        beforeUnmount() {
            clearInterval(this.setIntervalID)
        },

        async created() {
            this.administratorID = this.$route.params.administratorID

            db.collection("institution_staffs").get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                const data = {
                  'id': doc.id,
                  'userID': doc.data().userID,
                  'institutionID': doc.data().institutionID,
                  'fullname': doc.data().fullname,
                  'email': doc.data().email,
                  'userRole': doc.data().userRole,
                  'accountType': doc.data().accountType,
                  'address': doc.data().address,
                  'phone': doc.data().phone,
                  'gender': doc.data().gender,
                  'dob': doc.data().dob,
                  'avatar': doc.data().avatar
                }                                                                                                                                               
                  this.users.push(data)
                })
              })
            
            db.collection("system_administrator").get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'userID': doc.data().userID,
                    'fullname': doc.data().fullname,
                    'email': doc.data().email,
                    'userRole': doc.data().userRole,
                    'accountType': doc.data().accountType
                }                                                           
                  this.users.push(data)
                })
              })

            db.collection("chat_accounts").where('userID', '==', this.loggedUserDetails.userID).get()
            .then( async querySnapshot => {
              querySnapshot.forEach(doc => {
                this.loggedUserChatAccount.accountID = doc.data().accountID
                this.loggedUserChatAccount.userID = doc.data().userID
                this.loggedUserChatAccount.accountType = doc.data().accountType
                this.loggedUserChatAccount.userType = doc.data().userType
                this.loggedUserChatAccount.referenced_db = doc.data().referenced_db
                this.loggedUserChatAccount.referenceName = doc.data().referenceName
              })
            })

            await this.updateChatMate(this.chatMateID)
        },

        mounted() {
            this.updateChatUIAtInterval()
        },

        watch: {
            async chatMateID(newChatMateID, oldChatMateID) {
                await this.updateChatMate(newChatMateID)
                this.$forceUpdate()
                console.log(oldChatMateID)
            }
        },
    }
</script>