<template>
<div class="main-viewer mb-5">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title">Edit {{classroom.name}}'s Time Table</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                <router-link 
                  :to="{
                    name: 'InstitutionClassroomDetails',
                    params: {
                      institutionID: institutionID,
                      classroomID: classroomID
                    }                              
                  }" 
                  class="btn btn-outline-danger"
                >
                  <span class="mb-2"><i class="fas fa-times mr-2"></i></span>Cancel
                </router-link>
              </div>
          </div>
          <div class="row mb-5">
            <div v-if="!isEmptyObject(timetable)" class="col-md-6 offset-md-3">
                <ul class="list-group list-group-flush">
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Time Table ID</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{timetable.timetableID}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Classroom ID</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{timetable.classroomID}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Examination ID</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{timetable.examinationID}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Examinations per Day</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{timetable.maxNumExaminationPerDay}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Each Paper Duration</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{customizeHoursMinutes(parseInt(timetable.maxExaminationDuration))}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">First Paper Start Time</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{`${timetable.startHour}:00:00`}}</strong></div>
                        </div>
                    </li>                    
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">First Break</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{customizeHoursMinutes(parseInt(timetable.firstBreak))}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Second Break</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{customizeHoursMinutes(parseInt(timetable.secondBreak))}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Created On</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{customizeDate(parseInt(timetable.createdOn))}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Description</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{timetable.timeTableDescription}}</strong></div>
                        </div>
                    </li>
                </ul>
            </div>                        
          </div>
          <div v-if="!isEmptyObject(timetable)" class="row">
              <div class="col-md-12 mb-5">
                <h4 class="h4 text-center mt-3 mb-3">({{getExaminationSemesterCode(examination.academicSession, examination.startDate)}}) {{examination.title}}</h4>
                <table class="table table-bordered table-dark text-white">
                  <!-- <thead> -->
                    <!-- <tr> -->
                      <!-- <th scope="col">#</th> -->
                      <!-- <th scope="col">First</th> -->
                      <!-- <th scope="col">Last</th> -->
                      <!-- <th scope="col">Handle</th> -->
                    <!-- </tr> -->
                  <!-- </thead> -->
                  <tbody>
                    <tr v-for="(day, index) in timetable.details" :key="index">
                      <td v-for="(paper, paperIndex) in day" :key="paper.paerID">
                        <div v-if="paper.isDayInfo" class="day-info">
                          <span class="pt-5">
                            <strong class="text-danger text-capitalize font-weight-bold font-italic">
                              {{paper.weekday}}
                            </strong>
                          </span>
                          <span>
                            {{paper.date}}
                          </span>
                        </div>
                        <div v-else>
                          <div class="mb-3 col-12 text-center bg-danger" v-if="index == 0"><strong>{{paper.start}}</strong> - <strong>{{paper.end}}</strong></div>
                          <span v-if="paper.paperID.indexOf('break') > 0" class="paper-info" :class="{'pt-3': index == 0, 'pt-4': index != 0}" v-html="getPaperInformation(paper.courseID, paper.paperID)"></span>
                          <div v-if="!(paper.paperID.indexOf('break') > 0)" class="form-group text-center">
                            <label>Paper {{index}}{{paperIndex}}</label>
                            <select @change.prevent="modifyCanUpdateStatus" class="form-control select" v-model="paper.courseID">
                              <option value="">Select Examination Course</option>
                              <option v-for="course in classroomCourses" :key="course.courseID" :value="course.courseID">{{getCourseRecordByID(course.courseID).title}} ({{getCourseRecordByID(course.courseID).code}})</option>
                            </select>
                          </div>                                                
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>   
              </div>            
          </div>
          <div v-if="!isEmptyObject(timetable)" class="row">
            <div class="col-4 text-center">
                <button @click.prevent="cancelTimeTableUpdate" class="btn btn-outline-secondary">
                  <span class="mb-2"><i class="fas fa-times mr-2"></i></span>Cancel Update
                </button>              
            </div>
            <div class="col-4 text-center">
                <button v-if="canUpdate" @click.prevent="attemptTimeTableUpdate" class="btn btn-outline-success">
                  <span class="mb-2"><i class="fas fa-upload mr-2"></i></span>Save Changes
                </button>              
            </div>
            <div class="col-4 text-center">
                <button @click.prevent="attemptTimeTableDelete" class="btn btn-outline-danger">
                  <span class="mb-2"><i class="fas fa-trash mr-2"></i></span>Delete Time Table
                </button>              
            </div>
          </div>
          <div v-if="isEmptyObject(timetable)" class="row">
            <div class="col-8 offset-2 mt-3 mb-3 text-center">
              <div class="jumbotron text-center">
                <h3 class="h4">Time Table does NOT exist!</h3>
              </div>
            </div>
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'
  import { customizeDate } from '../../helpers/customizeDate'

  import { isEmptyObject } from '../../helpers/isEmptyObject'

  export default {
    name: "EditTimeTableDetails",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'registered institutions',
        institutionID: null,
        classroomID: null,
        timetableID: null,
        institution: {},
        timetable: {},
        classroom: {},
        examination: {},

        courses: [],
        classroomCourses: [],

        canUpdate: false
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      customizeDate(timestamp) {
        return customizeDate(timestamp)
      },

      customizeHoursMinutes(timeInMinutes) {
        const hrs = parseInt(timeInMinutes/60, 20)
        const mins = timeInMinutes%60
        return `${hrs} ${hrs>1? 'hours':'hour'} ${mins} ${mins>1? 'minutes' : 'minute'}`
      },

      isEmptyObject(obj) {
        return isEmptyObject(obj)
      },

      modifyCanUpdateStatus() {
        this.canUpdate = true
      },

      getDateString(startDate) {
        return startDate
      },

      getSemesterString(academicSession) {
        return `${academicSession}`
      },

      getCourseRecordByID(courseID) {
        let record = null
        this.courses.map(course => {
          if (course.courseID === courseID) {
            record = course
          }
        })
        return record
      },

      getExaminationSemesterCode(academicSession, startDate) {
          let dateString = this.getDateString(startDate)
          let semString = this.getSemesterString(academicSession)
          return `${semString} ${dateString}`
      },

      getPaperInformation(courseID, paperID) {
        if (paperID.indexOf('break') > 0) {
          const labelArray = paperID.split("-")
          labelArray.splice(0, 1)
          return `<span>${labelArray.join(" ").toUpperCase()}</span>`
        }
        const course = this.getCourseRecordByID(courseID)
        return this.isEmptyObject(course) ? 
        `<span>${'No Examination'}</span><span>(${'Free Period'})</span>` :
        `<span>${course.title}</span>
         <span>(${course.code})</span>`
      },

      cancelTimeTableUpdate() {
        this.$router.push({
          name: 'InstitutionClassroomDetails',
          params: {
            institutionID: this.institutionID,
            classroomID: this.classroomID
          }
        })
      },

      attemptTimeTableDelete() {
        if (confirm("Delete examination time table?")) {
          db.collection("examination_timetables").where('timeTableID', '==', this.timetableID).get()
            .then( querySnapshot => {
              querySnapshot.forEach( doc => {
                  doc.ref.delete()
                  this.timetable = {}
              })
            })
        }
      },

      attemptTimeTableUpdate() {
          db.collection("examination_timetables").where('timeTableID', '==', this.timetableID).get()
          .then( querySnapshot => {
              querySnapshot.forEach( doc => {
                  doc.ref.update({
                      "details": JSON.stringify(this.timetable.details)
                  })
              })

              this.$router.push({
                name: 'InstitutionClassroomDetails',
                params: {
                  institutionID: this.institutionID,
                  classroomID: this.classroomID
                }
              })
          })
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);


    },

    created() {
      this.institutionID = this.$route.params.institutionID
      this.classroomID = this.$route.params.classroomID
      this.timetableID = this.$route.params.timetableID

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

        db.collection("examination_timetables").where("classroomID", "==", this.classroomID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              this.timetable.timetableID = doc.data().timeTableID
              this.timetable.classroomID = doc.data().classroomID
              this.timetable.examinationID = doc.data().examinationID
              this.timetable.details = JSON.parse(doc.data().details)
              this.timetable.startHour = doc.data().startHour
              this.timetable.maxNumExaminationPerDay = doc.data().maxNumExaminationPerDay
              this.timetable.maxExaminationDuration = doc.data().maxExaminationDuration
              this.timetable.firstBreak = doc.data().firstBreak
              this.timetable.secondBreak = doc.data().secondBreak
              this.timetable.timeTableDescription = doc.data().timeTableDescription
              this.timetable.createdOn = doc.data().createdOn                                                        
            })

            if (this.timetable.examinationID) {
              db.collection("examinations").where("examinationID", "==", this.timetable.examinationID).get()
              .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.examination.examinationID = doc.data().examinationID
                    this.examination.institutionID = doc.data().institutionID
                    this.examination.title = doc.data().title
                    this.examination.academicSession = doc.data().academicSession
                    this.examination.examinationStatus = doc.data().examinationStatus
                    this.examination.startDate = doc.data().startDate
                    this.examination.endDate = doc.data().endDate
                    this.examination.examinationDescription = doc.data().examinationDescription
                    this.examination.createdOn = doc.data().createdOn
                  })
                })
            }
          })

      db.collection("classrooms").where("classroomID", "==", this.classroomID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            if (this.institutionID === doc.data().institutionID) {
              this.classroom.id = doc.id
              this.classroom.classroomID = doc.data().classroomID,
              this.classroom.institutionID = doc.data().institutionID,
              this.classroom.name = doc.data().name,
              this.classroom.grade = doc.data().grade,
              this.classroom.capacity = parseInt(doc.data().capacity),
              this.classroom.numberOfStudentsAssigned = doc.data().numberOfStudentsAssigned,
              this.classroom.overSeenBy = doc.data().overSeenBy,
              this.classroom.createdOn = doc.data().createdOn
            }
          })
        })

      db.collection("courses").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              const data = {
                'id': doc.id,
                'courseID': doc.data().courseID,
                'institutionID': doc.data().institutionID,
                'title': doc.data().title,
                'grade': doc.data().grade,
                'code': doc.data().code,
                'duration': doc.data().duration,
                'durationUnit': doc.data().durationUnit,
                'instructor': doc.data().instructor,
                'description': doc.data().description,
                'createdOn': doc.data().createdOn
              }                                                                                                                                       
              this.courses.push(data)
            })
          })    

        db.collection("classroom_courses").where("classroomID", "==", this.classroomID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
                'id': doc.id,
                'courseID': doc.data().courseID,
                'classroomID': doc.data().classroomID 
            }                                                                                             
              this.classroomCourses.push(data)
            })
          })
          
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }

  .day-info {
    display:flex; 
    flex-direction: column; 
    align-items:center; 
    justify-content: center;
  }

  .paper-info {
    display:flex;  
    flex-direction: column; 
    align-items:center; 
    justify-content: center;
  }
</style>
