<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-8 col-3">
                  <h4 class="page-title">{{selectedStudent['fullname']}}'s Details</h4>
              </div>
              <div class="col-sm-4 col-9 text-right m-b-20">
                  <router-link 
                    :to="{
                      name: 'InstitutionEditStudentDetails',
                      params: {
                        institutionID: institutionID,
                        studentID: studentID,
                      }                              
                    }" 
                    class="btn btn-outline-success mr-3"
                  >
                    <span class="mb-2"><i class="fas fa-edit mr-2"></i></span>Edit
                  </router-link>
                  <router-link 
                    :to="{
                      name: 'InstitutionStudents',
                      params: {
                        institutionID: institutionID,
                      }                              
                    }" 
                    class="btn btn-outline-secondary"
                  >
                    <span class="mb-2"><i class="fas fa-arrow-circle-left mr-2"></i></span>Back
                  </router-link>               
              </div>
          </div>
          <div class="row mb-3">
            <div class="col-md-6 offset-md-3">
                <ul class="list-group list-group-flush">
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Student ID</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ selectedStudent['studentID'] }}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Fullname</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ selectedStudent['fullname'] }} </strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Date of Birth</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ customizeDate(selectedStudent['dob']) }}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Gender</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark" style="text-transform: capitalize;">{{ selectedStudent['gender'] }}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Email</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ selectedStudent['email'] }}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Phone</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ selectedStudent['phone'] }}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Address</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ selectedStudent['address'] }}</strong></div>
                        </div>
                    </li>
                </ul>
            </div>              
          </div>
        </div>
        <hr class="mb-3"> 
        <h3 class="h3 ml-4">Assigned To</h3>
        <hr class="mb-3"> 
          <div class="row">
            <div v-if="classroomStudents.length <= 0" class="col-8 offset-2 mt-3 mb-3">
              <div class="jumbotron text-center">
                <h3 class="h4">Classroom list is empty!</h3>
              </div>
            </div>
            <div v-if="classroomStudents.length > 0" class="col-md-12 mb-5">
              <div class="table-responsive">
                <table class="table table-striped custom-table">
                  <thead>
                    <tr>
                      <th style="min-width:200px;">Classroom Name</th>
                      <th>Classroom ID</th>
                      <th>Grade/Year</th>
                      <th>Capacity</th>
                      <th>No. assigned Students</th>
                      <th>Overseen By</th>
                      <th>Created On</th>
                    </tr>
                  </thead>
                  <tbody :set="tempCourses = []">
                    <tr
                        v-for="classroom in classroomStudents"
                        :key="classroom.classroomID"
                      >
                        <td>
                          <img width="28" height="28" class="rounded-circle"> <h2>{{getClassroomByID(classroom.classroomID).name}}</h2>
                        </td>   
                        <td>{{classroom.classroomID}}</td>
                        <td>{{getClassroomByID(classroom.classroomID).grade}}</td>
                        <td>{{getClassroomByID(classroom.classroomID).capacity}}</td>
                        <td>{{getClassroomByID(classroom.classroomID).numberOfStudentsAssigned}}</td>
                        <td>{{getEmployeeNameByID(getClassroomByID(classroom.classroomID).overSeenBy)}}</td>
                        <td>{{customizeDate(parseInt(getClassroomByID(classroom.classroomID).createdOn))}}</td>
                      </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <hr class="mb-3"> 
        <h3 class="h3 ml-4">Classroom Course</h3>
        <hr class="mb-3"> 
          <div class="row">
            <div v-if="classroomCourses.length <= 0" class="col-8 offset-2 mt-3 mb-3">
              <div class="jumbotron text-center">
                <h3 class="h4">Course list is empty!</h3>
              </div>
            </div>
            <div v-if="classroomCourses.length > 0" class="col-md-12 mb-5">
              <div class="table-responsive">
                <table class="table table-striped custom-table">
                  <thead>
                      <tr>
                      <th style="min-width:200px;">Course Title</th>
                      <th>Course ID</th>
                      <th>Course Code</th>
                      <th>Grade</th>
                      <th>Duration</th>
                      <th>Instructor</th>
                      <th>Created On</th>
                    </tr>
                  </thead>
                  <tbody :set="tempCourses = []">
                    <tr
                      v-for="course in classroomCourses"
                      :key="course.courseID"
                    >
                      <td>
                        <img width="28" height="28" class="rounded-circle"> <h2>{{getCourseRecordByID(course.courseID).title}}</h2>
                      </td>   
                      <td>{{getCourseRecordByID(course.courseID).courseID}}</td>
                      <td>{{getCourseRecordByID(course.courseID).code}}</td>
                      <td>{{getCourseRecordByID(course.courseID).grade}}</td>
                      <td>{{getCourseRecordByID(course.courseID).duration}} {{getCourseRecordByID(course.courseID).durationUnit}}</td>
                      <td>{{getEmployeeNameByID(getCourseRecordByID(course.courseID).instructor)}}</td>
                      <td>{{customizeDate(parseInt(getCourseRecordByID(course.courseID).createdOn))}}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'

  import { customizeDate } from '../../helpers/customizeDate'

  export default {
    name: "CourseDetails",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'registered institutions',
        institutionID: null,
        studentID: null,
        institution: {},
        selectedStudent: {},
        employees: [],
        classrooms: [],
        classroomCourses: [],
        classroomStudents: [],
        courses: []
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      customizeDate(timestamp) {
        return customizeDate(timestamp)
      },

      getEmployeeNameByID(userID) {
        let name = "Unknown"
        this.employees.map(employee => {
          if (employee.userID === userID) {
            name = employee.fullname
          }
        })
        return name
      },

      getClassroomByID(classroomID) {
        let record = {}
        this.classrooms.map(classroom => {
          if (classroom.classroomID === classroomID) {
            record = classroom
          }
        })
        return record
      },

      getCourseRecordByID(courseID) {
        let record = null

        this.courses.map(course => {
          if (course.courseID === courseID) {
            record = course
          }
        })

        return record
      },
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    created() {
      this.institutionID = this.$route.params.institutionID
      this.studentID = this.$route.params.studentID

      db.collection("institution_staffs").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'userID': doc.data().userID,
              'institutionID': doc.data().institutionID,
              'fullname': doc.data().fullname,
              'email': doc.data().email,
              'userRole': doc.data().userRole,
              'accountType': doc.data().accountType,
              'address': doc.data().address,
              'phone': doc.data().phone,
              'gender': doc.data().gender,
              'dob': doc.data().dob,
              'avatar': doc.data().avatar
            }                                                                                                                                               
              this.employees.push(data)
            })
          })

      db.collection("courses").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              const data = {
                'id': doc.id,
                'courseID': doc.data().courseID,
                'institutionID': doc.data().institutionID,
                'title': doc.data().title,
                'grade': doc.data().grade,
                'code': doc.data().code,
                'duration': doc.data().duration,
                'durationUnit': doc.data().durationUnit,
                'instructor': doc.data().instructor,
                'description': doc.data().description,
                'createdOn': doc.data().createdOn
              }                                                                                                                                       
              this.courses.push(data)
            })
          })


      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })


        db.collection("students").where("studentID", "==", this.studentID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              this.selectedStudent.id = doc.id
              this.selectedStudent.studentID = doc.data().studentID
              this.selectedStudent.institutionID = doc.data().institutionID
              this.selectedStudent.fullname = doc.data().fullname
              this.selectedStudent.dob = doc.data().dob
              this.selectedStudent.gender = doc.data().gender
              this.selectedStudent.email = doc.data().email
              this.selectedStudent.phone = doc.data().phone
              this.selectedStudent.address = doc.data().address
              this.selectedStudent.userRole = doc.data().userRole
              this.selectedStudent.accountType = doc.data().accountType
              this.selectedStudent.createdOn = doc.data().createdOn                                                                                                                                
            })
          })


        db.collection("classrooms").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
                const data = {
                  'id': doc.id,
                  'classroomID': doc.data().classroomID,
                  'institutionID': doc.data().institutionID,
                  'name': doc.data().name,
                  'grade': doc.data().grade,
                  'capacity': parseInt(doc.data().capacity),
                  'numberOfStudentsAssigned': doc.data().numberOfStudentsAssigned,
                  'overSeenBy': doc.data().overSeenBy,
                  'createdOn': doc.data().createdOn
                }                                                                                                                                             
              this.classrooms.push(data)
            })
          })

        db.collection("classroom_students").where("studentID", "==", this.studentID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
                'id': doc.id,
                'studentID': doc.data().studentID,
                'classroomID': doc.data().classroomID 
            }                                                                                             
              this.classroomStudents.push(data)
              db.collection("classroom_courses").where("classroomID", "==", data.classroomID).get()
              .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                  const data = {
                      'id': doc.id,
                      'courseID': doc.data().courseID,
                      'classroomID': doc.data().classroomID 
                  }                                                                                             
                    this.classroomCourses.push(data)
                  })
                })
            })
          })
    },
  }
</script>

