<template>
  <div class="header">
    <div class="header-left">
      <router-link 
        :to="{ name: 'InstitutionalDashboard', params: { institutionID: institutionID } }" 
        class="logo"
      >
        <img :src="institution.avatar" width="35" height="35" alt="" srcset=""> <span class="h6 pt-2">{{institution.name}}</span>
      </router-link>
    </div>
    <a id="mobile_btn" @click.prevent="toggleMobileMenuOption" class="mobile_btn float-left" href="#sidebar"><i class="fa fa-bars"></i></a>
    <ul class="nav user-menu float-right">
      <li class="nav-item dropdown d-none d-sm-block">
        <a @click.prevent="toggleShowMessageBox" id="open_msg_box" class="hasnotifications nav-link"><i class="fa fa-comment"></i> <span class="badge badge-pill bg-danger float-right">8</span></a>
      </li>
      <li class="nav-item dropdown has-arrow">
        <a @click.prevent="toggleUserOptionMenu" class="dropdown-toggle nav-link user-link" data-toggle="dropdown">
          <span class="user-img">
            <img class="rounded-circle" src="../../../assets/user.png" width="24" alt="">
            <span class="status online"></span>
          </span>
          <span class="pr-2 pl-2" :title="loggedUserFullname"><strong>{{loggedUserInitials}}</strong></span>
        </a>
        <div class="dropdown-menu">
          <router-link 
            class="dropdown-item" 
            :to="{
              name: 'InstitutionProfile',
              params: {
                institutionID: institutionID
              }
            }"
          >My Profile</router-link>
          <router-link 
            class="dropdown-item" 
            :to="{
              name: 'InstitutionProfileEditor',
              params: {
                institutionID: institutionID
              }
            }"
          >Edit Profile</router-link>
          <a class="dropdown-item" @click.prevent="attemptLogout">Logout</a>
        </div>
      </li>
    </ul>
    <div class="dropdown mobile-user-menu float-right">
      <a @click.prevent="toggleUserOptionMenu" class="dropdown-toggle" data-toggle="dropdown" aria-expanded="false"><i class="fa fa-ellipsis-v"></i></a>
      <div class="dropdown-menu dropdown-menu-right">
        <router-link 
          class="dropdown-item" 
          :to="{
            name: 'InstitutionProfile',
            params: {
              institutionID: institutionID
            }
          }"
        >My Profile</router-link>
        <router-link 
          class="dropdown-item" 
          :to="{
            name: 'InstitutionProfileEditor',
            params: {
              institutionID: institutionID
            }
          }"
        >Edit Profile</router-link>
        <a class="dropdown-item" @click.prevent="attemptLogout">Logout</a>
      </div>
    </div>
  </div>
</template>

<script>
    import { auth } from '../../../firebase/firebaseInit'
    import { generateInitialsFromFullname } from '../../../helpers/generateInitialsFromFullname'

    export default {
      name: "PageHeader",

      props: {
        institution: {
          type: Object,
          required: true
        }
      },

      data() {
          return {
              institutionID: null,
              expectedAccountType: 'institution staff'
          }
      },

      computed: {
          loggedUserDetails() {
            return this.$store.getters.loggedUser
          },

          loggedUserFullname() {
              return this.loggedUserDetails.fullname
          },

          loggedUserInitials() {
              return generateInitialsFromFullname(this.loggedUserFullname)
          }
      },

      methods: {
        attemptLogout() {
          auth.signOut().then(() => {
            this.$store.dispatch('ATTEMPT_LOGOUT')
              .then(() => {
              this.$router.push({
                  name: "Home"
              })
            }).catch((err) => { console.log(err); })
          })
        },

        toggleMobileMenuOption() {
          window.EventBus.emit('page-header-mobile-menu');
        },

        toggleUserOptionMenu(e) {
            let targetElement = this.getTargetElement(e);

            if (targetElement) {
                if (targetElement.classList.contains('dropdown-toggle')) {
                    if (targetElement.nextElementSibling.classList.contains('show')) {
                        targetElement.nextElementSibling.classList.remove('show');
                    } else {
                        targetElement.nextElementSibling.classList.add('show');
                    }
                }
            }
        },

        toggleShowMessageBox() {
          window.EventBus.emit('page-header-message-box');
        },

        getTargetElement(events) {
            if (events.target.tagName === "A") {
                return events.target;
            } else if (events.target.parentNode.tagName === "A") {
                return events.target.parentNode;
            } else if (events.target.parentNode.parentNode.tagName === "A") {
                return events.target.parentNode.parentNode;
            } else {
                return null;
            }
        }
      },

      created() {
          this.institutionID = this.$route.params.institutionID
      },

      mounted() {
        if (this.loggedUserDetails.accountType !== this.expectedAccountType) {
          alert("You are NOT allowed to view this page!!!")
          this.attemptLogout()
        }
      }
    }
</script>

<style lang="scss" scoped>
    .logo {
        i {
            color: #FFF;
        }
    }

    .header {
      background: black !important;
    }
</style>
