<template>
    <div class="sidebar" id="sidebar">
      <div class="sidebar-inner slimscroll">
          <div id="sidebar-menu" class="sidebar-menu">
              <ul>
                  <li class="menu-title">Main</li>
                  <li :class="{active: active === 'dashboard'}">
                      <router-link 
                        :to="{
                            name: 'AdministratorDashboard',
                            params: {
                                administratorID: administratorID
                            }
                        }"
                      >
                        <i class="fas fa-tachometer-alt"></i> <span>Dashboard</span>
                      </router-link>
                  </li>
                  <li v-if="isSuperUser" :class="{active: active === 'activities'}">
                      <router-link 
                        :to="{
                            name: 'AdministratorActivities',
                            params: {
                                administratorID: administratorID
                            }
                        }"
                      >
                        <i class="fa fa-bell"></i> <span>Activities</span>
                      </router-link>
                  </li>
                  <li :class="{active: active === 'chats'}">
                      <router-link 
                        :to="{
                            name: 'AdministratorChats',
                            params: {
                                administratorID: administratorID,
                                chatMateID: applicationGeneralChatChannel.accountID
                            }
                        }"
                      >
                        <i class="fa fa-comment"></i> <span>Chats</span>
                      </router-link>
                  </li>
                  <li class="submenu">
                    <a @click.prevent="toggleSubmenu"><i class="fas fa-university"></i> <span>Institutions</span> <span class="menu-arrow"></span></a>
                    <ul style="display: none;">
                        <li :class="{active: active === 'registered institutions'}">
                          <router-link 
                            :to="{
                              name: 'RegisteredInstitutions',
                              params: {
                                administratorID: administratorID
                              }                              
                            }"
                          > Registered Institutions </router-link>
                        </li>
                        <li :class="{active: active === 'register institution'}">
                          <router-link 
                            :to="{
                              name: 'RegisterInstitution',
                              params: {
                                administratorID: administratorID
                              }                                                            
                            }"
                          > Register Institution </router-link>
                        </li>
                    </ul>
                  </li>
                  <li v-if="isSuperUser" class="submenu">
                    <a @click.prevent="toggleSubmenu"><i class="fas fa-users"></i> <span>Accounts</span> <span class="menu-arrow"></span></a>
                    <ul style="display: none;">
                        <li :class="{active: active === 'administrative users'}">
                          <router-link 
                            :to="{
                              name: 'AdministrativeUsers',
                              params: {
                                administratorID: administratorID
                              }                              
                            }"
                          > Administrative Users </router-link>
                        </li>
                        <li :class="{active: active === 'create user'}">
                          <router-link 
                            :to="{
                              name: 'CreateAdministrator',
                              params: {
                                administratorID: administratorID
                              }                                                            
                            }"
                          > Create Account </router-link>
                        </li>
                    </ul>
                  </li>
              </ul>
          </div>
      </div>
  </div>
</template>

<script>
    import { db } from '../../../firebase/firebaseInit'

    export default {
      name: "PageMenu",

      props: {
        active: {
          type: String,
          required: true
        }
      },

      data() {
          return {
              administratorID: null,
              applicationGeneralChatChannel: {}
          }
      },

      computed: {
        loggedUserDetails() {
          return this.$store.getters.loggedUser;
        },

        isSuperUser() {
          return this.loggedUserDetails.userRole === "super user"
        }
      },

      async created() {
          this.administratorID = this.$route.params.administratorID

          await db.collection("chat_accounts").get()
          .then( async querySnapshot => {
            querySnapshot.forEach(doc => {
               if (doc.data().userType === 'obea chat channel') {
                this.applicationGeneralChatChannel.accountID = doc.data().accountID
                this.applicationGeneralChatChannel.userID = doc.data().userID
                this.applicationGeneralChatChannel.accountType = doc.data().accountType
                this.applicationGeneralChatChannel.userType = doc.data().userType
                this.applicationGeneralChatChannel.referenced_db = doc.data().referenced_db
                this.applicationGeneralChatChannel.referenceName = doc.data().referenceName
              }
            })
          })
      },

      methods: {
        toggleSubmenu(e) {
          let targetElement = this.getTargetElement(e);

          if (targetElement.parentNode.classList.contains('submenu')) {
            if (targetElement.classList.contains('subdrop')) {
                targetElement.classList.remove('subdrop');
                targetElement.nextElementSibling.style.display = "none"; 
            } else {
                targetElement.classList.add('subdrop');
                targetElement.nextElementSibling.style.display = "block"; 
            }
          }
        },

        getTargetElement(events) {
          if (events.target.tagName === "A") {
            return events.target;
          } else if (events.target.parentNode.tagName === "A") {
            return events.target.parentNode;
          } else if (events.target.parentNode.parentNode.tagName === "A") {
            return events.target.parentNode.parentNode;
          } else {
            return null;
          }
        }
      }
    }
</script>

<style lang="scss" scoped>
  @import url("../../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .sidebar-menu .menu-arrow:before {
    font-family: "FontAwesome";
    content: "\f105" !important;
  }
</style>