<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title">Administrative Users</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                <a v-if="searchResultUpdate && (users.length > 0)" @click.prevent="reloadDataRecords" class="btn btn-outline-danger text-danger mr-3"><span class="mb-2"><i class="fas fa-times mr-2"></i></span>Clear Search</a>
                  <router-link
                    class="btn btn-success btn-rounded float-right"
                    :to="{
                      name: 'CreateAdministrator',
                      params: {
                        administratorID: administratorID
                      }                                                
                    }"
                  ><i class="fas fa-user-plus mr-2"></i> Create Account</router-link>
              </div>
          </div>
          <div v-if="users.length <= 0" class="row">
              <div class="col-sm-8 offset-sm-2 mt-5">
                <div class="jumbotron text-center">
                    <h3 class="h3">{{emptyMessage}}</h3> <br> <a v-if="searchResultUpdate" @click.prevent="reloadDataRecords" class="btn btn-success text-white"><span class="mb-2"><i class="fas fa-arrow-circle-left mr-2"></i></span>Back</a>
                </div>
              </div>
          </div>
          <div v-else class="container-fluid">
            <div class="row filter-row">
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">User ID</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.userID">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Fullname</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.fullname">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Email</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.email">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                  <a @click.prevent="searchUsers" class="btn btn-success btn-block"> Search </a>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12">
                <div class="table-responsive">
                  <table class="table table-striped custom-table">
                    <thead>
                      <tr>
                        <th style="min-width:200px;">Fullname</th>
                        <th>User ID</th>
                        <th>Email</th>
                        <th>Account Type</th>
                        <th>User Role</th>
                        <th class="text-right">Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="user in users"
                        :key="user.userID"
                      >
                        <td>
                          <img width="28" height="28" :src="users.avatar" class="rounded-circle" alt=""> <h2>{{user.fullname}}</h2>
                        </td>   
                        <td>{{user.userID}}</td>
                        <td>{{user.email}}</td>
                        <td>{{user.accountType}}</td>
                        <td>{{user.userRole}}</td>
                        <td class="text-right">
                          <div class="dropdown dropdown-action">
                            <a v-if="user.userRole !== 'super user'" @click.prevent="toggleActionMenuOptions($event)" class="action-icon dropdown-toggle" style="cursor: pointer;" aria-expanded="false"><i disabled class="fa fa-ellipsis-v"></i></a>
                            <div class="dropdown-menu dropdown-menu-right" style="position: absolute; will-change: transform; top: -38px; left: -55px; transform: translate3d(-63px, 27px, 0px);">
                              <a @click.prevent="requestUserAccount(user.userID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-trash m-r-5"></i> Delete</a>
                            </div>                            
                          </div>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'

  import { searchWithThreeParameters } from '../../helpers/searchWithThreeParameters'
  import { searchWithTwoParameters } from '../../helpers/searchWithTwoParameters'
  import { searchWithOneParameter } from '../../helpers/searchWithOneParameter'

  export default {
    name: "AdministrativeUsers",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'administrative users',
        administratorID: null,
        emptyMessage: "User list is empty.",
        searchResultUpdate: false,
        users: [],
        search: {
          userID: "",
          fullname: "",
          email: ""
        },
      }
    },

    computed: {
      loggedUserDetails() {
        return this.$store.getters.loggedUser;
      },

      isSuperUser() {
        return this.loggedUserDetails.userRole === "super user"
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      async toggleActionMenuOptions(event) {
        let targetElement = this.getTargetElement(event);

        if (targetElement.parentNode.classList.contains('show')) {
          targetElement.parentNode.classList.remove('show');
          targetElement.nextElementSibling.classList.remove('show');
        } else {
          await this.closeAllOpenedActionMenuOptions()
          targetElement.parentNode.classList.add('show');
          targetElement.nextElementSibling.classList.add('show');
        }
      },

      getTargetElement(events) {
        if (events.target.tagName === "A") {
          return events.target;
        } else if (events.target.parentNode.tagName === "A") {
          return events.target.parentNode;
        } else if (events.target.parentNode.parentNode.tagName === "A") {
          return events.target.parentNode.parentNode;
        } else {
          return null;
        }
      },

      async closeAllOpenedActionMenuOptions() {
        const allActionMenuOption = document.querySelectorAll(".dropdown-action")

        for (let i=0; i<allActionMenuOption.length; i++) {
          if (allActionMenuOption[i].classList.contains('show')) {
            allActionMenuOption[i].classList.remove('show');
          }
        }
      },

      requestUserAccount(userID) {
        if (confirm("Delete administrator account?")) {
          db.collection("system_administrators").where('userID', '==', userID).get()
            .then( querySnapshot => {
              querySnapshot.forEach( doc => {
                  doc.ref.delete()
                  this.users.map(user => {
                    if (user.userID === userID) {
                      this.users.splice(this.users.indexOf(user), 1)
                    }
                  })
              })
            })
        }
      },

      async getUsersFromFirebase() {
        const temp = []
        await db.collection("system_administrators").orderBy("fullname").get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'userID': doc.data().userID,
              'fullname': doc.data().fullname,
              'email': doc.data().email,
              'userRole': doc.data().userRole,
              'accountType': doc.data().accountType
            }                                                                                                                                               
              temp.push(data)
            })
          })

        return temp
      },

      async searchUsersWithThreeParameters(label1, param1, label2, param2, label3, param3) {
        const allUsers = await this.getUsersFromFirebase()
        const temp = searchWithThreeParameters(allUsers, label1, param1, label2, param2, label3, param3)
        
        this.users = temp

        if (this.users.length  >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },

      async searchUsersWithTwoParameters(label1, param1, label2, param2) {
        const allUsers = await this.getUsersFromFirebase()
        const temp = searchWithTwoParameters(allUsers, label1, param1, label2, param2)
        
        this.users = temp

        if (this.users.length >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },
      
      async searchUsersWithOneParameters(label1, param1) {
        const allUsers = await this.getUsersFromFirebase()
        const temp = searchWithOneParameter(allUsers, label1, param1)
        
        this.users = temp

        if (this.users.length  >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },

      async reloadDataRecords() {
        this.users = await this.getUsersFromFirebase()
        this.emptyMessage = "User list is empty."
        this.searchResultUpdate = false
        this.search.userID = ""
        this.search.fullname = ""
        this.search.email = ""
      },

      searchUsers() {
        if (this.search.userID && this.search.fullname && this.search.email) {
          this.searchUsersWithThreeParameters('userID', this.search.userID, 'fullname', this.search.fullname, 'email', this.search.email)
        } else if (
          (this.search.userID && this.search.fullname) ||
          (this.search.userID && this.search.email) ||
          (this.search.email && this.search.fullname)
        ) {
          if (this.search.userID && this.search.fullname) {
            this.searchUsersWithTwoParameters('userID', this.search.userID, 'fullname', this.search.fullname)
          } else if (this.search.userID && this.search.email) {
            this.searchUsersWithTwoParameters('userID', this.search.userID, 'email', this.search.email)
          } else if (this.search.email && this.search.fullname) {
            this.searchUsersWithTwoParameters('email', this.search.email, 'fullname', this.search.fullname)
          }
        } else if (this.search.userID || this.search.fullname || this.search.email) {
          if (this.search.userID) {
            this.searchUsersWithOneParameters('userID', this.search.userID)
          } else if (this.search.fullname) {
            this.searchUsersWithOneParameters('fullname', this.search.fullname)
          } else if (this.search.email) {
            this.searchUsersWithOneParameters('email', this.search.email)
          }
        }
      },

      async updateSearchOperation() {
        if (!this.search.userID && !this.search.fullname && !this.search.email) {
          this.users = await this.getUsersFromFirebase()
          this.emptyMessage = "User list is empty."
        }
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);

      if (!this.isSuperUser) {
        alert("Access denied. You are not permitted to view the requested resource.")
        this.$router.push({
          name: 'AdministratorDashboard',
          params: {
            administratorID: this.administratorID
          }
        })
      }
    },

    async created() {
      this.administratorID = this.$route.params.administratorID
      this.users = await this.getUsersFromFirebase()
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
