const UPDATE_LOGIN = (state, payload) => {
    const stringifiedLoggedUserDetails = JSON.stringify(payload.loggedUser);

    localStorage.setItem('LOGGED_USER', stringifiedLoggedUserDetails);
    localStorage.setItem('GENERAL_CHAT_CHANNELS', JSON.stringify([]))
    localStorage.setItem('PRIVATE_CHAT_CHANNELS', JSON.stringify([]))
    localStorage.setItem('PROSPECT_CHAT_CHANNELS', JSON.stringify([]))

    state.loggedUser = stringifiedLoggedUserDetails;
}

export default UPDATE_LOGIN;