<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-7 col-3">
                  <h4 class="page-title">{{pageTitle}}</h4>
              </div>
              <div class="col-sm-5 col-9 text-right m-b-20">
                  <router-link
                    class="btn btn-outline-success float-right"
                    :to="{
                      name: 'InstitutionExaminationCourseQuestions',
                      params: {
                        institutionID: institutionID,
                        classroomID: classroomID,
                        examinationID: examinationID,
                        timetableID: timetableID,
                        courseID: courseID
                      }                                                
                    }"
                  ><i class="fas fa-chevron-right mr-2"></i>Proceed to Questions</router-link>    

                  <router-link
                    class="btn btn-outline-danger float-right mr-2"
                    :to="{
                      name: 'InstitutionClassroomDetails',
                      params: {
                        institutionID: institutionID,
                        classroomID: classroomID
                      }                                                
                    }"
                  ><i class="fas fa-times mr-2"></i>Cancel</router-link>                  
              </div>
          </div>
          <div v-if="!isSelectedCourseQuestionCreated" class="row">
              <div class="col-lg-8 offset-lg-2">
                <form>
									<div class="row">
                    <div class="col-sm-12 pr-3 pl-3">
                      Lorem ipsum dolor sit amet consectetur adipisicing elit. Minima, quos ducimus fugit quas velit hic magni quisquam, distinctio facere ullam rem dicta quaerat in veniam nam itaque excepturi nostrum harum!
                    </div>
                  </div> 
                  <div class="row mt-5">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Title</label>
                        <input @input="updateErrorStatusOnInput('title')" class="form-control" type="text" v-model="form.title">
                        <small v-if="errors.title" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.title}}</small>
                      </div>
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Description</label>
                        <textarea @input="updateErrorStatusOnInput('description')" class="form-control" v-model="form.description"></textarea>
                        <small v-if="errors.description" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.description}}</small>
                      </div>
                    </div>
                  </div>                          
                  <hr class="mb-3">
                  <div class="form-group text-center">
                    <button v-if="!isLoading" @click.prevent="attemptCreatingExaminationRecord" class="btn btn-lg btn-success btn-block">Create Examination Questions</button>
                    <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                      <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                      Loading...      
                    </button>
                  </div>
                </form>
              </div>            
          </div>
<div v-if="isSelectedCourseQuestionCreated" class="container-fluid">
          <div class="row">
            <h3 class="h2 text-center col-12">{{questionInstruction.title}}</h3>
            <h4 class="h4 text-center col-12">{{'February, 2021.'}}</h4>
            <div v-for="(uiElement, index) in questionInstruction.details" :key="index" class="container ui-item-container mt-2 b mb-2">
              <button v-if="hasUpdateBeenEnable" @click.prevent="deleteUIElement(uiElement, index)" class="badge badge-pill badge-danger"><i class="fas fa-times fa-2x"></i></button>
              <div class="mt-4" v-html="uiElement.elementString"></div>
            </div>

            <form class="col-sm-8 offset-sm-2 mt-5" style="margin-bottom: 400px;">
              <div class="form-group">
                <button v-if="!isEmptyObject(questionInstruction) && questionInstruction.details.length > 0 && !isLoading && hasUpdateBeenEnable" @click.prevent="publishUIElementView" class="btn btn-lg btn-success btn-block"><i class="fas fa-upload mr-2"></i>Publish Examination Instructions</button>
                <button v-if="isLoading" :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                  <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                  Loading...      
                </button>              
              </div>
            </form>
          </div>

          <div class="ui-input-element-container bg-light pt-3 col-10">
            <div class="row">
              <addinstruction-heading :tagName="selectedUITag" v-if="selectedUITag === 'h4' || selectedUITag === 'h3'"></addinstruction-heading>
              <addinstruction-paragraph :tagName="selectedUITag" v-if="selectedUITag === 'p'"></addinstruction-paragraph>
              <addinstruction-list :tagName="selectedUITag" v-if="selectedUITag === 'ul' || selectedUITag === 'ol'"></addinstruction-list>              
            </div>
            <form class="row">
              <div class="col-sm-10 offset-sm-1">
                <div class="row">
                  <div class="offset-sm-1" :class="{'col-sm-8': selectedUITag !== '', 'col-sm-10': selectedUITag === ''}">
                    <div class="form-group">
                      <select class="form-control text-center" v-model="selectedUITag">
                        <option value="">Select UI Element</option>
                        <option value="h3">Main Heading (h3 tag)</option>
                        <option value="h4">Small Heading (h4 tag)</option>
                        <option value="p">Paragraph (p tag)</option>
                        <option value="ul">Unordered List (ul tag)</option>
                        <option value="ol">Ordered List (ol tag)</option>
                      </select>
                    </div>
                  </div>
                  <div :class="{'col-sm-2': selectedUITag !== '', '': selectedUITag === ''}">
                    <div class="form-group">
                    <button v-if="selectedUITag !== ''" @click.prevent="enableSelectedUIElementView" class="btn btn-lg btn-danger btn-block"><i class="fas fa-times mr-2"></i>Cancel</button>
                    </div>
                  </div>
                </div>
              </div>        
            </form>
          </div>
</div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'
  import AddInstuctionHeading from './AddInstuctionHeading'
  import AddInstuctionParagraph from './AddInstuctionParagraph'
  import AddInstuctionList from './AddInstuctionList'

  import { db } from '../../firebase/firebaseInit'

  import uuid from 'uuid'
  import { isEmptyObject } from '../../helpers/isEmptyObject'

  export default {
    name: "EditClassroomDetails",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages,
      'addinstruction-heading': AddInstuctionHeading,
      'addinstruction-paragraph': AddInstuctionParagraph,
      'addinstruction-list': AddInstuctionList
    },

    data() {
      return {
        isLoading: false,
        active: 'registered institutions',
        institutionID: null,
        classroomID: null,
        courseID: null,
        examinationID: null,
        timetableID: null,
        institution: {},
        course: {},
        question: {},
        questionInstruction: {},

        isSelectedCourseQuestionCreated: false,

        pageTitle: "",

        form: {
          title: "",
          description: ""
        },
        errors: {
          title: "",
          description: ""
        },

        selectedUITag: "",
        hasUpdateBeenEnable: false
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },

      isInstructorLogin() {
        return this.loggedUser.userRole === "institution staff"
      },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      updateUIElements(headerInstance) {
        if (this.questionInstruction) {
          this.questionInstruction.details.push(headerInstance)
          this.enableSelectedUIElementView()
          this.hasUpdateBeenEnable = true
        }
      },

      deleteUIElement(uiElement, index) {
        const tagInfo = this.getTagNameDescription(uiElement.tagName)
        if (confirm(`Delete ${tagInfo} at position ${index+1}`)) {
          this.questionInstruction.details.splice(index, 1)
        }
      },

      getTagNameDescription(tagName) {
        let desc = ""
        if (tagName === 'h3' || tagName === 'h4') {
          desc = "UI heading item"
        } else if (tagName === 'p') {
          desc = "UI paragraph item"
        } else if (tagName === 'ol' || tagName === 'ul') {
          desc = "UI list group item"
        }
        return desc
      },

      enableSelectedUIElementView() {
        this.selectedUITag = ""
      },

      isEmptyObject(obj) {
        return isEmptyObject(obj)
      },

      validateForm() {
        if (!this.form.title) {
            this.errors.title = "Title is required!"
        }

        if(this.form.title) {
            return true
        } else {
            return false
        }
      },

      async attemptCreatingExaminationRecord() {
        this.isLoading = true
        if ( this.isSelectedCourseQuestionCreated ) {
          await this.getQuestionObject()
          this.isLoading = false
          return
        }

        if (!this.validateForm()) {
          this.isLoading = false; 
          return;
        }

        const questionID = uuid.v1()
        db.collection("questions").add({
          'questionID': questionID,
          'institutionID': this.institutionID,
          'classroomID': this.classroomID,
          'courseID': this.courseID,
          'examinationID': this.examinationID,
          'timetableID': this.timetableID,
          'createdOn': new Date().getTime()
        }).then(async (res) => {
          console.log(res)
          const questionInstructionID = uuid.v4()
          db.collection("question_instructions").add({
            'questionInstructionID': questionInstructionID,
            'questionID': questionID,
            'title': this.form.title,
            'details': JSON.stringify([]),
            'description': this.form.description,
            'createdOn': new Date().getTime()
          }).then(async (res) => {
            console.log(res)
            await this.getQuestionObject()
            this.isLoading = false
          })
        }).catch(error => {
          this.isLoading = false
          alert(error)
        });
      },

      updateErrorStatusOnInput(fieldName) {
        if(this.form[fieldName].toString().length > 0){
          this.errors[fieldName] = ""
          this.errors.message = ""
        }
      },

      getQuestionObject() {
        db.collection("questions").where("timetableID", "==", this.timetableID).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              if (doc.data().institutionID === this.institutionID && this.classroomID === doc.data().classroomID && doc.data().examinationID === this.examinationID && doc.data().courseID === this.courseID) {
                this.question.questionID = doc.data().questionID
                this.question.institutionID = doc.data().institutionID
                this.question.classroomID = doc.data().classroomID
                this.question.courseID = doc.data().courseID
                this.question.examinationID = doc.data().examinationID
                this.question.timetableID = doc.data().timetableID
                this.question.createdOn = doc.data().createdOn
              }
            })
            if (!this.isEmptyObject(this.question)) {
              this.isSelectedCourseQuestionCreated  = true
              this.pageTitle = `Manage ${this.course.title} (${this.course.code})'s Examination Question`
              this.hasUpdateBeenEnable = false

              db.collection("question_instructions").where("questionID", "==", this.question.questionID).get()
                .then(querySnapshot => {
                  querySnapshot.forEach(doc => {
                    this.questionInstruction.questionInstructionID = doc.data().questionInstructionID
                    this.questionInstruction.questionID = doc.data().questionID
                    this.questionInstruction.title = doc.data().title
                    this.questionInstruction.details = JSON.parse(doc.data().details)
                    this.questionInstruction.description = doc.data().description
                    this.questionInstruction.createdOn = doc.data().createdOn
                  })
                })  
            } else {
              this.pageTitle = `Create ${this.course.title} (${this.course.code})'s Examination Question`
            }
          })        
      },

      publishUIElementView() {
        this.isLoading = true
        db.collection("question_instructions").where('questionID', '==', this.question.questionID).get()
        .then( querySnapshot => {
            querySnapshot.forEach(async doc => {
                doc.ref.update({
                    "details": JSON.stringify(this.questionInstruction.details)
                })
                this.isLoading = false
                await this.getQuestionObject()
            })
        }).catch(errors => {
          this.isLoading = false
          alert(errors)
        })
      }
    },

    async mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
      window.EventBus.on('instruction-ui-element-added', this.updateUIElements);

      if (!this.isInstructorLogin) {
        alert("You are NOT allowed to view this page!!!")
        this.$router.push({
          name: 'InstitutionClassroomDetails',
          params: {
            institutionID: this.institutionID,
            classroomID: this.classroomID
          }
        })
      }

      await this.getQuestionObject()
    },

    created() {
      this.institutionID = this.$route.params.institutionID
      this.classroomID = this.$route.params.classroomID
      this.courseID = this.$route.params.courseID
      this.examinationID = this.$route.params.examinationID
      this.timetableID = this.$route.params.timetableID

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

        db.collection("courses").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              if (doc.data().courseID === this.courseID) {
                this.course.id =  doc.id
                this.course.courseID =  doc.data().courseID
                this.course.institutionID =  doc.data().institutionID
                this.course.title =  doc.data().title
                this.course.grade =  doc.data().grade
                this.course.code =  doc.data().code
                this.course.duration =  doc.data().duration
                this.course.durationUnit =  doc.data().durationUnit
                this.course.instructor =  doc.data().instructor
                this.course.description =  doc.data().description
                this.course.createdOn =  doc.data().createdOn
              }                                                                                                               
            })
          })
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }

  .ui-input-element-container {
    position: fixed;
    bottom: 20px;
  }

  .ui-item-container {
    position: relative;

    button {
      position: absolute;
      top: 0;
      right: 0;
    }
  }
</style>
