<template>
    <div class="chat-footer">
        <div class="message-bar">
            <div class="message-inner">
                <!-- <a class="link attach-icon" href="#" data-toggle="modal" data-target="#drag_files"><img src="assets/img/attachment.png" alt=""></a> -->
                <div class="message-area">
                    <div class="input-group">
                        <textarea class="form-control" placeholder="Type message..." v-model="message"></textarea>
                        <span class="input-group-append">
                            <button @click.prevent="attemptSendingMessage" class="btn btn-success" type="button"><i class="fa fa-send"></i></button>
                        </span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { db } from '../../firebase/firebaseInit'

    import uuid from 'uuid'

    export default {
        name: "ChatFooter",

        data() {
            return {
                message: "",
                loggedUserChatAccount: {},
                messageError: ""
            }
        },

        computed: {
            loggedUserDetails() {
              return this.$store.getters.loggedUser
            },
        },

        methods: {
            attemptSendingMessage() {
                if (this.message) {
                    const tempMessage = this.message
                    db.collection("chats").add({
                        "chatID": uuid.v4(),
                        "sender": this.loggedUserChatAccount.accountID,
                        "recipient": this.$route.params.chatMateID,
                        "message": tempMessage,
                        "status": 'unread',
                        "createdOn": new Date().getTime()
                    }).then(()=> {
                        
                    }).catch(() => {
                        this.message = tempMessage
                        this.messageError = `Failed to send message!!`
                    })
                    this.message = ""
                }
            }
        },

        created() {
            db.collection("chat_accounts").where('userID', '==', this.loggedUserDetails.userID).get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                this.loggedUserChatAccount.accountID = doc.data().accountID
                this.loggedUserChatAccount.userID = doc.data().userID
                this.loggedUserChatAccount.accountType = doc.data().accountType
                this.loggedUserChatAccount.userType = doc.data().userType
                this.loggedUserChatAccount.referenced_db = doc.data().referenced_db
                this.loggedUserChatAccount.referenceName = doc.data().referenceName
              })
            })
        }
    }
</script>