import {customizeDate} from './customizeDate'

class TimeTable {
    constructor(dailyActivityStartTime, activityDuration, activitiesPerDay, examinationStartDate, examinationEndDate, firstBreak, secondBreak) {
        this.dailyActivityStartTime = dailyActivityStartTime
        this.activityDuration = activityDuration
        this.firstBreak = firstBreak
        this.secondBreak = secondBreak
        this.activitiesPerDay = activitiesPerDay
        this.examinationStartDate = examinationStartDate
        this.examinationEndDate = examinationEndDate
        this.dailyActivityDuration = null
        this.dailyActivityHours = null
        this.activityHistory = []
        this.activityLayout = []

        this.activeWeekDays = ["sunday", "monday", "tuesday", "wednesday", "thursday", "friday", "saturday"]
    }

    getAllActivityTime() {
        return this.firstBreak + this.secondBreak + (this.activitiesPerDay * this.activityDuration)
    }

    padNumber(numberToPad) {
        if (numberToPad >= 0 && numberToPad < 10) {
            return `0${numberToPad}`
        } else {
            return `${numberToPad}`
        }
    }

    setDailyActivityDuration() {
        const activityTotalTime = this.getAllActivityTime()
        let hours = parseInt(activityTotalTime/60)
        let minutes = activityTotalTime%60
        this.dailyActivityHours = `${hours}.${minutes} hours`
        this.dailyActivityDuration = `${this.padNumber(hours)}:${this.padNumber(minutes)}:01`
    }

    generateActivityLayout() {
        switch(this.activitiesPerDay) {
            case 3:
                this.activityLayout = [1, 1, 1]
                break
            case 4:
                this.activityLayout = [2, 1, 1]
                break
            case 5:
                this.activityLayout = [2, 2, 1]
                break
            case 6:
                this.activityLayout = [3, 2, 1]
                break
            case 7:
                this.activityLayout = [3, 2, 2]
                break
            case 8:
                this.activityLayout = [3, 3, 2]
                break
            case 9:
                this.activityLayout = [4, 3, 2]
                break
            case 10:
                this.activityLayout = [5, 3, 2]
                break
            default:
                this.activityLayout =  []
                break
        }
    }

    generateDailyActivityDetails(weekDayID) {
        const targetDaySummary = []
        this.generateActivityLayout()
        this.setDailyActivityDuration()
        let elapseTime = 0
        let counter = 0
        let activityStartTimePlaceholder = `${this.dailyActivityStartTime}:00:00`
        let breakLabel = ""
        let breakTimeDuration = 0
        let generatePaperAllocation = null

        for (let i=0; i<this.activityLayout.length; i++) {
            for (let index=0; index<this.activityLayout[i]; index++) {
                elapseTime += this.activityDuration
                counter += 1
                generatePaperAllocation = this.generateExaminationPaperAllotedSpace(weekDayID, "Paper", elapseTime, counter, activityStartTimePlaceholder)
                targetDaySummary.push(generatePaperAllocation[0])
                activityStartTimePlaceholder = generatePaperAllocation[1]
            }

            if (i < (this.activityLayout.length - 1)) {
                breakLabel = i === 0 ? "First Break" : "Second Break"
                breakTimeDuration = i === 0 ? this.firstBreak : this.secondBreak
                elapseTime += breakTimeDuration
                counter += 1
                generatePaperAllocation = this.generateExaminationPaperAllotedSpace(weekDayID, breakLabel, elapseTime, counter, activityStartTimePlaceholder)
                targetDaySummary.push(generatePaperAllocation[0])
                activityStartTimePlaceholder = generatePaperAllocation[1]
            }  
        }

        return targetDaySummary
    }

    generateExaminationPaperAllotedSpace(weekDayID, paperLabel, elapseTime, counter, activityStartTimePlaceholder) {
        const paperAllocation = {}
        let stopHours = parseInt((elapseTime/60), 10)
        let stopMinutes = elapseTime%60

        paperAllocation.paperID = this.getPaperID(weekDayID, paperLabel, counter)
        paperAllocation.courseID = ""
        paperAllocation.start = activityStartTimePlaceholder
        paperAllocation.end = `${this.padNumber(this.dailyActivityStartTime + stopHours)}:${this.padNumber(stopMinutes)}:00`
        paperAllocation.isDayInfo = false

        return [paperAllocation, `${this.padNumber(this.dailyActivityStartTime + stopHours)}:${this.padNumber(stopMinutes)}:01`]
    }

    getPaperID(weekDayID, paperLabel, counter) {
        let paperLabelArray = paperLabel.split(" ")
        return `${weekDayID}-${paperLabelArray.length == 1 ? `${paperLabelArray[0].toLowerCase()}-${counter}` : `${paperLabelArray[0].toLowerCase()}-${paperLabelArray[1].toLowerCase()}`}`
    }

    generateTimeTableDetails() {
        let startTime = this.examinationStartDate.getTime(), endTime = this.examinationEndDate.getTime(), oneDateInMilliSeconds = 86400000;
        const timetableDetails = []

        for (let loopFactor = startTime; loopFactor <= endTime; loopFactor += oneDateInMilliSeconds) {
            const targetDate = new Date(loopFactor);
            if (targetDate.getDay() > 0 && targetDate.getDay() < 6) {
                let summary = []
                const dayInfo = {}
                dayInfo.paperID = `${loopFactor}`
                dayInfo.date = customizeDate(loopFactor)
                dayInfo.weekday = this.activeWeekDays[targetDate.getDay()]
                dayInfo.week = ""
                dayInfo.isDayInfo = true
                summary = this.generateDailyActivityDetails(`${loopFactor}`)
                summary.unshift(dayInfo)
                timetableDetails.push(summary)
            }
        }

        return timetableDetails
    }
}

export const generateTimeTable = (dailyActivityStartTime, activityDuration, activitiesPerDay, examinationStartDate, examinationEndDate, firstBreak, secondBreak) => {
    const timeTable = new TimeTable(dailyActivityStartTime, activityDuration, activitiesPerDay, examinationStartDate, examinationEndDate, firstBreak, secondBreak)
    return timeTable.generateTimeTableDetails()
}