<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title">Registered Institutions</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                  <a v-if="searchResultUpdate && (institutions.length > 0)" @click.prevent="reloadDataRecords" class="btn btn-outline-danger text-danger mr-3"><span class="mb-2"><i class="fas fa-times mr-2"></i></span>Clear Search</a>
                  <router-link
                    class="btn btn-success btn-rounded float-right"
                    :to="{
                      name: 'RegisterInstitution',
                      params: {
                        administratorID: administratorID
                      }                                                
                    }"
                  ><i class="fa fa-plus mr-2"></i> Register Institution</router-link>
              </div>
          </div>
          <div v-if="institutions.length <= 0" class="row">
              <div class="col-sm-8 offset-sm-2 mt-5">
                <div class="jumbotron text-center">
                    <h3 class="h3">{{emptyMessage}}</h3> <br> <a v-if="searchResultUpdate" @click.prevent="reloadDataRecords" class="btn btn-success text-white"><span class="mb-2"><i class="fas fa-arrow-circle-left mr-2"></i></span>Back</a>
                </div>
              </div>
          </div>
          <div v-else class="container-fluid">
            <div class="row filter-row">
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Institution ID</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.institutionID">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Institution Name</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.name">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Institution Email</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.email">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                  <a @click.prevent="searchRegisteredInstitution" class="btn btn-success btn-block text-white"> Search </a>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12">
                <div class="table-responsive">
                  <table class="table table-striped custom-table">
                    <thead>
                      <tr>
                        <th style="min-width:200px;">Name</th>
                        <th>Institution ID</th>
                        <th>Email</th>
                        <th>Mobile</th>
                        <th style="min-width: 110px;">Reg. Number</th>
                        <th>Registered On</th>
                        <th class="text-right">Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="institution in institutions"
                        :key="institution.institutionID"
                      >
                        <td>
                          <img width="28" height="28" :src="institution.avatar" class="rounded-circle" alt=""> <h2>{{institution.name}}</h2>
                        </td>   
                        <td>{{institution.institutionID}}</td>
                        <td>{{institution.email}}</td>
                        <td>{{institution.phone}}</td>
                        <td>{{institution.registrationNumber}}</td>
                        <td>
                          {{customizeDate(parseInt(institution.createdOn))}}
                        </td>
                        <td class="text-right">
                          <div class="dropdown dropdown-action">
                            <a @click.prevent="toggleActionMenuOptions($event)" class="action-icon dropdown-toggle" style="cursor: pointer;" aria-expanded="false"><i class="fa fa-ellipsis-v"></i></a>
                            <div v-if="canViewOption" class="dropdown-menu dropdown-menu-right" style="position: absolute; will-change: transform; top: -50px; left: -55px; transform: translate3d(-63px, 27px, 0px);">
                              <a @click.prevent="loadSelectedInstitutionPage(institution.institutionID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-cogs m-r-5"></i> Manage</a>
                              <a v-if="canViewOption" @click.prevent="requestDeleteInstitutionOperation(institution.institutionID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-trash m-r-5"></i> Delete</a>
                            </div>
                            <div v-if="!canViewOption" class="dropdown-menu dropdown-menu-right" style="position: absolute; will-change: transform; top: -38px; left: -55px; transform: translate3d(-63px, 27px, 0px);">
                              <a @click.prevent="loadSelectedInstitutionPage(institution.institutionID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-cogs m-r-5"></i> Manage</a>
                              <a v-if="canViewOption" @click.prevent="requestDeleteInstitutionOperation(institution.institutionID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-trash m-r-5"></i> Delete</a>
                            </div>                            
                          </div>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'
  import { customizeDate } from '../../helpers/customizeDate'

  import { searchWithThreeParameters } from '../../helpers/searchWithThreeParameters'
  import { searchWithTwoParameters } from '../../helpers/searchWithTwoParameters'
  import { searchWithOneParameter } from '../../helpers/searchWithOneParameter'

  export default {
    name: "RegisteredInstitutions",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'registered institutions',
        administratorID: null,
        emptyMessage: "Institution list is empty.",
        searchResultUpdate: false,
        search: {
          institutionID: "",
          name: "",
          email: ""
        },

        institutions: []
      }
    },

    computed: {
      loggedUserDetails() {
          return this.$store.getters.loggedUser;
      },

      canViewOption() {
        return this.loggedUserDetails.userRole === 'super user'
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      async toggleActionMenuOptions(event) {
        let targetElement = this.getTargetElement(event);

        if (targetElement.parentNode.classList.contains('show')) {
          targetElement.parentNode.classList.remove('show');
          targetElement.nextElementSibling.classList.remove('show');
        } else {
          await this.closeAllOpenedActionMenuOptions()
          targetElement.parentNode.classList.add('show');
          targetElement.nextElementSibling.classList.add('show');
        }
      },

      getTargetElement(events) {
        if (events.target.tagName === "A") {
          return events.target;
        } else if (events.target.parentNode.tagName === "A") {
          return events.target.parentNode;
        } else if (events.target.parentNode.parentNode.tagName === "A") {
          return events.target.parentNode.parentNode;
        } else {
          return null;
        }
      },

      async closeAllOpenedActionMenuOptions() {
        const allActionMenuOption = document.querySelectorAll(".dropdown-action")

        for (let i=0; i<allActionMenuOption.length; i++) {
          if (allActionMenuOption[i].classList.contains('show')) {
            allActionMenuOption[i].classList.remove('show');
          }
        }
      },

      customizeDate(timestamp) {
        return customizeDate(timestamp)
      },

      loadSelectedInstitutionPage(institutionID) {
        this.$router.push({
          name: 'RegisteredInstitutionDetails',
          params: {
            administratorID: this.administratorID,
            institutionID: institutionID
          }
        })
      },

      async requestDeleteInstitutionOperation(institutionID) {
        if (confirm(`Do you want to Delete ${this.getInstitutionNameByID(institutionID)} institution records?`)) {
          await this.closeAllOpenedActionMenuOptions()
          db.collection("institutions").where('institutionID', '==', institutionID).get()
            .then( querySnapshot => {
              querySnapshot.forEach( doc => {
                  doc.ref.delete()
                  db.collection("administrator_activities").add({
                    'userID': this.loggedUserDetails.userID,
                    'connectorPhrase': 'deleted institution record',
                    'subject': this.getInstitutionNameByID(institutionID),
                    'action': 'delete institution',
                    'createdAt': new Date().getTime()
                  }).then(res => { console.log(res)})
                  this.institutions.map(institution => {
                    if (institution.institutionID === institutionID) {
                      this.institutions.splice(this.institutions.indexOf(institution), 1)
                    }
                  })
              })
            })
        }
      },

      getInstitutionNameByID(institutionID) {
        let name = "Unknown"

        this.institutions.map(institution => {
          if (institution.institutionID === institutionID) {
            name = institution.name
          }
        })

        return name
      },

      async getInstitutionsFromFirebase() {
        const temp = []
        await db.collection("institutions").orderBy("name").get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              const data = {
                'id': doc.id,
                'institutionID': doc.data().institutionID,
                'name': doc.data().name,
                'avatar': doc.data().avatar,
                'email': doc.data().email,
                'phone': doc.data().phone,
                'registrationNumber': doc.data().registrationNumber,
                'address': doc.data().address,
                'country': doc.data().country,
                'stateProvince': doc.data().stateProvince,
                'city': doc.data().city,
                'accountType': doc.data().accountType,
                'status': doc.data().status,
                'createdOn': doc.data().createdOn
              }
              temp.push(data)
            })
          })

          return temp
      },

      async searchRegisteredInstitutionWithThreeParameters(label1, param1, label2, param2, label3, param3) {
        const allInstitutions = await this.getInstitutionsFromFirebase()
        const temp = searchWithThreeParameters(allInstitutions, label1, param1, label2, param2, label3, param3)
        
        this.institutions = temp

        if (this.institutions.length  >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },

      async reloadDataRecords() {
        this.institutions = await this.getInstitutionsFromFirebase()
        this.emptyMessage = "Institution list is empty."
        this.searchResultUpdate = false
        this.search.institutionID = ""
        this.search.name = ""
        this.search.email = ""
      },

      async searchRegisteredInstitutionWithTwoParameters(label1, param1, label2, param2) {
        const allInstitutions = await this.getInstitutionsFromFirebase()
        const temp = searchWithTwoParameters(allInstitutions, label1, param1, label2, param2)
        
        this.institutions = temp

        if (this.institutions.length >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },
      
      async searchRegisteredInstitutionWithOneParameters(label1, param1) {
        const allInstitutions = await this.getInstitutionsFromFirebase()
        const temp = searchWithOneParameter(allInstitutions, label1, param1)
        
        this.institutions = temp

        if (this.institutions.length >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },

      searchRegisteredInstitution() {
        if (this.search.institutionID && this.search.name && this.search.email) {
          this.searchRegisteredInstitutionWithThreeParameters('institutionID', this.search.institutionID, 'name', this.search.name, 'email', this.search.email)
        } else if (
          (this.search.institutionID && this.search.name) ||
          (this.search.institutionID && this.search.email) ||
          (this.search.email && this.search.name)
        ) {
          if (this.search.institutionID && this.search.name) {
            this.searchRegisteredInstitutionWithTwoParameters('institutionID', this.search.institutionID, 'name', this.search.name)
          } else if (this.search.institutionID && this.search.email) {
            this.searchRegisteredInstitutionWithTwoParameters('institutionID', this.search.institutionID, 'email', this.search.email)
          } else if (this.search.email && this.search.name) {
            this.searchRegisteredInstitutionWithTwoParameters('email', this.search.email, 'name', this.search.name)
          }
        } else if (this.search.institutionID || this.search.name || this.search.email) {
          if (this.search.institutionID) {
            this.searchRegisteredInstitutionWithOneParameters('institutionID', this.search.institutionID)
          } else if (this.search.name) {
            this.searchRegisteredInstitutionWithOneParameters('name', this.search.name)
          } else if (this.search.email) {
            this.searchRegisteredInstitutionWithOneParameters('email', this.search.email)
          }
        }
      },

      async updateSearchOperation() {
        if (!this.search.institutionID && !this.search.name && !this.search.email) {
          this.institutions = await this.getInstitutionsFromFirebase()
          this.emptyMessage = "Institution list is empty."
        }
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    async created() {
      this.administratorID = this.$route.params.administratorID
      this.institutions = await this.getInstitutionsFromFirebase()
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
