<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title">Create Classroom</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                <router-link 
                    :to="{
                        name: 'InstitutionClassrooms',
                        params: {
                            institutionID: institutionID,
                        }
                    }" 
                    class="btn btn-outline-danger"
                  >
                    <span class="mb-2"><i class="fas fa-times mr-2"></i></span>Cancel
                  </router-link>
              </div>
          </div>
          <div class="row">
              <div class="col-lg-8 offset-lg-2">
                <form>
									<div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Classroom ID</label>
                        <input @input="updateErrorStatusOnInput('classroomID')" type="text" class="form-control text-center" readonly v-model="form.classroomID">
                        <small v-if="errors.classroomID" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.classroomID}}</small>
                      </div>
                    </div>
                  </div>         
                  <div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Name <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('name')" class="form-control" type="text" v-model="form.name">
                        <small v-if="errors.name" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.name}}</small>
                      </div>
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Grade/Year<span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('grade')" class="form-control" type="text" v-model="form.grade">
                        <small v-if="errors.grade" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.grade}}</small>
                      </div>
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Capacity <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('capacity')" class="form-control" type="email" v-model="form.capacity">
                        <small v-if="errors.capacity" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.capacity}}</small>
                      </div>
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Overseen By</label>
                        <select @change="updateErrorStatusOnInput('overSeenBy')" class="form-control select" v-model="form.overSeenBy">
                          <option value="">Select Instructor</option>
                          <option v-for="employee in employees" :key="employee.userID" :value="employee.userID">{{employee.fullname}}</option>
                        </select>
                        <small v-if="errors.overSeenBy" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.overSeenBy}}</small>
                      </div>                      
                    </div>
                  </div>
                  <hr class="mb-3">
                  <div class="form-group text-center">
                    <button v-if="!isLoading" @click.prevent="attemptCreatingClassroom" class="btn btn-lg btn-success btn-block">Create Classroom</button>
                    <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                      <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                      Loading...      
                    </button>
                  </div>
                </form>
              </div>            
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'

  import uuid from 'uuid'

  export default {
    name: "CreateClassroom",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        isLoading: false,
        active: 'create classroom',
        institutionID: null,
        institution: {},
        employees: [],
        form: {
          classroomID: "",
          name: "",
          grade: "",
          capacity: "",
          overSeenBy: ""
        },
        errors: {
          classroomID: "",
          name: "",
          grade: "",
          capacity: "",
          overSeenBy: ""
        }
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      validateForm() {
        if (!this.form.classroomID) {
            this.errors.classroomID = "Classroom ID is required!"
        }
        if (!this.form.name) {
            this.errors.name = "Name is required!"
        }
        if (!this.form.grade) {
            this.errors.grade = "Grade/Year is required!"
        }
        if (!this.form.capacity) {
            this.errors.capacity = "Capacity is required!"
        }
        if (!this.form.overSeenBy) {
            this.errors.overSeenBy = "Overseen By is required!"
        }
        if(this.form.classroomID && this.form.name && this.form.grade && this.form.capacity && this.form.overSeenBy) {
            return true
        } else {
            return false
        }
      },

      attemptCreatingClassroom() {
        this.isLoading = true
        if (!this.validateForm()) {
          this.isLoading = false; 
          return;
        }
        
        db.collection("classrooms").add({
          'classroomID': this.form.classroomID,
          'institutionID': this.institutionID,
          'name': this.form.name,
          'grade': this.form.grade,
          'capacity': parseInt(this.form.capacity),
          'numberOfStudentsAssigned': 0,
          'overSeenBy': this.form.overSeenBy,
          'createdOn': new Date().getTime()
        }).then((res) => {
          console.log(res)

          const accountID = uuid.v4()
          db.collection("chat_accounts").add({
              'accountID': accountID,
              'userID': this.form.classroomID,
              'accountType': 'general',
              'userType': "classroom group",
              'referenced_db': 'classrooms',
              'referenceName': `${this.form.name.split(" ")[0].toUpperCase()}#General`
          }).then((res) => {
            console.log(res);

            this.isLoading = false
            this.$router.push({
              name: "InstitutionClassrooms",
              params: {
                institutionID: this.institutionID,
              }
            })

          }).catch(error => {
              this.isLoading = false
              alert(error.message)
          })
        }).catch(error => {
          this.isLoading = false
          alert(error)
        });
      },

      updateErrorStatusOnInput(fieldName) {
        if(this.form[fieldName].toString().length > 0){
          this.errors[fieldName] = ""
          this.errors.message = ""
        }
      },
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    created() {
      this.institutionID = this.$route.params.institutionID
      this.form.classroomID = uuid.v1()

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

      db.collection("institution_staffs").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              if (doc.data().userRole !== 'institution administrator') {
                const data = {
                  'id': doc.id,
                  'userID': doc.data().userID,
                  'institutionID': doc.data().institutionID,
                  'fullname': doc.data().fullname,
                  'email': doc.data().email,
                  'userRole': doc.data().userRole,
                  'accountType': doc.data().accountType,
                  'address': doc.data().address,
                  'phone': doc.data().phone,
                  'gender': doc.data().gender,
                  'dob': doc.data().dob,
                  'avatar': doc.data().avatar
                }                                                                                                                                               
                this.employees.push(data)
              }
            })
          })
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
