const UPDATE_CHAT_CHANNELS = (state, payload) => {
    const stringifiedData = JSON.stringify(payload.data)

    if (payload.accountType === 'general') {
        localStorage.setItem('GENERAL_CHAT_CHANNELS', stringifiedData)
        state.generalChatChannels = payload.data
    }

    if (payload.accountType === 'prospect') {
        localStorage.setItem('PROSPECT_CHAT_CHANNELS', stringifiedData)
        state.prospectChatChannels = payload.data
    }

    if (payload.accountType === 'private') {
        localStorage.setItem('PRIVATE_CHAT_CHANNELS', stringifiedData)
        state.privateChatChannels = payload.data
    }
}

export default UPDATE_CHAT_CHANNELS;