<template>
  <div class="main-wrapper account-wrapper">
    <div class="account-page">
      <div class="account-center">
        <div class="account-box">
          <form  class="form-signin">
            <div class="account-logo">
              <router-link 
                :to="{ name: 'AdministratorLogin' }"
              >
                <div class="mb-2"><i class="fas fa-user-cog fa-5x"></i></div>
                <div class="h4">Administrator Login</div>
              </router-link>
            </div>
            <div class="form-group">
              <label>Email</label>
              <input type="email" @input="updateErrorStatusOnInput('email')" autofocus="" class="form-control" v-model="form.email">
              <small v-if="errors.email" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.email}}</small>
            </div>
            <div class="form-group">
              <label>Password</label>
              <input type="password" @input="updateErrorStatusOnInput('password')" class="form-control" v-model="form.password">
              <small v-if="errors.password" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.password}}</small>
            </div>
            <div class="form-group text-right">
              <!-- <a href="forgot-password.html">Forgot your password?</a> -->
            </div>
            <div class="form-group text-center">
              <button v-if="!isLoading" @click.prevent="attemptLogin" class="btn btn-lg btn-success btn-block">Login</button>
              <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                Loading...      
              </button>
            </div>
            <div class="form-group text-center">
              <router-link to="/" class="btn btn-outline-secondary"><span class="mb-2"><i class="fas fa-arrow-circle-left mr-2"></i></span>Back</router-link>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import { db, auth } from '../../firebase/firebaseInit'

    export default {
      name: "Login",

      data() {
        return {
          isLoading: false,

          form: {
            email: "",
            password: "",
          },
          errors: {
            email: "",
            password: "",
            message: ""
          },

          loggedUserDetails: {},
          expectedAccountType: 'system administrator'
        }
      },

      methods: {
        attemptLogin() {
          this.isLoading = true
          if (!this.validateForm()) {
            this.isLoading = false; 
            return;
          }
          auth.signInWithEmailAndPassword(this.form.email, this.form.password)
            .then( response => {
              console.log(`Login Response: ${response}`);
              db.collection("system_administrators").where('email', '==', this.form.email).get()
                .then(querySnapshot => {
                    querySnapshot.forEach( doc => {
                      this.loggedUserDetails.userID = doc.data().userID
                      this.loggedUserDetails.fullname = doc.data().fullname
                      this.loggedUserDetails.email = doc.data().email
                      this.loggedUserDetails.userRole = doc.data().userRole
                      this.loggedUserDetails.accountType = doc.data().accountType
                    })
                    this.$store.dispatch('UPDATE_LOGIN', {loggedUser: this.loggedUserDetails})   
                    .then(() => {
                      if (this.loggedUserDetails.accountType === this.expectedAccountType) {
                        this.$router.push({ name: "AdministratorDashboard", params: { administratorID: this.loggedUserDetails.userID }})
                      } else {
                        this.form.email = ""
                        this.form.password = ""
                        alert("You are NOT an administrator. Please login using the appropriate channel!!!");
                        this.attemptLogout()
                      }
                      this.isLoading = false
                    }).catch(err => { console.log('ADMINISTRATOR LOGIN STORE ERROR:', err ); this.isLoading = false })  
                })
                this.isLoading = false
            },
            err => {
                this.isLoading = false
                alert(err.message)
            })
        },

        attemptLogout() {
          auth.signOut().then(() => {
            this.$store.dispatch('ATTEMPT_LOGOUT')
              .then(() => {
              this.$router.push({
                  name: "AdministratorLogin"
              })
            }).catch((err) => { console.log(err); })
          })
        },

        validateForm() {
          if (!this.form.email) {
              this.errors.email = "Email is required!"
          }
          if (!this.form.password) {
              this.errors.password = "Password is required!"
          }
          if(this.form.email && this.form.password) {
              return true
          } else {
              return false;
          }
        },

        updateErrorStatusOnInput(fieldName) {
          if(this.form[fieldName].toString().length > 0){
            this.errors[fieldName] = ""
            this.errors.message = ""
          }
        },
      }
    }
</script>

<style lang="scss" scoped>
    @import url("../../assets/css/style.css");
    @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

