<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content" style="position: relative;">
          <div class="water-mark" style="position: absolute; font-family: sans-serif">{{getStatusName}}</div>
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title">{{selectedInstitution.name}} Details Page</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                <router-link 
                  :to="{
                    name: 'EditRegisteredInstitutionDetails',
                    params: {
                      administratorID: administratorID,
                      institutionID: institutionID
                    }                              
                  }" 
                  class="btn btn-success mr-3"
                >
                  <span class="mb-2"><i class="fas fa-edit mr-2"></i></span>Edit
                </router-link>
                <router-link 
                  :to="{
                    name: 'RegisteredInstitutions',
                    params: {
                      administratorID: administratorID
                    }                              
                  }" 
                  class="btn btn-outline-secondary"
                >
                  <span class="mb-2"><i class="fas fa-arrow-circle-left mr-2"></i></span>Back
                </router-link>
              </div>
          </div>
          <div class="row">
            <div class="col-md-6 offset-md-3">
                <ul class="list-group list-group-flush">
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Institution ID</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedInstitution.institutionID}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Name</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedInstitution.name}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Email</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedInstitution.email}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Phone</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedInstitution.phone}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Registration Number</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedInstitution.registrationNumber}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Address</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedInstitution.address}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">City/County</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedInstitution.city}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">State/Province</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedInstitution.stateProvince}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Country</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedInstitution.country}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Account Type</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedInstitution.accountType}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Registered On</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{customizeDate(selectedInstitution.createdOn)}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div v-if="isAccountActive" class="col-3">Account Status</div><div v-if="isAccountActive" class="col-1">:</div> <h3 v-if="isAccountActive" class="col-8"><strong class="badge badge-lg badge-success text-capitalize">{{getStatusName}}</strong></h3>
                            <div v-if="!isAccountActive" class="col-3">Account Status</div><div v-if="!isAccountActive" class="col-1">:</div> <h3 v-if="!isAccountActive" class="col-8"><strong class="badge badge-lg badge-secondary text-capitalize">{{getStatusName}}</strong></h3>
                        </div>
                    </li>                    
                </ul>
            </div>              
          </div>
          <div class="row" v-if="isAccountActive">
            <!-- Review Approved submissions -->
            <h3 class="col-12 h3 mt-5 d-flex justify-content-center text-dark">No Action Required</h3>

            <div class="list-group col-10 offset-1">
                <span class="list-group-item list-group-item-action  transparent-bg d-flex justify-content-center" style="display: flex; align-items: center; justify-content: center;">
                    <h3 class="h4"><a href="#" class="badge badge-success"><i class="fas fa-check-circle fa-3x"></i></a></h3>
                </span>
                <span class="text-light list-group-item list-group-item-action transparent-bg" style="display: flex; align-items: center; justify-content: center;">

                </span>
            </div>

            <h3 class="col-12 h3 mt-5 d-flex justify-content-center text-dark" v-if="isAccountActive">Review Account Status</h3>

            <div class="col-12 mt-3" style="text-align: center;" v-if="!showSuspendAccount">
                <button @click="initiateSuspend" :disabled="isLoading" class="btn btn-lg btn-warning">Review Options</button>
            </div>

            <div class="col-12 mt-3" style="text-align: center;" v-if="showSuspendAccount">
                <form>
                    <div class="form-group col-10 offset-1">
                        <label>Reason for Suspension</label>
                        <textarea v-model="form.reasonForSuspension" class="form-control" rows="3"></textarea>
                    </div>
                    <div class="col-12 mt-3" style="text-align: center;">
                        <button @click="cancelSuspend" class="btn btn-lg btn-secondary mr-5">Cancel</button>
                        <button v-if="!isLoading" @click.prevent="suspendInstitutionAccount" class="btn btn-lg btn-danger">Suspend Institution Account</button>
                        <button v-else class="btn btn-lg btn-danger" type="button" disabled>
                            <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                            Loading...
                        </button>
                    </div>
                </form>
            </div>
          </div>
          <div class="row" v-if="!isAccountActive">
            <div class="jumbotron col-md-6 offset-md-3" style="background: transparent;">
              <h3 class="text-center text-dark">Account has been Suspended</h3>
              <h4>Reason</h4>
              <p>
                Lorem ipsum dolor, sit amet consectetur adipisicing elit. Ab doloremque repudiandae illo dolores hic consequatur harum aut saepe quibusdam obcaecati! Quam corrupti enim doloribus saepe dolor ipsum, commodi quia soluta!
              </p>

              <h3 class="col-12 h3 mt-5 d-flex justify-content-center text-dark" v-if="!isAccountActive">Review Account Status</h3>

              <div class="col-12 mt-3" style="text-align: center;" v-if="!isAccountActive && !showReviewSuspendAccount">
                  <button @click="initiateRevokeSuspension" :disabled="isLoading" class="btn btn-lg btn-warning">Review Options</button>
              </div>

              <div class="col-12 mt-3" v-if="showReviewSuspendAccount" style="text-align: center;">
                  <button @click="cancelSuspend" class="btn btn-lg btn-secondary mr-5">Cancel</button>
                  <button v-if="!isLoading" @click.prevent="revokeAccountSuspension" class="btn btn-lg btn-success">Revoke Account Suspension</button>
                  <button v-else class="btn btn-lg btn-success" type="button" disabled>
                      <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                      Loading...
                  </button>
              </div>
            </div>
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'
  // import { customizeDate } from '../../helpers/customizeDate'

  export default {
    name: "Dashboard",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        isLoading: false,
        active: 'dashboard',
        administratorID: '',
        institutionID: '',
        selectedInstitution: {},
        showSuspendAccount: false,
        showReviewSuspendAccount: false,
        form: {
            reasonForSuspension: ''
        }
      }
    },

    computed: {
      appName() {
        return this.$store.getters.appName
      },

      appNameAbbr() {
        return this.$store.getters.appNameAbbr
      },

      getStatusName() {
        return this.selectedInstitution.status === 1 ? "Active" : "Suspended"
      },

      isAccountActive() {
        return this.selectedInstitution.status === 1 ? true : false
      },

      loggedUserDetails() {
        return this.$store.getters.loggedUser;
      },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      customizeDate(timestamp) {
        // return customizeDate(timestamp)
        return typeof timestamp
      },

      initiateSuspend() {
          this.showSuspendAccount = true
      },
      cancelSuspend() {
          this.showSuspendAccount = false
          this.showReviewSuspendAccount = false
      },

      initiateRevokeSuspension() {
        this.showReviewSuspendAccount = true
      },

      suspendInstitutionAccount() {
        this.isLoading = true
        if (!this.form.reasonForSuspension) {
            alert("Please add Suspension Message!!!")
            this.isLoading = false
        } else {
            if (confirm(`Do you want to suspend ${this.selectedInstitution.name} Account?`)) {
              db.collection("institutions").where('institutionID', '==', this.$route.params.institutionID).get()
              .then( querySnapshot => {
                querySnapshot.forEach( doc => {
                  doc.ref.update({
                    'status': 0
                  }).then(() => {

                  db.collection("administrator_activities").add({
                    'userID': this.loggedUserDetails.userID,
                    'connectorPhrase': 'suspended institution account',
                    'subject': this.selectedInstitution.name,
                    'action': 'suspend institution',
                    'createdAt': new Date().getTime()
                  }).then(res => { 
                    console.log(res)
                    location.reload()
                    this.isLoading = false                      
                  })
                  })
                })
              })
            }
        }          
      },

      revokeAccountSuspension() {
        this.isLoading = true
        if (confirm(`Do you want to revoke ${this.selectedInstitution.name} account suspension?`)) {
          db.collection("institutions").where('institutionID', '==', this.$route.params.institutionID).get()
          .then( querySnapshot => {
            querySnapshot.forEach( doc => {
              doc.ref.update({
                'status': 1
              }).then(() => {
                
                db.collection("administrator_activities").add({
                  'userID': this.loggedUserDetails.userID,
                  'connectorPhrase': 'revoked institution account suspension for',
                  'subject': this.selectedInstitution.name,
                  'action': 'revoke institution suspension',
                  'createdAt': new Date().getTime()
                }).then(res => { 
                  console.log(res)
                  location.reload()
                  this.isLoading = false
                })
              })
            })
          })
            this.isLoading = false
        }
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);

      console.log("selected institution ===>>>> ", this.selectedInstitution)
    },

    created() {
        this.administratorID = this.$route.params.administratorID
        this.institutionID = this.$route.params.institutionID

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.selectedInstitution.id = doc.id
            this.selectedInstitution.institutionID = doc.data().institutionID
            this.selectedInstitution.name = doc.data().name
            this.selectedInstitution.avatar = doc.data().avatar
            this.selectedInstitution.email = doc.data().email
            this.selectedInstitution.phone = doc.data().phone
            this.selectedInstitution.registrationNumber = doc.data().registrationNumber
            this.selectedInstitution.address = doc.data().address
            this.selectedInstitution.country = doc.data().country
            this.selectedInstitution.stateProvince = doc.data().stateProvince
            this.selectedInstitution.city = doc.data().city
            this.selectedInstitution.accountType = doc.data().accountType
            this.selectedInstitution.status = doc.data().status
            this.selectedInstitution.createdOn = doc.data().createdOn
          })
        })
    }
  }
</script>

