export const processChatAccountRetrieval = (db, vueInstance, chatAccountUserIDs) => {
    const allChatAccount = []
    const generalAccount = []
    const prospectAccount = []

    db.collection("chat_accounts").get()
    .then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
            'accountID': doc.data().accountID,
            'userID': doc.data().userID,
            'accountType': doc.data().accountType,
            'userType': doc.data().userType,
            'referenced_db': doc.data().referenced_db,
            'referenceName': doc.data().referenceName
        }
        allChatAccount.push(data)
      })
        for (let i=0; i<chatAccountUserIDs.length; i++) {
            allChatAccount.map(account => {
                if (account.userID === chatAccountUserIDs[i]) {
                    if (account.accountType === 'general') {
                        generalAccount.push(account)
                    } else {
                        prospectAccount.push(account)
                    }
                }
            })
        }

        vueInstance.$store.dispatch('UPDATE_CHAT_CHANNELS', {data: generalAccount, accountType: 'general'})
        vueInstance.$store.dispatch('UPDATE_CHAT_CHANNELS', {data: prospectAccount, accountType: 'prospect'})
    })
}

export const processPrivateChatAccountRetrieval = (db, vueInstance, chatAccountUserIDs) => {
    const allChatAccount = []
    const privateAccount = []
    db.collection("chat_accounts").get()
    .then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
            'accountID': doc.data().accountID,
            'userID': doc.data().userID,
            'accountType': doc.data().accountType,
            'userType': doc.data().userType,
            'referenced_db': doc.data().referenced_db,
            'referenceName': doc.data().referenceName
        }
        allChatAccount.push(data)
      })
        for (let i=0; i<chatAccountUserIDs.length; i++) {
            allChatAccount.map(account => {
                if (account.accountID === chatAccountUserIDs[i]) {
                    if (!(account.accountType === 'general')) {
                        privateAccount.push(account)
                    }
                }
            })
        }
        vueInstance.$store.dispatch('UPDATE_CHAT_CHANNELS', {data: privateAccount, accountType: 'private'})
    })

}