<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title">Create Timetable</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                <router-link 
                  :to="{
                    name: 'InstitutionClassroomDetails',
                    params: {
                      institutionID: institutionID,
                      classroomID: classroomID
                    }                              
                  }" 
                  class="btn btn-outline-danger"
                >
                  <span class="mb-2"><i class="fas fa-times mr-2"></i></span>Cancel
                </router-link>
              </div>
          </div>
          <div class="row">
              <div class="col-lg-8 offset-lg-2">
                <form>
									<div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Time Table ID</label>
                        <input @input="updateErrorStatusOnInput('timeTableID')" type="text" class="form-control text-center" readonly v-model="form.timeTableID">
                        <small v-if="errors.timeTableID" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.timeTableID}}</small>
                      </div>
                    </div>
                  </div>         
                  <div class="row">
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Examination Record</label>
                        <select @change="updateErrorStatusOnInput('selectedExaminationRecord')" class="form-control select" v-model="form.selectedExaminationRecord">
                          <option value="">Select Examination Record</option>
                          <option v-for="examination in examinations" :key="examination.examinationID" :value="examination.examinationID">({{getExaminationSemesterCode(examination.academicSession, examination.startDate)}}) {{examination.title}}</option>
                        </select>
                        <small v-if="errors.selectedExaminationRecord" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.selectedExaminationRecord}}</small>
                      </div>                      
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Max. No. of Examination Per Day<span class="text-danger"></span></label>
                        <select @change="updateErrorStatusOnInput('maxNumExaminationPerDay')" class="form-control select" v-model="form.maxNumExaminationPerDay">
                          <option value="">Select No. of Examination per Day</option>
                          <option v-for="(n, index) in 5" :key="index" :value="n+2">{{n+2}}</option>
                        </select>
                        <small v-if="errors.maxNumExaminationPerDay" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.maxNumExaminationPerDay}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Max. Examination Duration (in minutes)<span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('maxExaminationDuration')" min="0" class="form-control" type="number" v-model="form.maxExaminationDuration">                        
                        <small v-if="errors.maxExaminationDuration" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.maxExaminationDuration}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Daily Examination Start Hour (Using 24 hrs time clock) <span class="text-danger"></span></label>
                        <select @change="updateErrorStatusOnInput('startHour')" class="form-control select" v-model="form.startHour">
                          <option value="">Select Daily Examination Start Hour</option>
                          <option v-for="(n, index) in 16" :key="index" :value="n+7">{{n+7}}</option>
                        </select>
                        <small v-if="errors.startHour" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.startHour}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>First Break Duration (in minutes) <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('firstBreak')" min="0" max="120" class="form-control" type="number" v-model="form.firstBreak">
                        <small v-if="errors.firstBreak" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.firstBreak}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Second Break Duration (in minutes) <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('secondBreak')" min="0" max="120" class="form-control" type="number" v-model="form.secondBreak">
                        <small v-if="errors.secondBreak" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.secondBreak}}</small>
                      </div>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label> Time Table Description</label>
                        <textarea @input="updateErrorStatusOnInput('timeTableDescription')" class="form-control" v-model="form.timeTableDescription"></textarea>
                        <small v-if="errors.timeTableDescription" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.timeTableDescription}}</small>
                      </div>
                    </div>
                  </div>
                  <hr v-if="mustCreateExaminationRecord" class="mb-3">
                  <h3 v-if="mustCreateExaminationRecord" class="h3">Examintion Record</h3>
                  <hr v-if="mustCreateExaminationRecord" class="mb-3">
                  <div v-if="mustCreateExaminationRecord" class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Examination ID</label>
                        <input @input="updateErrorStatusOnInput('examinationID')" type="text" class="form-control text-center" readonly v-model="form.examinationID">
                        <small v-if="errors.examinationID" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.examinationID}}</small>
                      </div>
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Academic Session <span class="text-danger"></span></label>
                        <select @change="updateErrorStatusOnInput('academicSession')" class="form-control select" v-model="form.academicSession">
                          <option value="">Select Academic Session</option>
                          <option value="1st Semester">1st Semester</option>
                          <option value="2nd Semester">2nd Semester</option>
                          <option value="1st Term">1st Term</option>
                          <option value="2nd Term">2nd Term</option>
                          <option value="3rd Term">3rd Term</option>
                        </select>
                        <small v-if="errors.academicSession" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.academicSession}}</small>
                      </div>
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Title <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('title')" class="form-control" type="text" v-model="form.title">
                        <small v-if="errors.title" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.title}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Examination Start Date <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('startDate')" class="form-control" type="date" v-model="form.startDate">
                        <small v-if="errors.startDate" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.startDate}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Examination End Date<span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('endDate')" class="form-control" type="date" v-model="form.endDate">
                        <small v-if="errors.endDate" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.endDate}}</small>
                      </div>
                    </div>
                  </div>
                  <div v-if="mustCreateExaminationRecord" class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Examination Description</label>
                        <textarea @input="updateErrorStatusOnInput('examinationDescription')" class="form-control" v-model="form.examinationDescription"></textarea>
                        <small v-if="errors.examinationDescription" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.examinationDescription}}</small>
                      </div>
                    </div>
                  </div>
                  <hr class="mb3">
                  <div class="form-group text-center">
                    <button v-if="!isLoading" @click.prevent="attemptCreatingExaminationTimeTable" class="btn btn-lg btn-success btn-block">Create Time Table</button>
                    <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                      <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                      Loading...      
                    </button>
                  </div>
                </form>
              </div>            
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'
  import { generateTimeTable } from '../../helpers/generateTimeTable'

  import uuid from 'uuid'

  export default {
    name: "CreateTimeTable",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        isLoading: false,
        active: 'registered institutions',
        institutionID: null,
        classroomID: null,
        institution: {},
        examinations: [],
        form: {
          // Timetable Information
          timeTableID: "",
          selectedExaminationRecord: "",
          details: {},
          startHour: "",
          maxNumExaminationPerDay: "",
          maxExaminationDuration: "",
          firstBreak: 20,
          secondBreak: 20,
          timeTableDescription: "",

          // Examination Details
          examinationID: "",
          title: "",
          academicSession: "",
          startDate: "",
          endDate: "",
          examinationDescription: "",
        },
        errors: {
          // Timetable Information
          timeTableID: "",
          selectedExaminationRecord: "",
          startHour: "",
          maxNumExaminationPerDay: "",
          maxExaminationDuration: "",
          firstBreak: "",
          secondBreak: "",
          timeTableDescription: "",

          // Examination Details
          examinationID: "",
          title: "",
          academicSession: "",
          startDate: "",
          endDate: "",
          examinationDescription: "",
        }
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },

      mustCreateExaminationRecord() {
        return this.form.selectedExaminationRecord == "" ? true : false
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      getExaminationSemesterCode(academicSession, startDate) {
          let dateString = this.getDateString(academicSession)
          let semString = this.getSemesterString(startDate)

          return `${semString} ${dateString}`
      },

      getDateString(academicSession) {
        let sessString = ""
        const academicSessionArray = academicSession.split(" ")

        if (academicSessionArray[1] === "Semester") {
          sessString = "Sem."
        } else {
          sessString = academicSessionArray[1]
        }

        return `${academicSessionArray[0]} ${sessString}`
      },

      getSemesterString(startDate) {
        const formatedDate = new Intl.DateTimeFormat('en', { year: 'numeric', month: 'short', day: '2-digit' }).format(new Date(startDate)) 
        const dateStringArray = formatedDate.toString().split(" ")
        return `${dateStringArray[0]}. ${dateStringArray[2]}`
      },

      validateForm() {
        if (!this.form.timeTableID) {
            this.errors.timeTableID = "Time Table ID is required!"
        }
        if (!this.form.maxNumExaminationPerDay) {
            this.errors.maxNumExaminationPerDay = "Maximum number of examination per day is required!"
        }
        if (!this.form.maxExaminationDuration) {
            this.errors.maxExaminationDuration = "Maximum examination duration is required!"
        }
        if (!this.form.startHour) {
            this.errors.startHour = "Daily start hour for examination is required!"
        }
        if (!this.form.firstBreak) {
            this.errors.firstBreak = "First break duration is required!"
        } else if (parseInt(this.form.firstBreak, 10) < 20) {
          this.errors.firstBreak = "First break CANNOT be less than 20 (minutes)!"
        }

        if (!this.form.secondBreak) {
            this.errors.secondBreak = "Second break duration is required!"
        } else if (parseInt(this.form.secondBreak, 10) < 20) {
          this.errors.secondBreak = "Second break CANNOT be less than 20 (minutes)!"
        }

        if (this.mustCreateExaminationRecord) {
          if (!this.form.examinationID) {
              this.errors.examinationID = "Examination ID is required!"
          }
          
          if (!this.form.academicSession) {
              this.errors.academicSession = "Academic session is required!"
          }           

          if (!this.form.title) {
              this.errors.title = "Title is required!"
          } 
          
          if (!this.form.startDate) {
              this.errors.startDate = "Examination start date is required!"
          }     
          
          if (!this.form.endDate) {
              this.errors.endDate = "Examination end date is required!"
          }  
          
        }

        if (this.mustCreateExaminationRecord) {
          if(this.form.timeTableID && this.form.maxNumExaminationPerDay && this.form.maxExaminationDuration && this.form.startHour && (this.form.firstBreak && parseInt(this.form.firstBreak) >= 20) && (this.form.secondBreak && parseInt(this.form.secondBreak) >= 20) && this.form.examinationID && this.form.academicSession && this.form.title && this.form.startDate && this.form.endDate)  {
            return true
          } else {
            return false
          }
        } else {
          if(this.form.timeTableID && this.form.maxNumExaminationPerDay && this.form.maxExaminationDuration && this.form.startHour && (this.form.firstBreak && parseInt(this.form.firstBreak) >= 20) && (this.form.secondBreak && parseInt(this.form.secondBreak) >= 20) )  {
            return true
          } else {
            return false
          }
        }
      },

      attemptCreatingExaminationTimeTable() {
        this.isLoading = true
        if (!this.validateForm()) {
          this.isLoading = false; 
          return;
        }

        this.form.details = JSON.stringify(generateTimeTable(parseInt(this.form.startHour), parseInt(this.form.maxExaminationDuration), parseInt(this.form.maxNumExaminationPerDay), new Date(this.form.startDate), new Date(this.form.endDate), parseInt(this.form.firstBreak), parseInt(this.form.secondBreak)))
        db.collection("examinations").add({
          "examinationID": this.form.examinationID,
          "institutionID": this.institutionID,
          "title": this.form.title,
          "academicSession": this.form.academicSession,
          "examinationStatus": "Pending",
          "startDate": new Date(this.form.startDate).getTime(),
          "endDate": new Date(this.form.endDate).getTime(),
          "examinationDescription": this.form.examinationDescription,
          "createdOn": new Date().getTime()
        }).then((res) => {
          console.log(res)
            const data = {
              "timeTableID": this.form.timeTableID,
              "classroomID": this.classroomID,
              "examinationID": this.form.examinationID,
              "details": this.form.details,
              "startHour": parseInt(this.form.startHour),
              "maxNumExaminationPerDay": parseInt(this.form.maxNumExaminationPerDay),
              "maxExaminationDuration": parseInt(this.form.maxExaminationDuration),
              "firstBreak": parseInt(this.form.firstBreak),
              "secondBreak": parseInt(this.form.secondBreak),
              "timeTableDescription": this.form.timeTableDescription,
              'createdOn': new Date().getTime()
            }

            db.collection("examination_timetables").add(data).then((res) => {
              console.log(res)
              this.isLoading = false
              this.$router.push({
                name: "InstitutionClassroomDetails",
                params: {
                  institutionID: this.institutionID,
                  classroomID: this.classroomID
                }
              })
            }).catch(errorTimeTable => {
              this.isLoading = false
              alert(errorTimeTable)
            });
        }).catch(errorExamination => {
          this.isLoading = false
          alert(errorExamination)
        });
      },

      updateErrorStatusOnInput(fieldName) {
        if(this.form[fieldName].toString().length > 0){
          this.errors[fieldName] = ""
          this.errors.message = ""
        }

        if (fieldName === "selectedExaminationRecord") {
          if(this.form[fieldName].toString().length > 0){
            const record = this.getExaminationRecord(this.form[fieldName])
            this.form.examinationID = record.examinationID
            this.form.title = record.title
            this.form.academicSession = record.academicSession
            this.form.startDate = new Date(record.startDate)
            this.form.endDate = new Date(record.endDate)
            this.form.examinationDescription = record.examinationDescription
          } else {
            this.form.examinationID = uuid.v1()
            this.form.title = ""
            this.form.academicSession = ""
            this.form.startDate = ""
            this.form.endDate = ""
            this.form.examinationDescription = ""
          }
        }
      },

      getExaminationRecord(targetExaminationID) {
        let record = null

        this.examinations.map(examination => {
          if (examination.examinationID === targetExaminationID) {
            record = examination
          }
        })

        return record
      },

      isThisYearExamination(examinationStartDate) {
        const examinationYear = new Date(examinationStartDate).getFullYear()
        if (examinationYear >= (new Date()).getFullYear()) {
          return true
        } 
        return false
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    created() {
      this.institutionID = this.$route.params.institutionID
      this.classroomID = this.$route.params.classroomID

      this.form.timeTableID = uuid.v4()
      this.form.examinationID = uuid.v1()

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

        db.collection("examinations").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            if (this.isThisYearExamination(doc.data().startDate)) {
              const record = {
                "id": doc.id,
                "examinationID": doc.data().examinationID,
                "title": doc.data().title,
                "academicSession": doc.data().academicSession,
                "examinationStatus": doc.data().examinationStatus,
                "startDate": doc.data().startDate,
                "endDate": doc.data().endDate,
                "examinationDescription": doc.data().examinationDescription,
                "createdOn": doc.data().createdOn
              }

              this.examinations.push(record)
            }
          })
        })
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
