<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title">All Courses</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                  <a v-if="searchResultUpdate && (courses.length > 0)" @click.prevent="reloadDataRecords" class="btn btn-outline-danger text-danger mr-3"><span class="mb-2"><i class="fas fa-times mr-2"></i></span>Clear Search</a>
                  <router-link
                    class="btn btn-success btn-rounded float-right"
                    :to="{
                      name: 'CreateCourse',
                      params: {
                        institutionID: institutionID
                      }                                                
                    }"
                  ><i class="fas fa-user-edit mr-2"></i>Create Course</router-link>
              </div>
          </div>
          <div v-if="courses.length <= 0" class="row">
              <div class="col-sm-8 offset-sm-2 mt-5">
                <div class="jumbotron text-center">
                    <h3 class="h3">{{emptyMessage}}</h3> <br> <a v-if="searchResultUpdate" @click.prevent="reloadDataRecords" class="btn btn-success text-white"><span class="mb-2"><i class="fas fa-arrow-circle-left mr-2"></i></span>Back</a>
                </div>
              </div>
          </div>
          <div v-else class="container-fluid">
            <div class="row filter-row">
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Course ID</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.courseID">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Title</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.title">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Course Code</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.code">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                  <a @click.prevent="searchCourses" class="btn btn-success btn-block text-white"> Search </a>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12">
                <div class="table-responsive">
                  <table class="table table-striped custom-table">
                    <thead>
                      <tr>
                        <th style="min-width:200px;">Course Title</th>
                        <th>Course ID</th>
                        <th>Course Code</th>
                        <th>Grade</th>
                        <th>Duration</th>
                        <th>Instructor</th>
                        <th>Created On</th>
                        <th class="text-right">Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="course in courses"
                        :key="course.courseID"
                      >
                        <td>
                          <img width="28" height="28" class="rounded-circle"> <h2>{{course.title}}</h2>
                        </td>   
                        <td>{{course.courseID}}</td>
                        <td>{{course.code}}</td>
                        <td>{{course.grade}}</td>
                        <td>{{course.duration}} {{course.durationUnit}}</td>
                        <td>{{getEmployeeNameByID(course.instructor)}}</td>
                        <td>{{customizeDate(parseInt(course.createdOn))}}</td>
                        <td class="text-right">
                          <div class="dropdown dropdown-action">
                            <a @click.prevent="toggleActionMenuOptions($event)" class="action-icon dropdown-toggle" style="cursor: pointer;" aria-expanded="false"><i class="fa fa-ellipsis-v"></i></a>
                            <div v-if="canViewOption" class="dropdown-menu dropdown-menu-right" style="position: absolute; will-change: transform; top: -50px; left: -55px; transform: translate3d(-63px, 27px, 0px);">
                              <a @click.prevent="loadSelectedCoursePage(course.courseID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-cogs m-r-5"></i> Manage</a>
                              <a v-if="canViewOption" @click.prevent="requestDeleteCourseOperation(course.courseID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-trash m-r-5"></i> Delete</a>
                            </div>
                            <div v-if="!canViewOption" class="dropdown-menu dropdown-menu-right" style="position: absolute; will-change: transform; top: -38px; left: -55px; transform: translate3d(-63px, 27px, 0px);">
                              <a @click.prevent="loadSelectedCoursePage(course.courseID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-cogs m-r-5"></i> Manage</a>
                              <a v-if="canViewOption" @click.prevent="requestDeleteCourseOperation(course.courseID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-trash m-r-5"></i> Delete</a>
                            </div>                            
                          </div>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'

  import { customizeDate } from '../../helpers/customizeDate'

  import { searchWithThreeParameters } from '../../helpers/searchWithThreeParameters'
  import { searchWithTwoParameters } from '../../helpers/searchWithTwoParameters'
  import { searchWithOneParameter } from '../../helpers/searchWithOneParameter'

  export default {
    name: "Courses",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'all courses',
        institutionID: null,
        institution: {},
        employees: [],
        courses: [],
        emptyMessage: "Course list is empty.",
        searchResultUpdate: false,
        search: {
          courseID: "",
          title: "",
          code: ""
        },
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },

      canViewOption() {
        return this.loggedUser.userRole === "institution administrator"
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      async toggleActionMenuOptions(event) {
        let targetElement = this.getTargetElement(event);

        if (targetElement.parentNode.classList.contains('show')) {
          targetElement.parentNode.classList.remove('show');
          targetElement.nextElementSibling.classList.remove('show');
        } else {
          await this.closeAllOpenedActionMenuOptions()
          targetElement.parentNode.classList.add('show');
          targetElement.nextElementSibling.classList.add('show');
        }
      },

      getTargetElement(events) {
        if (events.target.tagName === "A") {
          return events.target;
        } else if (events.target.parentNode.tagName === "A") {
          return events.target.parentNode;
        } else if (events.target.parentNode.parentNode.tagName === "A") {
          return events.target.parentNode.parentNode;
        } else {
          return null;
        }
      },

      async closeAllOpenedActionMenuOptions() {
        const allActionMenuOption = document.querySelectorAll(".dropdown-action")

        for (let i=0; i<allActionMenuOption.length; i++) {
          if (allActionMenuOption[i].classList.contains('show')) {
            allActionMenuOption[i].classList.remove('show');
          }
        }
      },

      customizeDate(timestamp) {
        return customizeDate(timestamp)
      },

      loadSelectedCoursePage(courseID) {
        this.$router.push({
          name: 'InstitutionCourseDetails',
          params: {
            institutionID: this.institutionID,
            courseID: courseID
          }
        })
      },

      async requestDeleteCourseOperation(courseID) {
        if (confirm(`Do you want to Delete ${this.getCourseNameByID(courseID)} course records?`)) {
          await this.closeAllOpenedActionMenuOptions()
          db.collection("courseID").where('courseID', '==', courseID).get()
            .then( querySnapshot => {
              querySnapshot.forEach( doc => {
                  doc.ref.delete()
                  this.courses.map(course => {
                    if (course.courseID === courseID) {
                      this.courses.splice(this.courses.indexOf(courseID), 1)
                    }
                  })
              })
            })
        }
      },

      getEmployeeNameByID(userID) {
        let name = "Unknown"

        this.employees.map(employee => {
          if (employee.userID === userID) {
            name = employee.fullname
          }
        })

        return name
      },

      getCourseNameByID(courseID) {
         let name = "Unknown"
         this.courses.map(course => {
           if (course.courseID === courseID) {
             name = course.title
           }
         })
         return name
      },

      async getCoursesFromFirebase() {
        const temp = []
        await db.collection("courses").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              const data = {
                'id': doc.id,
                'courseID': doc.data().courseID,
                'institutionID': doc.data().institutionID,
                'title': doc.data().title,
                'grade': doc.data().grade,
                'code': doc.data().code,
                'duration': doc.data().duration,
                'durationUnit': doc.data().durationUnit,
                'instructor': doc.data().instructor,
                'description': doc.data().description,
                'createdOn': doc.data().createdOn
              }                                                                                                                                             
              temp.push(data)
            })
          })

        return temp
      },

      async searchCoursesWithThreeParameters(label1, param1, label2, param2, label3, param3) {
        const allCourses = await this.getCoursesFromFirebase()
        const temp = searchWithThreeParameters(allCourses, label1, param1, label2, param2, label3, param3)
        
        this.courses = temp

        if (this.courses.length  >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },

      async searchCoursesWithTwoParameters(label1, param1, label2, param2) {
        const allCourses = await this.getCoursesFromFirebase()
        const temp = searchWithTwoParameters(allCourses, label1, param1, label2, param2)
        
        this.courses = temp

        if (this.courses.length >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },
      
      async searchCoursesWithOneParameters(label1, param1) {
        const allCourses = await this.getCoursesFromFirebase()
        const temp = searchWithOneParameter(allCourses, label1, param1)
        
        this.courses = temp

        if (this.courses.length  >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },

      async reloadDataRecords() {
        this.courses = await this.getCoursesFromFirebase()
        this.emptyMessage = "Course list is empty."
        this.searchResultUpdate = false
        this.search.courseID = ""
        this.search.title = ""
        this.search.code = ""
      },

      searchCourses() {
        if (this.search.courseID && this.search.title && this.search.code) {
          this.searchCoursesWithThreeParameters('courseID', this.search.courseID, 'title', this.search.title, 'code', this.search.code)
        } else if (
          (this.search.courseID && this.search.title) ||
          (this.search.courseID && this.search.code) ||
          (this.search.code && this.search.title)
        ) {
          if (this.search.courseID && this.search.title) {
            this.searchCoursesWithTwoParameters('courseID', this.search.courseID, 'title', this.search.title)
          } else if (this.search.courseID && this.search.code) {
            this.searchCoursesWithTwoParameters('courseID', this.search.courseID, 'code', this.search.code)
          } else if (this.search.code && this.search.title) {
            this.searchCoursesWithTwoParameters('code', this.search.code, 'title', this.search.title)
          }
        } else if (this.search.courseID || this.search.title || this.search.code) {
          if (this.search.courseID) {
            this.searchCoursesWithOneParameters('courseID', this.search.courseID)
          } else if (this.search.title) {
            this.searchCoursesWithOneParameters('title', this.search.title)
          } else if (this.search.code) {
            this.searchCoursesWithOneParameters('code', this.search.code)
          }
        }
      },

      async updateSearchOperation() {
        if (!this.search.courseID && !this.search.title && !this.search.code) {
          this.courses = await this.getCoursesFromFirebase()
          this.emptyMessage = "Course list is empty."
        }
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    async created() {
      this.institutionID = this.$route.params.institutionID

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

        db.collection("institution_staffs").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'userID': doc.data().userID,
              'institutionID': doc.data().institutionID,
              'fullname': doc.data().fullname,
              'email': doc.data().email,
              'userRole': doc.data().userRole,
              'accountType': doc.data().accountType,
              'address': doc.data().address,
              'phone': doc.data().phone,
              'gender': doc.data().gender,
              'dob': doc.data().dob,
              'avatar': doc.data().avatar
            }                                                                                                               
              this.employees.push(data)
            })
          })

      this.courses = await this.getCoursesFromFirebase()
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
