<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-8 col-3">
                  <h4 class="page-title"><strong>{{examination.title}} ({{examination.academicSession}})</strong></h4>
              </div>
              <div class="col-sm-4 col-9 text-right m-b-20">
                  <router-link
                    class="btn btn-outline-secondary float-right"
                    :to="{
                      name: 'InstitutionExaminationClassrooms',
                      params: {
                        institutionID: institutionID,
                        examinationID: examinationID,
                      }                                                    
                    }"
                  ><i class="fas fa-arrow-circle-left mr-2"></i>Back</router-link>
              </div>
          </div>
          <div v-if="classroomCourses.length <= 0" class="row">
              <div class="col-sm-8 offset-sm-2 mt-5">
                <div class="jumbotron text-center">
                    <h3 class="h3">Course list for the selected Classroom is Empty!</h3> 
                </div>
              </div>
          </div>
          <div v-if="classroomCourses.length > 0" class="row">
            <ul style="list-style: none; width: 100%; margin: 0px; padding: 0px;">
              <li class="mb-5" v-for="(classroomCourse, index)  in classroomCourses" :key="index">
                <div 
                  class="card"
                >
                  <div class="card-body">
                    <div class="row mb-3">
                      <div class="col-md-10 offset-md-1">
                          <h3 class="h4 text-center mt-3 mb-4">{{getCourseRecordByID(classroomCourse.courseID).title}} ({{getCourseRecordByID(classroomCourse.courseID).code}}) Examination Summary</h3>
                          <ul class="list-group list-group-flush mb-5 col-8 offset-2">
                              <li class="list-group-item transparent-bg">
                                  <div class="row">
                                      <div class="col-3">Number of Participants</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ generateExaminationSummaryforSelectedCourse(classroomCourse.courseID).participant }}</strong></div>
                                  </div>
                              </li>
                              <li class="list-group-item transparent-bg">
                                  <div class="row">
                                      <div class="col-3">Minimum Score</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ generateExaminationSummaryforSelectedCourse(classroomCourse.courseID).minimum }}</strong></div>
                                  </div>
                              </li>
                              <li class="list-group-item transparent-bg">
                                  <div class="row">
                                      <div class="col-3">Maximum Score</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ generateExaminationSummaryforSelectedCourse(classroomCourse.courseID).maximum }}</strong></div>
                                  </div>
                              </li>
                              <li class="list-group-item transparent-bg">
                                  <div class="row">
                                      <div class="col-3">Class Average Score</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{ generateExaminationSummaryforSelectedCourse(classroomCourse.courseID).average }}</strong></div>
                                  </div>
                              </li>
                          </ul>
                          <h3  class="h4 text-center mt-5 mb-3">Participant Summary Table</h3>
                          <div v-if="getStudentExaminationRecordsForSelectedCourse(classroomCourse.courseID).length <= 0" class="row">
                              <div class="col-sm-8 offset-sm-2 mt-3">
                                <div class="jumbotron text-center">
                                    <h3 class="h3">Student record list is empty!</h3> 
                                </div>
                              </div>
                          </div>
                            <table v-if="getStudentExaminationRecordsForSelectedCourse(classroomCourse.courseID).length > 0" class="table table-light">
                              <thead>
                                <tr>
                                  <th scope="col">Student Name</th>
                                  <th scope="col">Correct Answers</th>
                                  <th scope="col">Wrong Answers</th>
                                  <th scope="col">Score</th>
                                  <th scope="col">Percentage Score</th>
                                  <th scope="col">Grade</th>
                                </tr>
                              </thead>
                              <tbody>
                                <tr v-for="(studentRecord, index) in getStudentExaminationRecordsForSelectedCourse(classroomCourse.courseID)" :key="index">
                                  <td>{{getStudentRecordByID(studentRecord.studentID).fullname}}</td>
                                  <td>{{studentRecord.correctAnswer}}</td>
                                  <td>{{studentRecord.wrongAnswer}}</td>
                                  <td>{{`${studentRecord.correctAnswer} / ${studentRecord.correctAnswer + studentRecord.wrongAnswer}`}}</td>
                                  <td>{{studentRecord.percentageScore}} %</td>
                                  <td>{{studentRecord.grade}}</td>
                                </tr>
                              </tbody>
                            </table>
                      </div>              
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'
  import { isEmptyObject } from '../../helpers/isEmptyObject'
  import { customizeDate, customizeTime } from '../../helpers/customizeDate'

  export default {
    name: "Employees",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'registered institutions',
        institutionID: null,
        examinationID: null,
        classroomID: null,
        institution: {},
        examination: {},
        courses: [],
        studentExaminationGrade: [],
        classroomCourses: [],
        students: []
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },

      getObjectKeys() {
        if (isEmptyObject(this.examinationSchedule)) {
          return []
        } else {
          return Object.keys(this.examinationSchedule)
        }
      },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      }, 

      processDateFromPaperID(paperID) {
          return customizeDate(parseInt(paperID.split("-")[0]))
      },

      processTime(time, paperID) {
            let dateTime = new Date(`${this.processDateFromPaperID(paperID)} ${time}`)
            return customizeTime(dateTime.getTime())
      },

      getCourseRecordByID(courseID) {
        let record = null
        this.courses.map(course => {
          if (course.courseID === courseID) {
            record = course
          }
        })
        return record
      },

      getStudentRecordByID(studentID) {
        let record = null
        this.students.map(student => {
          if (student.studentID === studentID) {
            record = student
          }
        })
        return record 
      },

      customizeDate(timestamp) {
        return customizeDate(timestamp)
      },

      customizeTime(timestamp) {
        return customizeTime(timestamp)
      },

      isEmptyObject(obj) {
        return isEmptyObject(obj)
      },

      generateExaminationSummaryforSelectedCourse(courseID) {
        let min = 0, max = 0, sum = 0, counter = 0

        this.studentExaminationGrade.map((record, index) => {
          if (record.courseID === courseID) {
            sum += record.percentageScore
            counter += 1
            
            if (index === 0) {
              min = 100
            }

            if (min > record.percentageScore) {
              min = record.percentageScore
            }

            if (max < record.percentageScore) {
              max = record.percentageScore
            }
          }
        })

        return {
          minimum: min.toFixed(1),
          maximum: max.toFixed(1),
          participant: counter,
          average: counter > 0 ? (sum/counter).toFixed(1) : "0.0"
        }
      },

      getStudentExaminationRecordsForSelectedCourse(courseID) {
        const examinationRecord = []
        this.studentExaminationGrade.map(record => {
          if (record.courseID === courseID) {
            examinationRecord.push(record)
          }
        })
        return examinationRecord
      },

      initialization() {
        db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

         db.collection("students").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
                'id': doc.id,
                'studentID': doc.data().studentID,
                'institutionID': doc.data().institutionID,
                'fullname': doc.data().fullname,
                'dob': doc.data().dob,
                'email': doc.data().email,
                'phone': doc.data().phone,
                'address': doc.data().address,
                'userRole': doc.data().userRole,
                'accountType': doc.data().accountType,
                'createdOn': doc.data().createdOn,
                'avatar': doc.data().avatar
            }                                                                                                                      
              this.students.push(data)
            })
          })

        db.collection("examinations").where("examinationID", "==", this.examinationID).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                this.examination.id = doc.id
                this.examination.examinationID = doc.data().examinationID
                this.examination.institutionID = doc.data().institutionID
                this.examination.title = doc.data().title
                this.examination.academicSession = doc.data().academicSession
                this.examination.examinationStatus = doc.data().examinationStatus
                this.examination.startDate = doc.data().startDate
                this.examination.endDate = doc.data().endDate
                this.examination.examinationDescription = doc.data().examinationDescription
                this.examination.createdOn = doc.data().createdOn
            })
          })

        db.collection("courses").where("institutionID", "==", this.$route.params.institutionID).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                  'id': doc.id,
                  'courseID': doc.data().courseID,
                  'institutionID': doc.data().institutionID,
                  'title': doc.data().title,
                  'grade': doc.data().grade,
                  'code': doc.data().code,
                  'duration': doc.data().duration,
                  'durationUnit': doc.data().durationUnit,
                  'instructor': doc.data().instructor,
                  'description': doc.data().description,
                  'createdOn': doc.data().createdOn
                }                                                                                                                                       
                this.courses.push(data)
              })
            })
        
            db.collection("student_examination_grades").where("examinationID", "==", this.examinationID).where("classroomID", "==", this.classroomID).get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                if (doc.data().institutionID === this.institutionID && doc.data().examinationID === this.examinationID) {
                    const data = {
                        'studentExaminationGradeID': doc.data().studentExaminationGradeID,
                        'institutionID': doc.data().institutionID,
                        'studentID': doc.data().studentID,
                        'classroomID': doc.data().classroomID,
                        'examinationID': doc.data().examinationID,
                        'timetableID': doc.data().timetableID,
                        'courseID': doc.data().courseID,
                        'questionID': doc.data().questionID,
                        'examinationDetails': JSON.parse(doc.data().examinationDetails),
                        "paperDetails": doc.data().paperDetails,
                        'correctAnswer': doc.data().correctAnswer,
                        'wrongAnswer': doc.data().wrongAnswer,
                        'score': doc.data().score,
                        'percentageScore': doc.data().percentageScore,
                        'grade': doc.data().grade,
                        'createdOn': doc.data().createdOn
                    }
                    this.studentExaminationGrade.push(data)
                }
              })
            })
            
            db.collection("classroom_courses").where('classroomID', '==', this.classroomID).get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                  const data = {
                    'id': doc.id,
                    'classroomID': doc.data().classroomID,
                    'courseID': doc.data().courseID,
                  }                                                                                   
                  this.classroomCourses.push(data)
                })
              })
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    async created() {
      this.institutionID = this.$route.params.institutionID
      this.classroomID = this.$route.params.classroomID
      this.examinationID = this.$route.params.examinationID

        await this.initialization()
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }

  .can-write { background: #d4edda; }
  .can-not-write { background: #f8d7da; }
  .has-written { background: #d6d8db; }
</style>
