<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title">Register New Institution</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                  <!-- <router-link to="" class="btn btn-success btn-rounded float-right"><i class="fa fa-plus"></i> Register Institution</router-link> -->
              </div>
          </div>
            <div class="row">
              <div class="col-lg-8 offset-lg-2">
                <form>
									<div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Institution ID</label>
                        <input @input="updateErrorStatusOnInput('institutionID')" type="text" class="form-control text-center" readonly v-model="form.institutionID">
                        <small v-if="errors.institutionID" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.institutionID}}</small>
                      </div>
                    </div>
                  </div>         
                  <div class="row">
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Name <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('name')" class="form-control" type="text" v-model="form.name">
                        <small v-if="errors.name" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.name}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Email <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('email')" class="form-control" type="email" v-model="form.email">
                        <small v-if="errors.email" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.email}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Phone</label>
                        <input @input="updateErrorStatusOnInput('phone')" class="form-control" type="text" v-model="form.phone">
                        <small v-if="errors.phone" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.phone}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Reg. Number <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('registrationNumber')" class="form-control" type="text" v-model="form.registrationNumber">
                        <small v-if="errors.registrationNumber" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.registrationNumber}}</small>
                      </div>
                    </div>
                  </div>
									<div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Address</label>
                        <textarea @input="updateErrorStatusOnInput('address')" class="form-control" v-model="form.address"></textarea>
                        <small v-if="errors.address" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.address}}</small>
                      </div>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-sm-4 col-md-4 col-lg-4">
                      <div class="form-group">
                        <label>Country</label>
                        <select @change="updateErrorStatusOnInput('country')" class="form-control select" v-model="form.country">
                          <option value="">Select Country</option>
                          <option value="Ireland">Ireland</option>
                        </select>
                        <small v-if="errors.country" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.country}}</small>
                      </div>
                    </div>
                    <div class="col-sm-4 col-md-4 col-lg-4">
                      <div class="form-group">
                        <label>City</label>
                        <input @input="updateErrorStatusOnInput('city')" type="text" class="form-control" v-model="form.city">
                        <small v-if="errors.city" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.city}}</small>
                      </div>
                    </div>
                    <div class="col-sm-4 col-md-4 col-lg-4">
                      <div class="form-group">
                        <label>State/Province</label>
                        <select @change="updateErrorStatusOnInput('stateProvince')" class="form-control select" v-model="form.stateProvince">
                          <option value="">Select State/Province</option>
                          <option value="Dublin">Dublin</option>
                          <option value="Corks">Cork</option>
                          <option value="Corks">Limerick</option>
                        </select>
                        <small v-if="errors.stateProvince" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.stateProvince}}</small>
                      </div>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Account Type</label>
                        <input  @input="updateErrorStatusOnInput('accountType')" class="form-control" type="text" v-model="form.accountType">
                        <small v-if="errors.accountType" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.accountType}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Avatar</label>
                        <div class="profile-upload">
                          <div class="upload-img">
                            <img alt="" :src="form.avatar">
                          </div>
                          <div class="upload-input">
                            <input type="file" class="form-control" @change="onFileSelected($event)" accept="image/*" required>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label class="display-block">Status</label>
                        <div class="form-check form-check-inline">
                          <input class="form-check-input" type="radio" v-model="form.status" value="1" checked>
                          <label class="form-check-label">
                            Active
                          </label>
                        </div>
                        <div class="form-check form-check-inline">
                          <input class="form-check-input" type="radio" v-model="form.status" value="0">
                          <label class="form-check-label">
                            Inactive
                          </label>
                        </div>
                        <small v-if="errors.status" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.status}}</small>
                      </div>
                    </div>
                  </div>
                  <hr class="mb-3">
                  <h3 class="h3">Institution Administrator Account</h3>
                  <div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Administrator ID</label>
                        <input type="text" class="form-control text-center" readonly v-model="form.institutionAdministrator.userID">
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Password</label>
                        <input class="form-control" type="text" readonly v-model="form.institutionAdministrator.password">
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Account Type<span class="text-danger"></span></label>
                        <input class="form-control" type="text" readonly v-model="form.institutionAdministrator.accountType">
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>User Role <span class="text-danger"></span></label>
                        <input class="form-control" type="text" readonly v-model="form.institutionAdministrator.userRole">
                      </div>
                    </div>                    
                  </div>
                  <hr class="mb-3">
                  <div class="form-group text-center">
                    <button v-if="!isLoading" @click.prevent="attemptRegisteringInstitution" class="btn btn-lg btn-success btn-block">Create Institution Account</button>
                    <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                      <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                      Loading...      
                    </button>
                  </div>
                </form>
              </div>
            </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { uuid } from 'vue-uuid';
  import { auth, db } from '../../firebase/firebaseInit'

  import firebase from 'firebase'

  export default {
    name: "RegisterInstitution",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'register institution',
        isLoading: false,
        selectedFile: null,
        form: {
          institutionID: '',
          name: '',
          email: '',
          phone: '',
          registrationNumber: '',
          address: '',
          country: '',
          stateProvince: '',
          city: '',
          accountType: 'institution account',
          avatar: '',
          status: 1,
          institutionAdministrator: {
            userID: '',
            fullname: "Institution Administrator",
            password: "P@$$w0rd",
            userRole: 'institution administrator',
            accountType: 'institution staff',
            gender: 'others',
            dob: new Date().getTime()
          }
        },
        errors: {
          institutionID: '',
          name: '',
          email: '',
          phone: '',
          registrationNumber: '',
          address: '',
          country: '',
          stateProvince: '',
          city: '',
          accountType: '',
          avatar: '',
          status: '',
        }
      }
    },

    computed: {
      loggedUserDetails() {
        return this.$store.getters.loggedUser;
      },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      onFileSelected(event) {
        const files = event.target.files
        let filename = files[0].name
        if(filename.lastIndexOf(".") <=  0) {
          return alert("Please upload a valid file!");
        }
        const fileReader = new FileReader()
        fileReader.addEventListener('load', () => {
          this.form.avatar = fileReader.result
        })
        fileReader.readAsDataURL(files[0])
        this.selectedFile = files[0]
      },

      validateForm() {
        if (!this.form.institutionID) {
            this.errors.institutionID = "Institution ID is required!"
        }
        if (!this.form.name) {
            this.errors.name = "Name is required!"
        }
        if (!this.form.email) {
            this.errors.email = "Email is required!"
        }
        if (!this.form.phone) {
            this.errors.phone = "Phone number is required!"
        }
        if (!this.form.registrationNumber) {
            this.errors.registrationNumber = "Institution Registration Number is required!"
        }
        if (!this.form.address) {
            this.errors.address = "Address is required!"
        }
        if (!this.form.country) {
            this.errors.country = "Country is required!"
        }
        if (!this.form.stateProvince) {
            this.errors.stateProvince = "State/Province is required!"
        }
        if (!this.form.city) {
            this.errors.city = "City is required!"
        }
        if (!this.form.accountType) {
            this.errors.accountType = "Account type is required!"
        }
        if (parseInt(this.form.status) < 0 && parseInt(this.form.status) > 1) {
            this.errors.status = "Status is required!"
        }
        if(this.form.institutionID && this.form.name && this.form.email && this.form.phone && this.form.registrationNumber && this.form.address && this.form.country && this.form.stateProvince && this.form.city && this.form.accountType && this.form.avatar && this.form.status) {
            return true
        } else {
            return false
        }
      },

      updateErrorStatusOnInput(fieldName) {
        if(this.form[fieldName].toString().length > 0){
          this.errors[fieldName] = ""
          this.errors.message = ""
        }
      },

      attemptRegisteringInstitution() {
        this.isLoading = true
        if (!this.validateForm()) {
          this.isLoading = false; 
          return;
        }

        auth.createUserWithEmailAndPassword(this.form.email, this.form.institutionAdministrator.password)
          .then(res => {
            console.log(res);
            db.collection("institution_staffs").add({
              'userID': this.form.institutionAdministrator.userID,
              'institutionID': this.form.institutionID,
              'fullname': this.form.institutionAdministrator.fullname,
              'email': this.form.email,
              'userRole': this.form.institutionAdministrator.userRole,
              'accountType': this.form.institutionAdministrator.accountType,
              'address': this.form.address,
              'phone': this.form.phone,
              'gender': this.form.institutionAdministrator.gender,
              'dob': this.form.institutionAdministrator.dob,
              'avatar': ''
            }).then(() => {
              db.collection("institutions").add({
                'institutionID': this.form.institutionID,
                'name': this.form.name,
                'email': this.form.email,
                'phone': this.form.phone,
                'registrationNumber': this.form.registrationNumber,
                'address': this.form.address,
                'country': this.form.country,
                'stateProvince': this.form.stateProvince,
                'city': this.form.city,
                'accountType': this.form.accountType,
                'status': parseInt(this.form.status),
                'createdOn': new Date().getTime()
              }).then(() => {
                const filename = this.selectedFile.name
                const ext = filename.slice(filename.lastIndexOf("."))
                firebase.storage().ref("institutions/" + this.form.institutionID + ext).put(this.selectedFile)
                .then((snapshot) => {
                    snapshot.ref.getDownloadURL().then((downloadURL) => {
                        db.collection("institutions").where('institutionID', '==', this.form.institutionID).get()
                        .then( querySnapshot => {
                            querySnapshot.forEach( doc => {
                                doc.ref.update({
                                    "avatar": downloadURL
                                })
                            })

                            const accountID = uuid.v4()
                            db.collection("chat_accounts").add({
                                'accountID': accountID,
                                'userID': this.form.institutionID,
                                'accountType': 'general',
                                'userType': 'institution',
                                'referenced_db': 'institutions',
                                'referenceName': `${this.form.name.split(" ")[0].toUpperCase()}#General`
                            }).then((res) => {
                              console.log(res);
                            }).catch(error => {
                                this.isLoading = false
                                alert(error.message)
                            })

                            const accountID2 = uuid.v1()
                            db.collection("chat_accounts").add({
                                'accountID': accountID2,
                                'userID': this.form.institutionAdministrator.userID,
                                'accountType': 'private',
                                'userType': this.form.institutionAdministrator.userRole,
                                'referenced_db': 'institution_staffs',
                                'referenceName': ""
                            }).then((res) => {
                              console.log(res);
                            }).catch(error => {
                                this.isLoading = false
                                alert(error.message)
                            })

                            db.collection("administrator_activities").add({
                              'userID': this.loggedUserDetails.userID,
                              'connectorPhrase': 'registered new institution',
                              'subject': this.form.name,
                              'action': 'create institution',
                              'createdAt': new Date().getTime()
                            }).then(res => { 
                              console.log(res)
                              this.isLoading = false
                              this.$router.push({
                                name: 'RegisteredInstitutions',
                                params: {
                                  administratorID: this.$route.params.administratorID
                                }
                              });
                            })
                        })
                    })
                })
              }).catch(errorIns => {
                this.isLoading = false
                alert(errorIns.message)
              })
            }).catch(errorInsStf => {
              this.isLoading = false
              alert(errorInsStf.message)
            });
          },
          
          err => {
            this.isLoading = false
            alert(err.message)
          })
      }
    },

    created() {
      this.form.institutionID = uuid.v1()
      this.form.institutionAdministrator.userID = uuid.v4()
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    }
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
