            </div>
            <div class="col-md-4 col-sm-4 col-lg-4 offset-md-2 offset-sm-2 offset-lg-2 mt-5 mb-5">
              <div class="dash-widget">
                <h3 class="mb-3">Registered Students</h3>
                <span class="dash-widget-bg2"><i class="fa fa-users"></i></span>
                <div class="dash-widget-info text-right">
                    <h3>{{studentsCount}}</h3>
                    <span class="widget-title2">Students <i class="fa fa-check" aria-hidden="true"></i></span>
                </div>
              </div>
            </div>
            <div class="col-md-4 col-sm-4 col-lg-4 offset-md-1 offset-sm-1 offset-lg-1 mt-5 mb-5">
              <div class="dash-widget">
                <h3 class="mb-3">Registered Classrooms</h3>
                <span class="dash-widget-bg3"><i class="far fa-object-group"></i></span>
                <div class="dash-widget-info text-right">
                    <h3>{{classroomsCount}}</h3>
                    <span class="widget-title3">Classrooms <i class="fa fa-check" aria-hidden="true"></i></span>
                </div>
              </div>
            </div>
            <div class="col-md-4 col-sm-4 col-lg-4 offset-md-2 offset-sm-2 offset-lg-2 mt-5 mb-5">
              <div class="dash-widget">
                <h3 class="mb-3">Registered Courses</h3>
                <span class="dash-widget-bg4"><i class="fas fa-chalkboard-teacher"></i></span>
                <div class="dash-widget-info text-right">
                    <h3>{{coursesCount}}</h3>
                    <span class="widget-title4">Courses <i class="fa fa-check" aria-hidden="true"></i></span>
                </div>
              </div>
            </div>
            <div class="col-md-4 col-sm-4 col-lg-4 offset-md-1 offset-sm-1 offset-lg-1 mt-5 mb-5">
              <div class="dash-widget">
                <h3 class="mb-3">Examination History</h3>
                <span class="dash-widget-bg2"><i class="fas fa-file-alt"></i></span>
                <div class="dash-widget-info text-right">
                    <h3>{{examinationsCount}}</h3>
                    <span class="widget-title2">Examinations <i class="fa fa-check" aria-hidden="true"></i></span>
                </div>
              </div>
            </div>
          </div>
          <div v-if="!isAdministratorLogin" class="row">
            <div class="jumbotron">
              Welcome to <strong>{{loggedUser.fullname}}</strong>'s Dashboard<br>
              <p class="mt-3">
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nullam non eleifend nisi, et hendrerit erat. Nullam vitae ullamcorper eros, ac venenatis urna. Donec tempor ut ex ut efficitur. Maecenas a urna diam. Curabitur eleifend dolor orci, eget mollis leo volutpat id. 
                  Integer sit amet egestas eros. Suspendisse at ullamcorper urna. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos.In dictum ante tristique nisi tristique, id convallis nulla tempus. Ut sit amet massa venenatis, efficitur purus in, varius massa. 
                  Pellentesque eget lectus sed eros porta maximus. Phasellus hendrerit justo erat, eget finibus eros malesuada ac. Phasellus sodales turpis lobortis, aliquam urna eu, dapibus tellus. Sed consequat tincidunt tellus, quis pulvinar leo porttitor ac. Suspendisse sagittis eget est in egestas. Vestibulum at auctor lectus, quis ultrices turpis. Morbi viverra semper mi, vitae finibus magna maximus et. Suspendisse potenti. Duis vestibulum fringilla dui ac scelerisque. 
                  Aenean porttitor molestie rhoncus. Suspendisse dictum vel est lacinia tristique. Donec lacinia at nunc at bibendum. Fusce vel orci diam. Fusce id venenatis sapien, et imperdiet metus. 
              </p>
            </div>            
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'

  export default {
    name: "Dashboard",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'dashboard',
        institution: {},

        employeesCount: 0,
        studentsCount: 0,
        classroomsCount: 0,
        coursesCount: 0,
        examinationsCount: 0
      }
    },

    computed: {
        loggedUser() {
          return this.$store.getters.loggedUser
        },

        isAdministratorLogin() {
          return this.loggedUser.userRole === "institution administrator"
        },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    created() {
      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

        db.collection("institution_staffs").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              if (doc.data()) {
                this.employeesCount += 1
              }
            })
          })

        db.collection("students").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              if (doc.data()) {
                this.studentsCount += 1
              }
            })
          })

        db.collection("classrooms").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              if (doc.data()) {
                this.classroomsCount += 1
              }
            })
          })

        db.collection("courses").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              if (doc.data()) {
                this.coursesCount += 1
              }
            })
          })

        db.collection("examinations").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              if (doc.data()) {
                this.examinationsCount += 1
              }
            })
          })
    }
  }
</script>

<style>
    .dashboard {
      width: 100%;
    }
</style>
