<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-md-4 col-sm-4 col-lg-4 offset-md-1 offset-sm-1 offset-lg-1 mt-5 mb-5">
                <div class="dash-widget">
                  <h3 class="mb-3">Registered Institution Logs</h3>
                  <span class="dash-widget-bg1"><i class="fas fa-university"></i></span>
                  <div class="dash-widget-info text-right">
                    <h3>{{institutions.length}}</h3>
                    <span class="widget-title1">Registered Institutions <i class="fa fa-check" aria-hidden="true"></i></span>
                  </div>
                </div>
              </div>
              <div class="col-md-4 col-sm-4 col-lg-4 offset-md-2 offset-sm-2 offset-lg-2 mt-5 mb-5">
                  <div class="dash-widget">
                      <h3 class="mb-3">Active Institution Logs</h3>
                      <span class="dash-widget-bg2"><i class="fas fa-university"></i></span>
                      <div class="dash-widget-info text-right">
                          <h3>{{getActiveAccountCount()}}</h3>
                          <span class="widget-title2">Active Institutions<i class="fa fa-check" aria-hidden="true"></i></span>
                      </div>
                  </div>
              </div>
              <div class="col-md-4 col-sm-4 col-lg-4 offset-md-1 offset-sm-1 offset-lg-1 mt-5 mb-5">
                  <div class="dash-widget">
                      <h3 class="mb-3">Suspended Institution Logs</h3>
                      <span class="dash-widget-bg4"><i class="fas fa-university"></i></span>
                      <div class="dash-widget-info text-right">
                          <h3>{{getSuspendedAccountCount()}}</h3>
                          <span class="widget-title4">Suspend Institutions<i class="fa fa-check" aria-hidden="true"></i></span>
                      </div>
                  </div>
              </div>
              <div class="col-md-4 col-sm-4 col-lg-4 offset-md-2 offset-sm-2 offset-lg-2 mt-5 mb-5">
                  <div class="dash-widget">
                      <h3 class="mb-3">Registered Administrator Accounts</h3>
                      <span class="dash-widget-bg1"><i class="fas fa-user-cog"></i></span>
                      <div class="dash-widget-info text-right">
                          <h3>{{users.length}}</h3>
                          <span class="widget-title1">Administrative Users<i class="fa fa-check" aria-hidden="true"></i></span>
                      </div>
                  </div>
              </div>
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'

  export default {
    name: "Dashboard",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'dashboard',
        institutions: [],
        users: []
      }
    },

    computed: {
      appName() {
        return this.$store.getters.appName
      },

      appNameAbbr() {
        return this.$store.getters.appNameAbbr
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      getActiveAccountCount() {
         let count = 0;
         this.institutions.map(institution => {
           if (institution.status === 1) {
             count+= 1
           }
         })
         return count
      },

      getSuspendedAccountCount() {
         let count = 0;
         this.institutions.map(institution => {
           if (institution.status === 0) {
             count+= 1
           }
         })
         return count
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    }, 

    created() {

      db.collection("system_administrators").orderBy("fullname").get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'userID': doc.data().userID,
              'fullname': doc.data().fullname,
              'email': doc.data().email,
              'userRole': doc.data().userRole,
              'accountType': doc.data().accountType
            }
            this.users.push(data)
          })
        })


      db.collection("institutions").orderBy("name").get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'institutionID': doc.data().institutionID,
              'name': doc.data().name,
              'avatar': doc.data().avatar,
              'email': doc.data().email,
              'phone': doc.data().phone,
              'registrationNumber': doc.data().registrationNumber,
              'address': doc.data().address,
              'country': doc.data().country,
              'stateProvince': doc.data().stateProvince,
              'city': doc.data().city,
              'accountType': doc.data().accountType,
              'status': doc.data().status,
              'createdOn': doc.data().createdOn
            }
            this.institutions.push(data)
          })
        })
    }
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
