<template>
    <div class="container-fluid mb-4">
        <div class="row">
            <div class="col-sm-8 offset-md-2 mb-3">
                <h5 class="h4 text-center">Add Heading</h5>
                <input @input="updateErrorStatusOnInput('headerString')" type="text" class="form-control" v-model="form.headerString">
                <small v-if="errors.headerString" class="form-text text-danger d-flex justify-content-center mb-0">{{errors.headerString}}</small>
            </div>
            <div class="col-sm-6 offset-md-3 mb-2">
                <button @click.prevent="attemptAddingUIHeading" class="btn btn-block btn-success">Add UI Heading</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "addinstruction-heading",

        props: {
            tagName: {
                type: String,
                required: true
            }
        },

        data() {
            return {
                form: {
                    headerString: "",
                }, 

                errors: {
                    headerString: "",
                }
            }
        },

        methods: {
            attemptAddingUIHeading() {
                if (!this.validateForm()) {
                  return;
                }

                const elementObject = {}
                elementObject.tagName = this.tagName
                elementObject.value = this.form.headerString
                elementObject.elementString = `<${this.tagName} class="${this.tagName} text-center">${this.form.headerString}</${this.tagName}>`
                window.EventBus.emit('instruction-ui-element-added', elementObject)
            },

            validateForm() {
                if (!this.form.headerString) {
                    this.errors.headerString = "Header details is required!"
                }
                if(this.form.headerString) {
                    return true
                } else {
                    return false
                }
            },

            updateErrorStatusOnInput(fieldName) {
              if(this.form[fieldName].toString().length > 0){
                this.errors[fieldName] = ""
                this.errors.message = ""
              }
            }
        }
    }
</script>