          <div class="row mb-3">
            <div class="col-md-6 offset-md-3">
                <ul class="list-group list-group-flush">
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Classroom ID</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedClassroom.institutionID}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Name</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedClassroom.name}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Grade</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedClassroom.grade}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Capacity</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedClassroom.capacity}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Number of assigned students</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{selectedClassroom.numberOfStudentsAssigned}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Overseen By</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{getEmployeeNameByID(selectedClassroom.overSeenBy)}}</strong></div>
                        </div>
                    </li>
                    <li class="list-group-item transparent-bg">
                        <div class="row">
                            <div class="col-3">Created On</div><div class="col-1">:</div> <div class="col-8"><strong class="text-dark">{{customizeDate(parseInt(selectedClassroom.createdOn))}}</strong></div>
                        </div>
                    </li>
                </ul>
            </div>              
          </div>
          <hr class="mb-3">
          <h3 class="h4">
            Examination Time Table 
            <a v-if="isAdministratorLogin && isEmptyObject(timetable)" class="ml-5 text-success btn btn-outline-success btn-sm" @click.prevent="addTimeTableToClassroom">
              <i class="fas fa-plus mr-1"></i>
              Create Time Table
            </a>
            <a v-if="isAdministratorLogin && !isEmptyObject(timetable)" class="ml-5 text-success btn btn-outline-success btn-sm" @click.prevent="modifyTimeTable">
              <i class="fas fa-plus mr-1"></i>
              Manage Time Table
            </a>
          </h3>
          <hr class="mb-3">   
          <div class="row">
              <div v-if="isEmptyObject(timetable)" class="col-8 offset-2 mt-3 mb-3">
                <div class="jumbotron text-center">
                  <h3 class="h4">Time Table has NOT been created!</h3>
                </div>
              </div>
              <div v-if="!isEmptyObject(timetable)" class="col-md-12 mb-5">
                <h4 class="h4 text-center mt-3 mb-3">({{getExaminationSemesterCode(examination.academicSession, examination.startDate)}}) {{examination.title}}</h4>
                <table class="table table-bordered table-dark text-white">
                  <!-- <thead> -->
                    <!-- <tr> -->
                      <!-- <th scope="col">#</th> -->
                      <!-- <th scope="col">First</th> -->
                      <!-- <th scope="col">Last</th> -->
                      <!-- <th scope="col">Handle</th> -->
                    <!-- </tr> -->
                  <!-- </thead> -->
                  <tbody>
                    <tr v-for="(day, index) in timetable.details" :key="index">
                      <td v-for="paper in day" :key="paper.paerID">
                        <div v-if="paper.isDayInfo" class="day-info">
                          <span class="pt-5">
                            <strong class="text-danger text-capitalize font-weight-bold font-italic">
                              {{paper.weekday}}
                            </strong>
                          </span>
                          <span>
                            {{paper.date}}
                          </span>
                        </div>
                        <div v-else>
                          <div class="mb-3 col-12 text-center bg-danger" v-if="index == 0"><strong>{{paper.start}}</strong> - <strong>{{paper.end}}</strong></div>
                          <span class="paper-info" :class="{'pt-3': index == 0, 'pt-4': index != 0}" v-html="getPaperInformation(paper.courseID, paper.paperID)">
                        
                          </span>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>                
              </div>
          </div>
          <hr class="mb-3 mt-5">
          <h3 class="h3">
            Courses
            <a v-if="isAdministratorLogin && !showAddCourse" class="ml-5 text-success btn btn-outline-success btn-sm" @click.prevent="toggleShowAddCourse">
              <i class="fas fa-plus mr-1"></i>
              Add Course
            </a>
            <a v-if="isAdministratorLogin && showAddCourse" class="ml-5 text-white btn btn-danger btn-sm" @click.prevent="toggleShowAddCourse">
              <i class="fas fa-times mr-1"></i>
              Close
            </a>
          </h3>
          <hr class="mb-3"> 
          <addcourse-classroom :courses="courses" v-if="canShowAddCourse" class="addcourse-classroom"></addcourse-classroom> 
          <hr v-if="canShowAddCourse" class="mb-5">  
          <div class="row">
            <div v-if="classroomCourses.length <= 0" class="col-8 offset-2 mt-3 mb-3">
              <div class="jumbotron text-center">
                <h3 class="h4">Course list is empty!</h3>
              </div>
            </div>
            <div v-if="classroomCourses.length > 0" class="col-md-12 mb-5">
              <div class="table-responsive">
                <table class="table table-striped custom-table">
                  <thead>
                    <tr>
                      <th style="min-width:200px;">Course Title</th>
                      <th>Course ID</th>
                      <th>Course Code</th>
                      <th>Grade</th>
                      <th>Duration</th>
                      <th>Instructor</th>
                      <th>Created On</th>
                      <th v-if="!isAdministratorLogin" class="text-right">Action</th>
                    </tr>
                  </thead>
                  <tbody :set="tempCourses = []">
                    <tr
                      v-for="course in classroomCourses"
                      :key="course.courseID"
                    >
                      <td>
                        <img width="28" height="28" class="rounded-circle"> <h2>{{getCourseRecordByID(course.courseID).title}}</h2>
                      </td>   
                      <td>{{getCourseRecordByID(course.courseID).courseID}}</td>
                      <td>{{getCourseRecordByID(course.courseID).code}}</td>
                      <td>{{getCourseRecordByID(course.courseID).grade}}</td>
                      <td>{{getCourseRecordByID(course.courseID).duration}} {{getCourseRecordByID(course.courseID).durationUnit}}</td>
                      <td>{{getEmployeeNameByID(getCourseRecordByID(course.courseID).instructor)}}</td>
                      <td>{{customizeDate(parseInt(getCourseRecordByID(course.courseID).createdOn))}}</td>
                      <td v-if="!isAdministratorLogin" class="text-right">
                        <div class="dropdown dropdown-action">
                          <a @click.prevent="toggleActionMenuOptions($event)" class="action-icon dropdown-toggle" style="cursor: pointer;" aria-expanded="false"><i class="fa fa-ellipsis-v"></i></a>
                          <div class="dropdown-menu dropdown-menu-right" style="position: absolute; will-change: transform; top: -38px; left: -55px; transform: translate3d(-63px, 27px, 0px);">
                            <a v-if="!courseExaminationQuestRecordCreated(course.courseID)" @click.prevent="loadAddExaminationRecordPage(course.courseID, getCourseRecordByID(course.courseID).instructor)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-file m-r-5"></i>Add Examination</a>
                            <a v-if="courseExaminationQuestRecordCreated(course.courseID)" @click.prevent="loadAddExaminationRecordPage(course.courseID, getCourseRecordByID(course.courseID).instructor)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-cogs m-r-5"></i>Manage</a>
                          </div>                            
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <hr class="mb-3 mt-5">
          <h3 class="h3">
            Students
            <a v-if="isAdministratorLogin && !showAddStudent" class="ml-5 text-success btn btn-outline-success btn-sm" @click.prevent="toggleShowAddStudent">
              <i class="fas fa-user-plus mr-1"></i>
              Add Student
            </a>
            <a v-if="isAdministratorLogin && showAddStudent" class="ml-5 text-white btn btn-danger btn-sm" @click.prevent="toggleShowAddStudent">
              <i class="fas fa-times mr-1"></i>
              Close
            </a>
          </h3>
          <hr class="mb-3"> 
          <addstudent-classroom :students="students" v-if="canShowAddStudent" class="addstudent-classroom"></addstudent-classroom>
          <hr v-if="canShowAddStudent" class="mb-5"> 
          <div class="row">
            <div v-if="classroomStudents.length <= 0" class="col-8 offset-2 mt-3 mb-5">
              <div class="jumbotron text-center">
                <h3 class="h4">Student list is empty!</h3>
              </div>
            </div>
            <div v-if="classroomStudents.length > 0" class="col-md-12">
              <div class="table-responsive">
                <table class="table table-striped custom-table">
                  <thead>
                    <tr>
                      <th style="min-width:200px;">Name</th>
                      <th>Student ID</th>
                      <th>Email</th>
                      <th>Mobile</th>
                      <th style="min-width: 110px;">Date of Birth</th>
                      <th>Registered On</th>
                      <th v-if="!isAdministratorLogin" class="text-right">Action</th>
                    </tr>
                  </thead>
                  <tbody :set="tempStudents = []">
                    <tr
                      v-for="student in classroomStudents"
                      :key="student.studentID"
                    >
                      <td>
                        <img width="28" height="28" :src="getStudentRecordByID(student.studentID).avatar" class="rounded-circle" alt=""> <h2>{{getStudentRecordByID(student.studentID).fullname}}</h2>
                      </td>   
                      <td>{{getStudentRecordByID(student.studentID).studentID}}</td>
                      <td>{{getStudentRecordByID(student.studentID).email}}</td>
                      <td>{{getStudentRecordByID(student.studentID).phone}}</td>
                      <td>{{customizeDate(getStudentRecordByID(student.studentID).dob)}}</td>
                      <td>
                        {{customizeDate(getStudentRecordByID(student.studentID).createdOn)}}
                      </td>
                      <td v-if="!isAdministratorLogin" class="text-right">
                        <div class="dropdown dropdown-action">
                          <a class="action-icon dropdown-toggle" style="cursor: pointer;" aria-expanded="false"><i class="fa fa-ellipsis-v"></i></a>
                          <div class="dropdown-menu dropdown-menu-right" style="position: absolute; will-change: transform; top: -38px; left: -55px; transform: translate3d(-63px, 27px, 0px);">
                            <a class="dropdown-item" style="cursor: pointer;"><i class="fa fa-file m-r-5"></i> Manage</a>
                            <a class="dropdown-item" style="cursor: pointer;"><i class="fa fa-trash m-r-5"></i> Delete</a>
                          </div>  
                        </div>                          
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>            
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'
  import AddCourseToClassroom from './AddCourseToClassroom'
  import AddStudentToClassroom from './AddStudentToClassroom'

  import { db } from '../../firebase/firebaseInit'

  import { customizeDate } from '../../helpers/customizeDate'
  import { isEmptyObject } from '../../helpers/isEmptyObject'

  export default {
    name: "ClassroomDetails",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages,
      'addcourse-classroom': AddCourseToClassroom,
      'addstudent-classroom': AddStudentToClassroom
    },

    data() {
      return {
        active: 'registered institutions',
        institutionID: null,
        classroomID: null,
        institution: {},
        selectedClassroom: {},

        showAddCourse: false,
        showAddStudent: false,
        showAddTimeTable: false,

        timetable: {},
        examination: {},
        question: {},
        students: [],
        classroomStudents: [],
        courses: [],
        classroomCourses: [],
        employees: []
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },

      isAdministratorLogin() {
        return this.loggedUser.userRole === "institution administrator"
      },

      canShowAddStudent() {
        return this.isAdministratorLogin && this.showAddStudent
      },

      canShowAddCourse() {
        return this.isAdministratorLogin && this.showAddCourse
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      async toggleActionMenuOptions(event) {
        let targetElement = this.getTargetElement(event);

        if (targetElement.parentNode.classList.contains('show')) {
          targetElement.parentNode.classList.remove('show');
          targetElement.nextElementSibling.classList.remove('show');
        } else {
          await this.closeAllOpenedActionMenuOptions()
          targetElement.parentNode.classList.add('show');
          targetElement.nextElementSibling.classList.add('show');
        }
      },

      getTargetElement(events) {
        if (events.target.tagName === "A") {
          return events.target;
        } else if (events.target.parentNode.tagName === "A") {
          return events.target.parentNode;
        } else if (events.target.parentNode.parentNode.tagName === "A") {
          return events.target.parentNode.parentNode;
        } else {
          return null;
        }
      },

      async closeAllOpenedActionMenuOptions() {
        const allActionMenuOption = document.querySelectorAll(".dropdown-action")

        for (let i=0; i<allActionMenuOption.length; i++) {
          if (allActionMenuOption[i].classList.contains('show')) {
            allActionMenuOption[i].classList.remove('show');
          }
        }
      },

      async courseExaminationQuestRecordCreated(courseID) {
        if (!this.isEmptyObject(this.timetable)) {
          await this.getQuestionObject(courseID, this.timetable.timetableID)
        }
        return false
      },

      canCreateCourseExaminationQuestion(courseID) {
        let recordExist = false
        if (this.isEmptyObject(this.timetable)) return false
        this.timetable.details.map(dailyRecord => {
          dailyRecord.map(courseRecord => {
            if (courseRecord.courseID && courseRecord.courseID === courseID) {
              recordExist = true;
            }
          })
        }) 
        return recordExist
      },

      loadAddExaminationRecordPage(courseID, overSeenBy) {
        if (overSeenBy === this.loggedUser.userID) {
          if(this.canCreateCourseExaminationQuestion(courseID)) {
            this.$router.push({
              name: "InstitutionExaminationCourseQuestionInstructions",
              params: {
                institutionID: this.institutionID,
                classroomID: this.classroomID,
                examinationID: this.timetable.examinationID,
                timetableID: this.timetable.timetableID,
                courseID: courseID
              }
            })
          } else {
            alert("Time table record for this course is not available at this time!")
          }
        } else {
          alert("You DO NOT have the clearance to manage this course")
        }
      },

      loadManageExaminationRecordPage(courseID) {
        this.$router.push({
          name: "",
          params: {
            institutionID: this.institutionID,
            classroomID: this.classroomID,
            courseID: courseID
          }
        })        
      },

      isEmptyObject(obj) {
        return isEmptyObject(obj)
      },

      getEmployeeNameByID(userID) {
        let name = "Unknown"
        this.employees.map(employee => {
          if (employee.userID === userID) {
            name = employee.fullname
          }
        })
        return name
      },

      getStudentRecordByID(studentID) {
        let record = null

        this.students.map(student => {
          if (student.studentID === studentID) {
            record = student
          }
        })

        return record
      },

      getCourseRecordByID(courseID) {
        let record = null

        this.courses.map(course => {
          if (course.courseID === courseID) {
            record = course
          }
        })

        return record
      },

      getPaperInformation(courseID, paperID) {
        if (paperID.indexOf('break') > 0) {
          const labelArray = paperID.split("-")
          labelArray.splice(0, 1)
          return `<span>${labelArray.join(" ").toUpperCase()}</span>`
        }
        const course = this.getCourseRecordByID(courseID)
        return this.isEmptyObject(course) ? 
        `<span>${'No Examination'}</span><span>(${'Free Period'})</span>` :
        `<span>${course.title}</span>
         <span>(${course.code})</span>`
      },

      getDateString(startDate) {
        const formatedDate =  new Date(startDate).toLocaleString('en', { day: 'numeric', month: 'long', year: 'numeric' })
        const dateStringArray = formatedDate.toString().split(" ")
        return `${dateStringArray[0]} ${dateStringArray[2]}`
      },

      getExaminationSemesterCode(academicSession, startDate) {
          let dateString = this.getDateString(startDate)
          return `${academicSession} ${dateString}`
      },

      customizeDate(timestamp) {
        return customizeDate(timestamp)
      },

      addTimeTableToClassroom() {
        this.$router.push({
          name: 'CreateTimeTable',
          params: {
            institutionID: this.institutionID,
            classroomID: this.classroomID
          }
        })
      },

      modifyTimeTable() {
        this.$router.push({
          name: 'EditTimeTableDetails',
          params: {
            institutionID: this.institutionID,
            classroomID: this.classroomID,
            timetableID: this.timetable.timetableID
          }
        })
      },

      toggleShowAddCourse() {
        this.showAddCourse = !this.showAddCourse
      },

      toggleShowAddStudent() {
        this.showAddStudent = !this.showAddStudent
      },

      updateClassroomStudentRecords(student) {
        this. toggleShowAddStudent()
        let studentCount = 0
        if (!this.isEmptyObject(student)) {
           db.collection("classroom_students").where('classroomID', "==", student.classroomID).get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                if (doc.id) {
                  studentCount += 1
                }
              })
              this.classroomStudents.push(student)
              db.collection("classrooms").where('classroomID', '==', this.classroomID).get()
              .then( querySnapshot => {
                  querySnapshot.forEach( doc => {
                      doc.ref.update({
                          "numberOfStudentsAssigned": studentCount
                      })
                  })
                  this.selectedClassroom.numberOfStudentsAssigned = studentCount
              })
            })
        }
      },

      updateClassroomCourseRecords(course) {
        this. toggleShowAddCourse()
        if (!this.isEmptyObject(course)) {
          this.classroomCourses.push(course)
        }
      },

      getQuestionObject(courseID, timetableID) {
        db.collection("questions").where("timetableID", "==", timetableID).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              if (doc.data().institutionID === this.institutionID && this.classroomID === doc.data().classroomID && doc.data().examinationID === this.examination.examinationID && doc.data().courseID === courseID) {
                this.question.questionID = doc.data().questionID
                this.question.institutionID = doc.data().institutionID
                this.question.classroomID = doc.data().classroomID
                this.question.courseID = doc.data().courseID
                this.question.examinationID = doc.data().examinationID
                this.question.timetableID = doc.data().timetableID
                this.question.createdOn = doc.data().createdOn
              }
            })
            if (!this.isEmptyObject(this.question)) {
              return true
            }
          })        
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
      window.EventBus.on('student-added-to-classroom', this.updateClassroomStudentRecords);
      window.EventBus.on('course-added-to-classroom', this.updateClassroomCourseRecords);
    },

    created() {
      this.institutionID = this.$route.params.institutionID
      this.classroomID = this.$route.params.classroomID

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

      db.collection("classrooms").where("classroomID", "==", this.$route.params.classroomID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            if (this.institutionID === doc.data().institutionID) {
              this.selectedClassroom.id = doc.id
              this.selectedClassroom.classroomID = doc.data().classroomID,
              this.selectedClassroom.institutionID = doc.data().institutionID,
              this.selectedClassroom.name = doc.data().name,
              this.selectedClassroom.grade = doc.data().grade,
              this.selectedClassroom.capacity = parseInt(doc.data().capacity),
              this.selectedClassroom.numberOfStudentsAssigned = doc.data().numberOfStudentsAssigned,
              this.selectedClassroom.overSeenBy = doc.data().overSeenBy,
              this.selectedClassroom.createdOn = doc.data().createdOn
            }
          })
        })

      db.collection("institution_staffs").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'userID': doc.data().userID,
              'institutionID': doc.data().institutionID,
              'fullname': doc.data().fullname,
              'email': doc.data().email,
              'userRole': doc.data().userRole,
              'accountType': doc.data().accountType,
              'address': doc.data().address,
              'phone': doc.data().phone,
              'gender': doc.data().gender,
              'dob': doc.data().dob,
              'avatar': doc.data().avatar
            }                                                                                                                                               
              this.employees.push(data)
            })
          })

      db.collection("courses").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              const data = {
                'id': doc.id,
                'courseID': doc.data().courseID,
                'institutionID': doc.data().institutionID,
                'title': doc.data().title,
                'grade': doc.data().grade,
                'code': doc.data().code,
                'duration': doc.data().duration,
                'durationUnit': doc.data().durationUnit,
                'instructor': doc.data().instructor,
                'description': doc.data().description,
                'createdOn': doc.data().createdOn
              }                                                                                                                                       
              this.courses.push(data)
            })
          })

        db.collection("students").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
                'id': doc.id,
                'studentID': doc.data().studentID,
                'institutionID': doc.data().institutionID,
                'fullname': doc.data().fullname,
                'dob': doc.data().dob,
                'email': doc.data().email,
                'phone': doc.data().phone,
                'address': doc.data().address,
                'userRole': doc.data().userRole,
                'accountType': doc.data().accountType,
                'createdOn': doc.data().createdOn,
                'avatar': doc.data().avatar
            }                                                                                                                      
              this.students.push(data)
            })
          })

        db.collection("classroom_students").where("classroomID", "==", this.classroomID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
                'id': doc.id,
                'studentID': doc.data().studentID,
                'classroomID': doc.data().classroomID 
            }                                                                                                               
              this.classroomStudents.push(data)
            })
          })

        db.collection("classroom_courses").where("classroomID", "==", this.classroomID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
                'id': doc.id,
                'courseID': doc.data().courseID,
                'classroomID': doc.data().classroomID 
            }                                                                                             
              this.classroomCourses.push(data)
            })
          })


        db.collection("examination_timetables").where("classroomID", "==", this.classroomID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              this.timetable.timetableID = doc.data().timeTableID
              this.timetable.classroomID = doc.data().classroomID
              this.timetable.examinationID = doc.data().examinationID
              this.timetable.details = JSON.parse(doc.data().details)
              this.timetable.startHour = doc.data().startHour
              this.timetable.maxNumExaminationPerDay = doc.data().maxNumExaminationPerDay
              this.timetable.maxExaminationDuration = doc.data().maxExaminationDuration
              this.timetable.firstBreak = doc.data().firstBreak
              this.timetable.secondBreak = doc.data().secondBreak
              this.timetable.timeTableDescription = doc.data().timeTableDescription
              this.timetable.createdOn = doc.data().createdOn                                                        
            })
              if (this.timetable.examinationID) {
                db.collection("examinations").where("examinationID", "==", this.timetable.examinationID).get()
                .then(querySnapshot => {
                  querySnapshot.forEach(doc => {
                      this.examination.examinationID = doc.data().examinationID
                      this.examination.institutionID = doc.data().institutionID
                      this.examination.title = doc.data().title
                      this.examination.academicSession = doc.data().academicSession
                      this.examination.examinationStatus = doc.data().examinationStatus
                      this.examination.startDate = doc.data().startDate
                      this.examination.endDate = doc.data().endDate
                      this.examination.examinationDescription = doc.data().examinationDescription
                      this.examination.createdOn = doc.data().createdOn
                    })
                  })
              }
          })
    },
  }
</script>

