<template>
    <div class="sidebar" id="sidebar">
      <div class="sidebar-inner slimscroll">
          <div id="sidebar-menu" class="sidebar-menu">
              <ul>
                  <li class="menu-title">Main</li>
                  <li :class="{active: active === 'dashboard'}">
                      <router-link 
                        :to="{
                            name: 'InstitutionalDashboard',
                            params: {
                                institutionID: institutionID
                            }
                        }"
                      >
                        <i class="fas fa-tachometer-alt"></i> <span>Dashboard</span>
                      </router-link>
                  </li>
                  <li :class="{active: active === 'chats'}">
                      <router-link 
                        :to="{
                            name: 'InstitutionChats',
                            params: {
                                institutionID: institutionID,
                                chatMateID: this.institutionChatChannel.accountID
                            }
                        }"
                      >
                        <i class="fa fa-comment"></i> <span>Chats</span>
                      </router-link>
                  </li>
                  <li v-if="isAdministratorLogin" class="submenu">
                    <a @click.prevent="toggleSubmenu"><i class="fas fa-users"></i> <span>Employees</span> <span class="menu-arrow"></span></a>
                    <ul style="display: none;">
                        <li :class="{active: active === 'all employees'}">
                          <router-link 
                            :to="{
                              name: 'InstitutionEmployees',
                              params: {
                                institutionID: institutionID
                              }                              
                            }"
                          > All Employees </router-link>
                        </li>
                        <li :class="{active: active === 'Register Employee'}">
                          <router-link 
                            :to="{
                              name: 'RegisterEmployee',
                              params: {
                                institutionID: institutionID
                              }                                                            
                            }"
                          > Register Employee </router-link>
                        </li>
                    </ul>
                  </li>
                  <li v-if="isAdministratorLogin" class="submenu">
                    <a @click.prevent="toggleSubmenu"><i class="fas fa-users"></i> <span>Students</span> <span class="menu-arrow"></span></a>
                    <ul style="display: none;">
                        <li :class="{active: active === 'all students'}">
                          <router-link 
                            :to="{
                              name: 'InstitutionStudents',
                              params: {
                                institutionID: institutionID
                              }                              
                            }"
                          > All Students </router-link>
                        </li>
                        <li :class="{active: active === 'Register Employee'}">
                          <router-link 
                            :to="{
                              name: 'RegisterStudent',
                              params: {
                                institutionID: institutionID
                              }                                                            
                            }"
                          > Register Students </router-link>
                        </li>
                    </ul>
                  </li>
                  <li class="submenu">
                    <a @click.prevent="toggleSubmenu"><i class="far fa-object-group"></i> <span>Classrooms</span> <span class="menu-arrow"></span></a>
                    <ul style="display: none;">
                        <li :class="{active: active === 'all employees'}">
                          <router-link 
                            :to="{
                              name: 'InstitutionClassrooms',
                              params: {
                                institutionID: institutionID
                              }                              
                            }"
                          > All Classrooms </router-link>
                        </li>
                        <li v-if="isAdministratorLogin" :class="{active: active === 'Register Employee'}">
                          <router-link 
                            :to="{
                              name: 'CreateClassroom',
                              params: {
                                institutionID: institutionID
                              }                                                            
                            }"
                          > Create Classroom </router-link>
                        </li>
                    </ul>
                  </li>
                  <li v-if="isAdministratorLogin" class="submenu">
                    <a @click.prevent="toggleSubmenu"><i class="fas fa-chalkboard-teacher"></i> <span>Courses</span> <span class="menu-arrow"></span></a>
                    <ul style="display: none;">
                        <li :class="{active: active === 'all employees'}">
                          <router-link 
                            :to="{
                              name: 'InstitutionCourses',
                              params: {
                                institutionID: institutionID
                              }                              
                            }"
                          > All Courses </router-link>
                        </li>
                        <li :class="{active: active === 'Register Employee'}">
                          <router-link 
                            :to="{
                              name: 'CreateCourse',
                              params: {
                                institutionID: institutionID
                              }                                                            
                            }"
                          > Create Courses </router-link>
                        </li>
                    </ul>
                  </li>
                  <li class="submenu">
                    <a @click.prevent="toggleSubmenu"><i class="fas fa-file-alt"></i> <span>Examinations</span> <span class="menu-arrow"></span></a>
                    <ul style="display: none;">
                        <li :class="{active: active === 'all employees'}">
                          <router-link 
                            :to="{
                              name: 'InstitutionExamination',
                              params: {
                                institutionID: institutionID
                              }                              
                            }"
                          > All Examinations </router-link>
                        </li>              
                      </ul>
                  </li>                                    
              </ul>
          </div>
      </div>
  </div>
</template>

<script>
    export default {
      name: "PageMenu",

      props: {
        active: {
          type: String,
          required: true
        }
      },

      data() {
          return {
              institutionID: null,
          }
      },

      computed: {
        loggedUser() {
          return this.$store.getters.loggedUser
        },

        isAdministratorLogin() {
          return this.loggedUser.userRole === "institution administrator"
        },

        generalChatChannels() {
            return this.$store.getters.generalChatChannels
        },

        institutionChatChannel() {
          let targetChannel = {}
          this.generalChatChannels.map(channel => {
            if (channel.userID === this.institutionID) {
              targetChannel = channel
            }
          })
          return targetChannel
        }
      },

      created() {
          this.institutionID = this.$route.params.institutionID
      },

      methods: {
        toggleSubmenu(e) {
          let targetElement = this.getTargetElement(e);

          if (targetElement.parentNode.classList.contains('submenu')) {
            if (targetElement.classList.contains('subdrop')) {
                targetElement.classList.remove('subdrop');
                targetElement.nextElementSibling.style.display = "none"; 
            } else {
                targetElement.classList.add('subdrop');
                targetElement.nextElementSibling.style.display = "block"; 
            }
          }
        },

        getTargetElement(events) {
          if (events.target.tagName === "A") {
            return events.target;
          } else if (events.target.parentNode.tagName === "A") {
            return events.target.parentNode;
          } else if (events.target.parentNode.parentNode.tagName === "A") {
            return events.target.parentNode.parentNode;
          } else {
            return null;
          }
        }
      }
    }
</script>

