<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title">Create Administrator</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                  <router-link
                    class="btn btn-success btn-rounded float-right"
                    :to="{
                      name: 'AdministrativeUsers',
                      params: {
                        administratorID: administratorID
                      }                                                
                    }"
                  ><i class="fa fa-eye mr-2"></i>Administrative Users</router-link>
              </div>
          </div>
          <div class="row">
            <div class="col-lg-8 offset-lg-2">
                <form>
                  <div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>User ID</label>
                        <input @input="updateErrorStatusOnInput('userID')" type="text" class="form-control text-center" readonly v-model="form.userID">
                        <small v-if="errors.userID" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.userID}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Fullname <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('fullname')" class="form-control" type="text" v-model="form.fullname">
                        <small v-if="errors.fullname" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.fullname}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Email <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('email')" class="form-control" type="email" v-model="form.email">
                        <small v-if="errors.email" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.email}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Password <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('password')" class="form-control" type="text" v-model="form.password" readonly>
                        <small v-if="errors.password" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.password}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Role <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('userRole')" class="form-control" type="text" v-model="form.userRole" readonly>
                        <small v-if="errors.userRole" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.userRole}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Account Type <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('accountType')" class="form-control" type="text" v-model="form.accountType" readonly>
                        <small v-if="errors.accountType" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.accountType}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Avatar</label>
                        <div class="profile-upload">
                          <div class="upload-img">
                            <img alt="" :src="form.avatar">
                          </div>
                          <div class="upload-input">
                            <input type="file" class="form-control" @change="onFileSelected($event)" accept="image/*" required>
                          </div>
                        </div>
                      </div>
                    </div>

                  </div>         
                  <hr class="mb-3">
                  <div class="form-group text-center">
                    <button v-if="!isLoading" @click.prevent="attemptCreatingAdministratorAccount" class="btn btn-lg btn-success btn-block">Create User Account</button>
                    <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                      <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                      Loading...      
                    </button>
                  </div>
                </form>
            </div>            
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { uuid } from 'vue-uuid';
  import { auth, db } from '../../firebase/firebaseInit'

  import firebase from 'firebase'

  export default {
    name: "CreateAdministrator",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        isLoading: false,
        active: 'create user',
        administratorID: null,
        selectedFile: null,
        form: {
          userID: '',
          fullname: "",
          email: "",
          password: "P@$$w0rd",
          userRole: 'system administrator',
          accountType: 'system administrator',
          avatar: '',
        },
        errors: {
          userID: '',
          fullname: "",
          email: "",
          password: "",
          userRole: '',
          accountType: '',
          avatar: '',
        }
      }
    },

    computed: {
      loggedUserDetails() {
        return this.$store.getters.loggedUser;
      },

      isSuperUser() {
        return this.loggedUserDetails.userRole === "super user"
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      updateErrorStatusOnInput(fieldName) {
        if(this.form[fieldName].toString().length > 0){
          this.errors[fieldName] = ""
          this.errors.message = ""
        }
      },

      validateForm() {
        if (!this.form.userID) {
            this.errors.userID = "User ID is required!"
        }
        if (!this.form.fullname) {
            this.errors.fullname = "Fullname is required!"
        }
        if (!this.form.email) {
            this.errors.email = "Email is required!"
        }
        if (!this.form.password) {
            this.errors.password = "Password is required!"
        }
        if (!this.form.userRole) {
            this.errors.userRole = "User Role is required!"
        }
        if (!this.form.accountType) {
            this.errors.accountType = "Account Type is required!"
        }
        if(this.form.userID && this.form.fullname && this.form.email && this.form.password && this.form.userRole && this.form.accountType) {
          return true
        } else {
          return false;
        }
      },

      onFileSelected(event) {
        const files = event.target.files
        let filename = files[0].name
        if(filename.lastIndexOf(".") <=  0) {
          return alert("Please upload a valid file!");
        }
        const fileReader = new FileReader()
        fileReader.addEventListener('load', () => {
          this.form.avatar = fileReader.result
        })
        fileReader.readAsDataURL(files[0])
        this.selectedFile = files[0]
      },

      attemptCreatingAdministratorAccount() {
        this.isLoading = true
        if (!this.validateForm()) {
          this.isLoading = false; 
          return;
        }
        
        auth.createUserWithEmailAndPassword(this.form.email, this.form.password)
          .then(() => {
              db.collection("system_administrators").add({
                  'userID': this.form.userID,
                  'fullname': this.form.fullname,
                  'email': this.form.email,
                  'userRole': this.form.userRole,
                  'accountType': this.form.accountType,
                  'phone': '---',
                  'createdOn': new Date().getTime()
              }).then((res) => {
                console.log(res);
                const filename = this.selectedFile.name
                const ext = filename.slice(filename.lastIndexOf("."))
                firebase.storage().ref("system_administrators/" + this.form.userID + ext).put(this.selectedFile)
                .then((snapshot) => {
                    snapshot.ref.getDownloadURL().then((downloadURL) => {
                        db.collection("system_administrators").where('userID', '==', this.form.userID).get()
                        .then( querySnapshot => {
                            querySnapshot.forEach( doc => {
                                doc.ref.update({
                                    "avatar": downloadURL
                                })
                            })

                            const accountID = uuid.v4()
                            db.collection("chat_accounts").add({
                                'accountID': accountID,
                                'userID': this.form.userID,
                                'accountType': 'private',
                                'userType': this.form.userRole,
                                'referenced_db': 'system_administrators',
                                'referenceName': ""
                            }).then((res) => {
                              console.log(res);
                            }).catch(error => {
                                this.isLoading = false
                                alert(error.message)
                            })

                            db.collection("administrator_activities").add({
                              'userID': this.loggedUserDetails.userID,
                              'connectorPhrase': 'created an administrator account',
                              'subject': this.form.fullname,
                              'action': 'create user',
                              'createdAt': new Date().getTime()
                            }).then(res => { 
                              console.log(res);
                              alert(`${this.form.fullname}'s account was created successfully.`);
                              this.isLoading = false
                              this.$router.push({
                                name: 'AdministrativeUsers',
                                params: {
                                  administratorID: this.administratorID
                                }
                              })
                            })
                        })
                    })
                })
              }).catch(error => {
                  this.isLoading = false
                  alert(error.message)
              })
          },
                    
          error => {
              this.isLoading = false
              alert(error.message)
          })
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);

      if (!this.isSuperUser) {
        alert("Access denied. You are not permitted to view the requested resource.")
        this.$router.push({
          name: 'AdministratorDashboard',
          params: {
            administratorID: this.administratorID
          }
        })
      }
    },

    created() {
      this.administratorID = this.$route.params.administratorID
      this.form.userID = uuid.v1()
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
