<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title">All Classrooms</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                  <a v-if="searchResultUpdate && (classrooms.length > 0)" @click.prevent="reloadDataRecords" class="btn btn-outline-danger text-danger mr-3"><span class="mb-2"><i class="fas fa-times mr-2"></i></span>Clear Search</a>
                  <router-link
                    v-if="canViewOption"
                    class="btn btn-success btn-rounded float-right"
                    :to="{
                      name: 'CreateClassroom',
                      params: {
                        institutionID: institutionID
                      }                                                
                    }"
                  ><i class="fas fa-user-edit mr-2"></i>Create Classroom</router-link>
              </div>
          </div>
          <div v-if="classrooms.length <= 0" class="row">
              <div class="col-sm-8 offset-sm-2 mt-5">
                <div class="jumbotron text-center">
                    <h3 class="h3">{{emptyMessage}}</h3> <br> <a v-if="searchResultUpdate" @click.prevent="reloadDataRecords" class="btn btn-success text-white"><span class="mb-2"><i class="fas fa-arrow-circle-left mr-2"></i></span>Back</a>
                </div>
              </div>
          </div>
          <div v-else class="container-fluid">
            <div class="row filter-row">
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Classroom ID</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.classroomID">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Name</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.name">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group form-focus">
                  <label class="focus-label">Grade/Year</label>
                  <input @input.prevent="updateSearchOperation" type="text" class="form-control floating" v-model="search.grade">
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                  <a @click.prevent="searchClassrooms" class="btn btn-success btn-block text-white"> Search </a>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12">
                <div class="table-responsive">
                  <table class="table table-striped custom-table">
                    <thead>
                      <tr>
                        <th style="min-width:200px;">Classroom Name</th>
                        <th>Classroom ID</th>
                        <th>Grade/Year</th>
                        <th>Capacity</th>
                        <th>No. assigned Students</th>
                        <th>Overseen By</th>
                        <th>Created On</th>
                        <th class="text-right">Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="classroom in classrooms"
                        :key="classroom.classroomID"
                      >
                        <td>
                          <img width="28" height="28" class="rounded-circle"> <h2>{{classroom.name}}</h2>
                        </td>   
                        <td>{{classroom.classroomID}}</td>
                        <td>{{classroom.grade}}</td>
                        <td>{{classroom.capacity}}</td>
                        <td>{{classroom.numberOfStudentsAssigned}}</td>
                        <td>{{getEmployeeNameByID(classroom.overSeenBy)}}</td>
                        <td>{{customizeDate(parseInt(classroom.createdOn))}}</td>
                        <td class="text-right">
                          <div class="dropdown dropdown-action">
                            <a @click.prevent="toggleActionMenuOptions($event)" class="action-icon dropdown-toggle" style="cursor: pointer;" aria-expanded="false"><i class="fa fa-ellipsis-v"></i></a>
                            <div v-if="canViewOption" class="dropdown-menu dropdown-menu-right" style="position: absolute; will-change: transform; top: -50px; left: -55px; transform: translate3d(-63px, 27px, 0px);">
                              <a @click.prevent="loadSelectedClassroomPage(classroom.classroomID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-cogs m-r-5"></i> Manage</a>
                              <a v-if="canViewOption" @click.prevent="requestDeleteClassroomOperation(classroom.classroomID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-trash m-r-5"></i> Delete</a>
                            </div>
                            <div v-if="!canViewOption" class="dropdown-menu dropdown-menu-right" style="position: absolute; will-change: transform; top: -38px; left: -55px; transform: translate3d(-63px, 27px, 0px);">
                              <a @click.prevent="loadSelectedClassroomPage(classroom.classroomID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-cogs m-r-5"></i> Manage</a>
                              <a v-if="canViewOption" @click.prevent="requestDeleteClassroomOperation(classroom.classroomID)" class="dropdown-item" style="cursor: pointer;"><i class="fa fa-trash m-r-5"></i> Delete</a>
                            </div>                            
                          </div>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'

  import { customizeDate } from '../../helpers/customizeDate'

  import { searchWithThreeParameters } from '../../helpers/searchWithThreeParameters'
  import { searchWithTwoParameters } from '../../helpers/searchWithTwoParameters'
  import { searchWithOneParameter } from '../../helpers/searchWithOneParameter'

  export default {
    name: "Classrooms",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'all classrooms',
        institutionID: null,
        institution: {},
        employees: [],
        classrooms: [],
        emptyMessage: "Classroom list is empty.",
        searchResultUpdate: false,
        search: {
          classroomID: "",
          name: "",
          grade: ""
        },
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },

      canViewOption() {
        return this.loggedUser.userRole === "institution administrator"
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      async toggleActionMenuOptions(event) {
        let targetElement = this.getTargetElement(event);

        if (targetElement.parentNode.classList.contains('show')) {
          targetElement.parentNode.classList.remove('show');
          targetElement.nextElementSibling.classList.remove('show');
        } else {
          await this.closeAllOpenedActionMenuOptions()
          targetElement.parentNode.classList.add('show');
          targetElement.nextElementSibling.classList.add('show');
        }
      },

      getTargetElement(events) {
        if (events.target.tagName === "A") {
          return events.target;
        } else if (events.target.parentNode.tagName === "A") {
          return events.target.parentNode;
        } else if (events.target.parentNode.parentNode.tagName === "A") {
          return events.target.parentNode.parentNode;
        } else {
          return null;
        }
      },

      async closeAllOpenedActionMenuOptions() {
        const allActionMenuOption = document.querySelectorAll(".dropdown-action")

        for (let i=0; i<allActionMenuOption.length; i++) {
          if (allActionMenuOption[i].classList.contains('show')) {
            allActionMenuOption[i].classList.remove('show');
          }
        }
      },

      customizeDate(timestamp) {
        return customizeDate(timestamp)
      },

      loadSelectedClassroomPage(classroomID) {
        console.log(classroomID)
        this.$router.push({
          name: 'InstitutionClassroomDetails',
          params: {
            institutionID: this.institutionID,
            classroomID: classroomID
          }
        })
      },

      async requestDeleteClassroomOperation(classroomID) {
        if (confirm(`Do you want to Delete ${this.getClassroomNameByID(classroomID)} classroom records?`)) {
          await this.closeAllOpenedActionMenuOptions()
          db.collection("classrooms").where('classroomID', '==', classroomID).get()
            .then( querySnapshot => {
              querySnapshot.forEach( doc => {
                  doc.ref.delete()
                  this.classrooms.map(classroom => {
                    if (classroom.classroomID === classroomID) {
                      this.classrooms.splice(this.classrooms.indexOf(classroom), 1)
                    }
                  })
              })
            })
        }
      },

      getEmployeeNameByID(userID) {
        let name = "Unknown"

        this.employees.map(employee => {
          if (employee.userID === userID) {
            name = employee.fullname
          }
        })

        return name
      },

      getClassroomNameByID(classroomID) {
         let name = "Unknown"
         this.classrooms.map(classroom => {
           if (classroom.classroomID === classroomID) {
             name = classroom.name
           }
         })
         return name
      },

      async getClassroomFromFirebase() {
        const temp = []
        await db.collection("classrooms").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              // if (this.canViewOption || this.loggedUser.userID === doc.data().overSeenBy) {
                const data = {
                  'id': doc.id,
                  'classroomID': doc.data().classroomID,
                  'institutionID': doc.data().institutionID,
                  'name': doc.data().name,
                  'grade': doc.data().grade,
                  'capacity': parseInt(doc.data().capacity),
                  'numberOfStudentsAssigned': doc.data().numberOfStudentsAssigned,
                  'overSeenBy': doc.data().overSeenBy,
                  'createdOn': doc.data().createdOn
                }                                                                                                                                             
              temp.push(data)
              // }
            })
          })

        return temp
      },

      async searchClassroomsWithThreeParameters(label1, param1, label2, param2, label3, param3) {
        const allClassrooms = await this.getClassroomFromFirebase()
        const temp = searchWithThreeParameters(allClassrooms, label1, param1, label2, param2, label3, param3)
        
        this.classrooms = temp

        if (this.classrooms.length  >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },

      async searchClassroomsWithTwoParameters(label1, param1, label2, param2) {
        const allClassrooms = await this.getClassroomFromFirebase()
        const temp = searchWithTwoParameters(allClassrooms, label1, param1, label2, param2)
        
        this.classrooms = temp

        if (this.classrooms.length >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },
      
      async searchClassroomsWithOneParameters(label1, param1) {
        const allClassrooms = await this.getClassroomFromFirebase()
        const temp = searchWithOneParameter(allClassrooms, label1, param1)
        
        this.classrooms = temp

        if (this.classrooms.length  >= 0) {
          this.emptyMessage = "Search result is empty."
          this.searchResultUpdate = true
        }
      },

      async reloadDataRecords() {
        this.classrooms = await this.getClassroomFromFirebase()
        this.emptyMessage = "Classroom list is empty."
        this.searchResultUpdate = false
        this.search.classroomID = ""
        this.search.name = ""
        this.search.grade = ""
      },

      searchClassrooms() {
        if (this.search.classroomID && this.search.name && this.search.grade) {
          this.searchClassroomsWithThreeParameters('classroomID', this.search.classroomID, 'name', this.search.name, 'grade', this.search.grade)
        } else if (
          (this.search.classroomID && this.search.name) ||
          (this.search.classroomID && this.search.grade) ||
          (this.search.grade && this.search.name)
        ) {
          if (this.search.classroomID && this.search.name) {
            this.searchClassroomsWithTwoParameters('classroomID', this.search.classroomID, 'name', this.search.name)
          } else if (this.search.classroomID && this.search.grade) {
            this.searchClassroomsWithTwoParameters('classroomID', this.search.classroomID, 'grade', this.search.grade)
          } else if (this.search.grade && this.search.name) {
            this.searchClassroomsWithTwoParameters('grade', this.search.grade, 'name', this.search.name)
          }
        } else if (this.search.classroomID || this.search.name || this.search.grade) {
          if (this.search.classroomID) {
            this.searchClassroomsWithOneParameters('classroomID', this.search.classroomID)
          } else if (this.search.name) {
            this.searchClassroomsWithOneParameters('name', this.search.name)
          } else if (this.search.grade) {
            this.searchClassroomsWithOneParameters('grade', this.search.grade)
          }
        }
      },

      async updateSearchOperation() {
        if (!this.search.classroomID && !this.search.name && !this.search.grade) {
          this.classrooms = await this.getClassroomFromFirebase()
          this.emptyMessage = "Classroom list is empty."
        }
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    async created() {
      this.institutionID = this.$route.params.institutionID

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

        db.collection("institution_staffs").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'userID': doc.data().userID,
              'institutionID': doc.data().institutionID,
              'fullname': doc.data().fullname,
              'email': doc.data().email,
              'userRole': doc.data().userRole,
              'accountType': doc.data().accountType,
              'address': doc.data().address,
              'phone': doc.data().phone,
              'gender': doc.data().gender,
              'dob': doc.data().dob,
              'avatar': doc.data().avatar
            }                                                                                                               
              this.employees.push(data)
            })
          })

      this.classrooms = await this.getClassroomFromFirebase()
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
