export const customizeDate = function(date_info) {
    return new Date(parseInt(date_info)).toLocaleString('en', { day: 'numeric', month: 'short', year: 'numeric' })
}

export const customizeTime = function(date_info) {
    return new Date(parseInt(date_info)).toLocaleTimeString('en', { hour: 'numeric', minute: 'numeric', timeZoneName: 'short', timeZone: 'Europe/Dublin', hour12: false})
}