<template>
    <div class="col-lg-3 message-view chat-profile-view chat-sidebar" id="chat_sidebar">
        <div class="chat-window video-window">
            <div class="fixed-header">
                <ul class="nav nav-tabs nav-tabs-bottom">
                    <li class="nav-item"><a class="nav-link active" href="#profile_tab" data-toggle="tab">Profile</a></li>
                </ul>
            </div>
            <div class="tab-content chat-contents">
                <div class="content-full tab-pane show active" id="profile_tab">
                    <div class="display-table">
                        <div class="table-row">
                            <div class="table-body">
                                <div class="table-content">
                                    <div class="chat-profile-img">
                                        <div class="edit-profile-img">
                                            <img :src="userDetails.avatar ? userDetails.avatar : defaultImage" alt="" style="width:150px; height:120px;">
                                        </div>
                                        <h3 class="user-name m-t-10 mb-0">{{userDetails.name ? userDetails.name : userDetails.fullname}}</h3>
                                        <small class="text-muted">{{ userDetails.userRole }}</small>
                                    </div>
                                    <div class="chat-profile-info">
                                        <ul class="user-det-list">
                                            <li>
                                                <span>{{userDetails.dob ? 'DOB' : 'Created On'}}:</span>
                                                <span class="float-right text-muted">{{userDetails.dob ? customizeDate(userDetails.dob) : customizeDate(userDetails.createdOn) }}</span>
                                            </li>
                                            <li>
                                                <span>{{(userDetails.email ? "Email" : "Grade")}}:</span>
                                                <span class="float-right text-muted">{{userDetails.email ? userDetails.email : userDetails.grade}}</span>
                                            </li>
                                            <li>
                                                <span>{{(userDetails.phone ? "Phone" : "Capacity")}}:</span>
                                                <span class="float-right text-muted">{{userDetails.phone ? userDetails.phone : userDetails.capacity}}</span>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { db } from '../../firebase/firebaseInit'

    import { customizeDate } from '../../helpers/customizeDate'

    export default {
        name: "ChatProfileTab",

        data() {
            return {
                administratorID: "",
                chatMateID: "",
                userDetails: {},
                chatMateChatAccount: {},
                defaultImage: 'https://www.gravatar.com/avatar/205e460b479e2e5b48aec07710c08d50?f=y',
            } 
        },

        methods: {
             updateChatMate(chatMateID) {
                db.collection("chat_accounts").where('accountID', '==', chatMateID).get()
                .then(async querySnapshot => {
                  querySnapshot.forEach(doc => {
                    this.chatMateChatAccount.accountID = doc.data().accountID
                    this.chatMateChatAccount.userID = doc.data().userID
                    this.chatMateChatAccount.accountType = doc.data().accountType
                    this.chatMateChatAccount.userType = doc.data().userType
                    this.chatMateChatAccount.referenced_db = doc.data().referenced_db
                    this.chatMateChatAccount.referenceName = doc.data().referenceName
                  })
                  if (this.chatMateChatAccount) {
                      await this.updateChatMateDetails(this.chatMateChatAccount)
                  }
                })
            },

            customizeDate(targetDate) {
                return customizeDate(targetDate)
            },

            updateChatMateDetails(chatMateChatAccount) {
                if (chatMateChatAccount.referenced_db === 'system_administrators') {
                    db.collection('system_administrators').where("userID", '==', chatMateChatAccount.userID).get()
                    .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            this.userDetails = doc.data()
                        })
                    })  
                } else if (chatMateChatAccount.referenced_db === 'institution_staffs') {
                    db.collection('institution_staffs').where("userID", '==', chatMateChatAccount.userID).get()
                    .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            this.userDetails = doc.data()
                        })
                    })                                      
                } else if (chatMateChatAccount.referenced_db === 'institutions') {
                    db.collection('institutions').where("institutionID", '==', chatMateChatAccount.userID).get()
                    .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            this.userDetails = doc.data()
                        })
                    })                    
                } else if (chatMateChatAccount.referenced_db === 'students') {
                    db.collection('students').where("studentID", '==', chatMateChatAccount.userID).get()
                    .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            this.userDetails = doc.data()
                        })
                    })                    
                } else if (chatMateChatAccount.referenced_db === 'classrooms') {
                    db.collection('classrooms').where("classroomID", '==', chatMateChatAccount.userID).get()
                    .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            this.userDetails = doc.data()
                        })
                    })                    
                } 
            },

            async processChatMateUpdate(chatMateChatAccount) {
                await this.updateChatMateDetails(chatMateChatAccount)
            }
        },

        async created() {
            this.administratorID = this.$route.params.administratorID,
            this.chatMateID = this.$route.params.chatMateID

            await this. updateChatMate(this.chatMateID)
        },

        mounted() {
            window.EventBus.on('update-chat-mate-account-info', this.processChatMateUpdate);
        }
    }
</script>