<template>
    <div class="sidebar" id="sidebar">
        <div class="sidebar-inner slimscroll">
            <div class="sidebar-menu">
                <ul>
                    <li>
                        <router-link 
                         :to="{
                             name: 'InstitutionalDashboard',
                             params: {
                                 institutionID: institutionID
                             }
                         }"
                        ><i class="fa fa-home back-icon"></i> <span>Back to Home</span></router-link>
                    </li>
                    <li class="menu-title">Chat Groups <a href="#" class="add-user-icon" data-toggle="modal" data-target="#add_group"></a></li>
                    <li v-for="channel in generalChatChannels" :key="channel.accountID">
                        <router-link 
                          :to="{
                            name: 'InstitutionChats',
                            params: {
                              institutionID: institutionID,
                              chatMateID: channel.accountID
                            }
                          }"
                        >{{channel.referenceName}}</router-link>
                    </li>
                    <li class="menu-title">Direct Chats <a @click.prevent="requestTogglePrivateChatSearch" class="add-user-icon" data-toggle="modal" data-target="#add_chat_user"><i class="fa fa-plus"></i></a></li>
                    <li v-if="privateChatChannels.length <= 0">
                        <div class="jumbotron">
                          <h6 class="h6">No Private Chat Recorded</h6>
                        </div>
                    </li>
                    <li v-for="channel in privateChatChannels" :key="channel.accountID">
                        <router-link 
                          :to="{
                            name: 'InstitutionChats',
                            params: {
                              institutionID: institutionID,
                              chatMateID: channel.accountID
                            }
                          }"
                        ><span class="chat-avatar-sm user-img"><img :src="getChannelUserInstance(channel) && getChannelUserInstance(channel).avatar ? getChannelUserInstance(channel).avatar : defaultImage" alt="" class="rounded-circle"><span class="status online"></span></span> {{getChannelUserInstance(channel) ? getChannelUserInstance(channel).fullname : "Unknown Name"}} <span class="badge badge-pill bg-danger float-right">1</span></router-link>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import { db } from '../../../firebase/firebaseInit'

    import { processChatAccountRetrieval, processPrivateChatAccountRetrieval } from '../../../helpers/processChatAccountRetrieval'

    export default {
      name: "ChatMenu",

      props: {
        active: {
          type: String,
          required: true
        }
      },

      data() {
          return {
              chatAccountUserIDs: [],
              privateChatAccountUserIDs: [],
              chatMates: [],
              institutionID: "",
              employees: [],
              students: [],
              defaultImage: 'https://www.gravatar.com/avatar/205e460b479e2e5b48aec07710c08d50?f=y',
              loggedUserChatAccount: {},
              applicationGeneralChatChannel: {}
          }
      },

      computed: {
        loggedUserDetails() {
          return this.$store.getters.loggedUser
        },
        
        generalChatChannels() {
            return this.$store.getters.generalChatChannels
        },

        privateChatChannels() {
            return this.$store.getters.privateChatChannels
        },

        prospectChatChannels() {
            return this.$store.getters.prospectChatChannels
        },
      },

      async created() {
        this.institutionID = this.$route.params.institutionID

        db.collection("chat_accounts").get()
        .then( async querySnapshot => {
          querySnapshot.forEach(doc => {
            if ( doc.data().userID === this.loggedUserDetails.userID) {
              this.loggedUserChatAccount.accountID = doc.data().accountID
              this.loggedUserChatAccount.userID = doc.data().userID
              this.loggedUserChatAccount.accountType = doc.data().accountType
              this.loggedUserChatAccount.userType = doc.data().userType
              this.loggedUserChatAccount.referenced_db = doc.data().referenced_db
              this.loggedUserChatAccount.referenceName = doc.data().referenceName
            } else if (doc.data().userType === 'obea chat channel') {
              this.applicationGeneralChatChannel.accountID = doc.data().accountID
              this.applicationGeneralChatChannel.userID = doc.data().userID
              this.applicationGeneralChatChannel.accountType = doc.data().accountType
              this.applicationGeneralChatChannel.userType = doc.data().userType
              this.applicationGeneralChatChannel.referenced_db = doc.data().referenced_db
              this.applicationGeneralChatChannel.referenceName = doc.data().referenceName
            } else {
              const data = {
                'accountID': doc.data().accountID,
                'userID': doc.data().userID,
                'accountType': doc.data().accountType,
                'userType': doc.data().userType,
                'referenced_db': doc.data().referenced_db,
                'referenceName': doc.data().referenceName                
              }
              this.chatMates.push(data)
            }
          })
        })

        await db.collection("institution_staffs").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'userID': doc.data().userID,
              'institutionID': doc.data().institutionID,
              'fullname': doc.data().fullname,
              'email': doc.data().email,
              'userRole': doc.data().userRole,
              'accountType': doc.data().accountType,
              'address': doc.data().address,
              'phone': doc.data().phone,
              'gender': doc.data().gender,
              'dob': doc.data().dob,
              'avatar': doc.data().avatar
            }                                                                                                                                               
              this.employees.push(data)
            })
          })

        await db.collection("students").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
                'id': doc.id,
                'studentID': doc.data().studentID,
                'institutionID': doc.data().institutionID,
                'fullname': doc.data().fullname,
                'dob': doc.data().dob,
                'email': doc.data().email,
                'phone': doc.data().phone,
                'address': doc.data().address,
                'userRole': doc.data().userRole,
                'accountType': doc.data().accountType,
                'createdOn': doc.data().createdOn,
                'avatar': doc.data().avatar
            }                                                                                                  
              this.students.push(data)
            })
          })

        this.generateLoggedUserChatChannel()
      },

      methods: {
        toggleSubmenu(e) {
          let targetElement = this.getTargetElement(e);

          if (targetElement.parentNode.classList.contains('submenu')) {
            if (targetElement.classList.contains('subdrop')) {
                targetElement.classList.remove('subdrop');
                targetElement.nextElementSibling.style.display = "none"; 
            } else {
                targetElement.classList.add('subdrop');
                targetElement.nextElementSibling.style.display = "block"; 
            }
          }
        },

        getTargetElement(events) {
          if (events.target.tagName === "A") {
            return events.target;
          } else if (events.target.parentNode.tagName === "A") {
            return events.target.parentNode;
          } else if (events.target.parentNode.parentNode.tagName === "A") {
            return events.target.parentNode.parentNode;
          } else {
            return null;
          }
        },

        async getPrivateChatIDsFromChatHistory(vueInstance) {
            const temp = []
            await db.collection('chats').where('sender', '==', vueInstance.loggedUserChatAccount.accountID).get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => { 
                temp.push(doc.data().recipient)
              })
            })
            return temp  
        },

        async generateLoggedUserChatChannel() {
          await this.getLoggedUserClassroomfromCourseAssigned()
          await db.collection("classrooms").where("institutionID", "==", this.$route.params.institutionID).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                if (this.loggedUserDetails.userID === doc.data().overSeenBy) { 
                  this.chatAccountUserIDs.push(doc.data().classroomID)
                }
              })
            })

            for (let i=0; i<this.chatAccountUserIDs.length; i++) {
              await db.collection("classroom_students").where("classroomID", "==", this.chatAccountUserIDs[i]).get()
              .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.chatAccountUserIDs.push(doc.data().studentID)
                  })
                })
            }

            await db.collection("institution_staffs").where("institutionID", "==", this.$route.params.institutionID).get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                  if (this.loggedUserDetails.userID !== doc.data().userID) { 
                    this.chatAccountUserIDs.push(doc.data().userID)
                  }
                })
              })

            this.chatAccountUserIDs.push(this.$route.params.institutionID)
            if (this.loggedUserDetails.userRole === "institution administrator") {
              this.chatAccountUserIDs.push(this.applicationGeneralChatChannel.userID)
            }
            this.chatAccountUserIDs = [...new Set(this.chatAccountUserIDs)]

            this.privateChatAccountUserIDs = await this.getPrivateChatIDsFromChatHistory(this)
            this.privateChatAccountUserIDs = [...new Set(this.privateChatAccountUserIDs)]

            processChatAccountRetrieval(db, this, Object.values(this.chatAccountUserIDs))
            processPrivateChatAccountRetrieval(db, this, Object.values(this.privateChatAccountUserIDs))
        },

        async getLoggedUserClassroomfromCourseAssigned() {
          const courseList = await this.getLoggedUserCourses()
          courseList.map( async course => {
            await db.collection("classroom_courses").where("courseID", "==", course.courseID).get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                  this.chatAccountUserIDs.push(doc.data().classroomID)
                })
              })
            })
        },

        async getLoggedUserCourses() {
          const temp = []
          await db.collection("courses").where("instructor", "==", this.loggedUserDetails.userID).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                  'id': doc.id,
                  'courseID': doc.data().courseID,
                  'institutionID': doc.data().institutionID,
                  'title': doc.data().title,
                  'grade': doc.data().grade,
                  'code': doc.data().code,
                  'duration': doc.data().duration,
                  'durationUnit': doc.data().durationUnit,
                  'instructor': doc.data().instructor,
                  'description': doc.data().description,
                  'createdOn': doc.data().createdOn
                }                                                                                                                                             
                temp.push(data)
              })
            })
          return temp
        },

        getChannelUserInstance(channel) {
          let userDetails = null
          if (channel.referenced_db === 'students') {
            this.students.map(user => {
              if (user.studentID === channel.userID) {
                userDetails = user
              }
            })
          } else {
            if (channel.referenced_db === 'institution_staffs') {
              this.employees.map(user => {
                if (user.userID === channel.userID) {
                  userDetails = user
                }
              })
            }
          } 
          return userDetails
        },

        requestTogglePrivateChatSearch() {
           window.EventBus.emit('toggle-private-chat-search-view');
        }
      }
    }
</script>

<style lang="scss" scoped>
  @import url("../../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .sidebar-menu .menu-arrow:before {
    font-family: "FontAwesome";
    content: "\f105" !important;
  }
</style>