<template>
<div>
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-6 col-3">
                  <h4 class="page-title">Edit Examination Question to {{course.title}} ({{course.code}})</h4>
              </div>
              <div class="col-sm-6 text-right m-b-20">
                  <router-link
                    class="btn btn-outline-secondary float-right mr-2"
                    :to="{
                      name: 'InstitutionExaminationCourseQuestions',
                      params: {
                        institutionID: institutionID,
                        classroomID: classroomID,
                        examinationID: examinationID,
                        timetableID: timetableID,
                        courseID: courseID
                      }                                                
                    }"
                  ><i class="fas fa-chevron-left mr-2"></i>Back</router-link>
              </div>
          </div>
          <div class="row">
              <div class="col-lg-8 offset-lg-2">
                <form>
									<div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Examination Question ID</label>
                        <input @input="updateErrorStatusOnInput('examinationQuestionID')" type="text" class="form-control text-center" readonly v-model="form.examinationQuestionID">
                        <small v-if="errors.examinationQuestionID" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.examinationQuestionID}}</small>
                      </div>
                    </div>
                  </div>         
                  <div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Examination Question <span class="text-danger"></span></label>
                        <textarea @input="updateErrorStatusOnInput('examQuestion')" class="form-control" v-model="form.examQuestion"></textarea>
                        <small v-if="errors.examQuestion" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.examQuestion}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>A<span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('a')" class="form-control" type="text" v-model="form.a">
                        <small v-if="errors.a" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.a}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>B<span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('b')" class="form-control" type="text" v-model="form.b">
                        <small v-if="errors.b" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.b}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>C <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('c')" class="form-control" type="text" v-model="form.c">
                        <small v-if="errors.c" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.c}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>D</label>
                        <input @input="updateErrorStatusOnInput('d')" class="form-control" type="email" v-model="form.d">
                        <small v-if="errors.d" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.d}}</small>
                      </div>                      
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Answer Option</label>
                        <select @change="updateErrorStatusOnInput('answer')" class="form-control select" v-model="form.answer">
                          <option value="">Select Question Answer</option>
                          <option value="A">A</option>
                          <option value="B">B</option>
                          <option value="C">C</option>
                          <option value="D">D</option>
                        </select>
                        <small v-if="errors.answer" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.answer}}</small>
                      </div>                      
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Extra Information</label>
                        <textarea @input="updateErrorStatusOnInput('extraInformation')" class="form-control" v-model="form.extraInformation"></textarea>
                        <small v-if="errors.extraInformation" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.extraInformation}}</small>
                      </div>
                    </div>
                  </div>
                  <hr class="mb-3">
                  <div class="form-group text-center">
                    <button v-if="!isLoading" @click.prevent="attemptUpdatingExaminationQuestion" class="btn btn-lg btn-success btn-block">Update Examination Question</button>
                    <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                      <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                      Loading...      
                    </button>
                  </div>
                </form>
              </div>            
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'
  import CryptoJS from 'crypto-js'

  export default {
    name: "EditExaminationCourseQuestion",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        isLoading: false,
        active: 'dashboard',
        institutionID: null,
        classroomID: null,
        courseID: null,
        examinationID: null,
        timetableID: null,
        examinationQuestionID: null,
        institution: {},

        course: {},
        question: {},

        form: {
          examinationQuestionID: "",
          examQuestion: "",
          a: "",
          b: "",
          c: "",
          d: "",
          answer: "",
          extraInformation: ""
        },
        errors: {
          examinationQuestionID: "",
          examQuestion: "",
          a: "",
          b: "",
          c: "",
          d: "",
          answer: "",
          extraInformation: ""
        }
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      validateForm() {
        if (!this.form.examinationQuestionID) {
            this.errors.examinationQuestionID = "Examination Question ID is required!"
        }

        if (!this.form.examQuestion) {
            this.errors.examQuestion = "Examination question is required!"
        }

        if (!this.form.a) {
            this.errors.a = "Option A is required!"
        }

        if (!this.form.b) {
            this.errors.b = "Option B is required!"
        }

        if (!this.form.c) {
            this.errors.c = "Option C is required!"
        }

        if (!this.form.d) {
            this.errors.d = "Option D is required!"
        }

        if (!this.form.answer) {
            this.errors.answer = "Answer option is required!"
        }

        if(this.form.examinationQuestionID && this.form.examQuestion && this.form.a && this.form.b && this.form.c && this.form.d && this.form.answer) {
            return true
        } else {
            return false
        }
      },

      async attemptUpdatingExaminationQuestion() {
        this.isLoading = true

        if (!this.validateForm()) {
          this.isLoading = false; 
          return;
        }

        db.collection("examination_questions").where('examinationQuestionID', '==', this.form.examinationQuestionID).get()
        .then( querySnapshot => {
            querySnapshot.forEach( doc => {
                doc.ref.update({
                  'examQuestion': CryptoJS.enc.Base64.stringify(CryptoJS.enc.Utf8.parse(this.form.examQuestion)),
                  'A': CryptoJS.enc.Base64.stringify(CryptoJS.enc.Utf8.parse(this.form.a)),
                  'B': CryptoJS.enc.Base64.stringify(CryptoJS.enc.Utf8.parse(this.form.b)),
                  'C': CryptoJS.enc.Base64.stringify(CryptoJS.enc.Utf8.parse(this.form.c)),
                  'D': CryptoJS.enc.Base64.stringify(CryptoJS.enc.Utf8.parse(this.form.d)),
                  'answer': CryptoJS.enc.Base64.stringify(CryptoJS.enc.Utf8.parse(this.form.answer)),
                  'extraInformation': this.form.extraInformation,
                })
            })

            this.isLoading = false
            this.$router.push({
              name: 'InstitutionExaminationCourseQuestions',
              params: {
                institutionID: this.institutionID,
                classroomID: this.classroomID,
                examinationID: this.examinationID,
                timetableID: this.timetableID,
                courseID: this.courseID
              }                                                            
            })
        })
      },

      updateErrorStatusOnInput(fieldName) {
        if(this.form[fieldName].toString().length > 0){
          this.errors[fieldName] = ""
          this.errors.message = ""
        }
      },
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    created() {
      this.institutionID = this.$route.params.institutionID
      this.classroomID = this.$route.params.classroomID
      this.courseID = this.$route.params.courseID
      this.examinationID = this.$route.params.examinationID
      this.timetableID = this.$route.params.timetableID
      this.examinationQuestionID = this.$route.params.examinationQuestionID

      db.collection("courses").where("courseID", "==", this.courseID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              this.course.id = doc.id,
              this.course.courseID = doc.data().courseID,
              this.course.institutionID = doc.data().institutionID,
              this.course.title = doc.data().title,
              this.course.grade = doc.data().grade,
              this.course.code = doc.data().code,
              this.course.duration = doc.data().duration,
              this.course.durationUnit = doc.data().durationUnit,
              this.course.instructor = doc.data().instructor,
              this.course.description = doc.data().description,
              this.course.createdOn = doc.data().createdOn                                            
            })
          })

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

        db.collection("questions").where("timetableID", "==", this.timetableID).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              if (doc.data().institutionID === this.institutionID && this.classroomID === doc.data().classroomID && doc.data().examinationID === this.examinationID && doc.data().courseID === this.courseID) {
                this.question.questionID = doc.data().questionID
                this.question.institutionID = doc.data().institutionID
                this.question.classroomID = doc.data().classroomID
                this.question.courseID = doc.data().courseID
                this.question.examinationID = doc.data().examinationID
                this.question.timetableID = doc.data().timetableID
                this.question.createdOn = doc.data().createdOn
              }
            })
          })


        db.collection("examination_questions").where("examinationQuestionID", "==", this.examinationQuestionID).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.form.examinationQuestionID = doc.data().examinationQuestionID
              this.form.examQuestion = CryptoJS.enc.Base64.parse(doc.data().examQuestion).toString(CryptoJS.enc.Utf8)
              this.form.a = CryptoJS.enc.Base64.parse(doc.data()['A']).toString(CryptoJS.enc.Utf8)
              this.form.b = CryptoJS.enc.Base64.parse(doc.data()['B']).toString(CryptoJS.enc.Utf8)
              this.form.c = CryptoJS.enc.Base64.parse(doc.data()['C']).toString(CryptoJS.enc.Utf8)
              this.form.d = CryptoJS.enc.Base64.parse(doc.data()['D']).toString(CryptoJS.enc.Utf8)
              this.form.answer = CryptoJS.enc.Base64.parse(doc.data().answer).toString(CryptoJS.enc.Utf8)
              this.form.extraInformation = doc.data().extraInformation             
            })
          })
    }
  }
</script>

<style>
    .dashboard {
      width: 100%;
    }
</style>
