<template>
  <div class="header">
    <div class="header-left">
      <router-link 
        :to="{ name: 'AdministratorDashboard', params: { administratorID: administratorID } }" 
        class="logo"
      >
        <i class="fas fa-user-cog fa-2x"></i> <span class="h6 pt-2">{{appNameAbbr}} System Admin.</span>
      </router-link>
    </div>
    <a id="mobile_btn" @click.prevent="toggleMobileMenuOption" class="mobile_btn float-left" href="#sidebar"><i class="fa fa-bars"></i></a>
    <ul class="nav user-menu float-right">
      <li class="nav-item dropdown d-none d-sm-block">
        <a @click.prevent="toggleUserOptionMenu" class="dropdown-toggle nav-link" data-toggle="dropdown"><i class="fa fa-bell"></i> <span v-if="unreadNotificationsCount > 0" class="badge badge-pill bg-danger float-right">{{unreadNotificationsCount}}</span></a>
        <page-notifications class="dropdown-menu notifications" x-placement="top-start" style="position: absolute; will-change: transform; top: 50px; left: 0px; transform: translate3d(-171px, 5px, 0px);"></page-notifications>
      </li>
      <li class="nav-item dropdown d-none d-sm-block">
        <a @click.prevent="toggleShowMessageBox" id="open_msg_box" class="hasnotifications nav-link"><i class="fa fa-comment"></i> <span v-if="unreadMessagesCount > 0" class="badge badge-pill bg-danger float-right">{{unreadMessagesCount}}</span></a>
      </li>
      <li class="nav-item dropdown has-arrow">
        <a @click.prevent="toggleUserOptionMenu" class="dropdown-toggle nav-link user-link" data-toggle="dropdown">
          <span class="user-img">
            <img class="rounded-circle" :title="loggedUserFullname" src="../../../assets/user.png" width="24" alt="">
            <span class="status online"></span>
          </span>
          <span class="pr-2 pl-2" :title="loggedUserFullname"><strong>{{loggedUserInitials}}</strong></span>
        </a>
        <div class="dropdown-menu">
                  <router-link 
          class="dropdown-item" 
          :to="{
            name: 'AdministratorProfile',
            params: {
              administratorID: administratorID
            }
          }"
        >My Profile</router-link>
        <router-link 
          class="dropdown-item" 
          :to="{
            name: 'AdministratorProfileEditor',
            params: {
              administratorID: administratorID
            }
          }"
        >Edit Profile</router-link>
          <a class="dropdown-item" @click.prevent="attemptLogout">Logout</a>
        </div>
      </li>
    </ul>
    <div class="dropdown mobile-user-menu float-right">
      <a @click.prevent="toggleUserOptionMenu" class="dropdown-toggle" data-toggle="dropdown" aria-expanded="false"><i class="fa fa-ellipsis-v"></i></a>
      <div class="dropdown-menu dropdown-menu-right">
        <router-link 
          class="dropdown-item" 
          :to="{
            name: 'AdministratorProfile',
            params: {
              administratorID: administratorID
            }
          }"
        >My Profile</router-link>
        <router-link 
          class="dropdown-item" 
          :to="{
            name: 'AdministratorProfileEditor',
            params: {
              administratorID: administratorID
            }
          }"
        >Edit Profile</router-link>
        <a class="dropdown-item" @click.prevent="attemptLogout">Logout</a>
      </div>
    </div>
  </div>
</template>

<script>
    import { auth } from '../../../firebase/firebaseInit'
    import { generateInitialsFromFullname } from '../../../helpers/generateInitialsFromFullname'
    import PageNotification from './PageNotification'

    export default {
      name: "PageHeader",
      components: {
          'page-notifications': PageNotification
      },

      data() {
          return {
              administratorID: null,
              expectedAccountType: 'system administrator'
          }
      },

      computed: {
          appNameAbbr() {
              return this.$store.getters.appNameAbbr
          },

          loggedUserDetails() {
              return this.$store.getters.loggedUser;
          },

          loggedUserFullname() {
              return this.loggedUserDetails.fullname
          },

          loggedUserInitials() {
            return generateInitialsFromFullname(this.loggedUserFullname)
          },

          notifications() {
            return this.$store.getters.notifications
          },

          unreadNotificationsCount() {
            let count = 0
            this.notifications.map(notif => {
              if (notif.status === 'unread') {
                count+=1;
              }
            });
            return count;
          },

          messages() {
            return this.$store.getters.messages
          },

          unreadMessagesCount() {
            let count = 0
            this.messages.map(mess => {
              if (mess.status === 'unread') {
                count+=1;
              }
            });
            return count;
          },
          
      },

      methods: {
        attemptLogout() {
          auth.signOut().then(() => {
            this.$store.dispatch('ATTEMPT_LOGOUT')
              .then(() => {
              this.$router.push({
                  name: "Home"
              })
            }).catch((err) => { console.log(err); })
          })
        },

        toggleMobileMenuOption() {
          window.EventBus.emit('page-header-mobile-menu');
        },

        toggleUserOptionMenu(e) {
            let targetElement = this.getTargetElement(e);

            if (targetElement) {
                if (targetElement.classList.contains('dropdown-toggle')) {
                    if (targetElement.nextElementSibling.classList.contains('show')) {
                        targetElement.nextElementSibling.classList.remove('show');
                    } else {
                        targetElement.nextElementSibling.classList.add('show');
                    }
                }
            }
        },

        toggleShowMessageBox() {
          window.EventBus.emit('page-header-message-box');
        },

        getTargetElement(events) {
            if (events.target.tagName === "A") {
                return events.target;
            } else if (events.target.parentNode.tagName === "A") {
                return events.target.parentNode;
            } else if (events.target.parentNode.parentNode.tagName === "A") {
                return events.target.parentNode.parentNode;
            } else {
                return null;
            }
        }
      },

      created() {
          this.administratorID = this.$route.params.administratorID
      },

      mounted() {
        if (this.loggedUserDetails.accountType !== this.expectedAccountType) {
          alert("You are NOT allowed to view this page!!!")
          this.attemptLogout()
        }
      }
    }
</script>

<style lang="scss" scoped>
    .logo {
        i {
            color: #FFF;
        }
    }

    .header {
      background: maroon;
    }
</style>
