<template>
    <div class="container-fluid mb-4">
        <div class="row">
            <h5 v-if="UIList.length > 0" class="col-sm-6 offset-sm-3 h5">UI List Elements</h5>
            <ul v-if="tagName === 'ul'" class="col-sm-6 offset-sm-3 mb-3">
                <li v-for="(item, index) in UIList" :key="index" >
                    {{item}}
                </li>
            </ul>
            <ol v-if="tagName === 'ol'" class="col-sm-6 offset-sm-3 mb-3">
                <li v-for="(item, index) in UIList" :key="index" >
                    {{item}}
                </li>
            </ol>
            <div class="col-sm-12 mb-3">
                <h5 class="h4 text-center">Add {{tagName === "ol" ? "Ordered" : "Unordered"}} List</h5>
            </div>
            <div class="col-sm-6 offset-sm-2 mb-3">
                <input @input="updateErrorStatusOnInput('listItem')" type="text" v-model="form.listItem" class="form-control" required>
                <small v-if="errors.listItem" class="form-text text-danger d-flex justify-content-center mb-0">{{errors.listItem}}</small>
            </div>
            <div class="col-sm-2 mb-3">
                <button @click.prevent="attemptUpdatingUIListItem" class="btn btn-block btn-success">Add List Item</button>
            </div>
            <div class="col-sm-6 offset-md-3 mb-2">
                <button :disabled="UIList.length <= 0" @click.prevent="attemptAddingUIParagraph" class="btn btn-block btn-success">Add UI List</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "addinstruction-list",

        props: {
            tagName: {
                type: String,
                required: true
            }
        },

        data() {
            return {
                UIList: [],
                form: {
                    listItem: "",
                }, 
                errors: {
                    listItem: "",
                }
            }
        },

        methods: {
            attemptUpdatingUIListItem() {
                if (!this.validateForm()) {
                  return;
                }
                this.UIList.push(this.form.listItem)
                this.form.listItem = ""
            },

            attemptAddingUIParagraph() {
                const elementObject = {}
                elementObject.tagName = this.tagName
                elementObject.value = this.UIList
                elementObject.elementString = `<${this.tagName}>`
                for (let i=0; i<this.UIList.length; i++) {
                    elementObject.elementString += `<li>${this.UIList[i]}</li>`
                }
                elementObject.elementString += `</${this.tagName}>`
                window.EventBus.emit('instruction-ui-element-added', elementObject)
            },

            validateForm() {
                if (!this.form.listItem) {
                    this.errors.listItem = "List item details is required!"
                }
                if(this.form.listItem) {
                    return true
                } else {
                    return false
                }
            },

            updateErrorStatusOnInput(fieldName) {
              if(this.form[fieldName].toString().length > 0){
                this.errors[fieldName] = ""
                this.errors.message = ""
              }
            }                        
        }
    }
</script>