<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-8 col-3">
                  <h4 class="page-title">Edit {{course.title}} ({{course.code}})'s Details</h4>
              </div>
              <div class="col-sm-4 col-9 text-right m-b-20">
                <router-link 
                    :to="{
                        name: 'InstitutionCourseDetails',
                        params: {
                            institutionID: institutionID,
                            courseID: courseID
                        }
                    }" 
                    class="btn btn-outline-danger"
                  >
                    <span class="mb-2"><i class="fas fa-times mr-2"></i></span>Cancel
                  </router-link>
              </div>
          </div>
          <div class="row">
              <div class="col-lg-8 offset-lg-2">
                <form>
									<div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Course ID</label>
                        <input @input="updateErrorStatusOnInput('courseID')" type="text" class="form-control text-center" readonly v-model="form.courseID">
                        <small v-if="errors.courseID" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.courseID}}</small>
                      </div>
                    </div>
                  </div>         
                  <div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Title <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('title')" class="form-control" type="text" v-model="form.title">
                        <small v-if="errors.title" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.title}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Course Code<span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('code')" class="form-control" type="text" v-model="form.code">
                        <small v-if="errors.code" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.code}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Grade/Year<span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('grade')" class="form-control" type="text" v-model="form.grade">
                        <small v-if="errors.grade" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.grade}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Duration <span class="text-danger"></span></label>
                        <input @input="updateErrorStatusOnInput('duration')" class="form-control" type="email" v-model="form.duration">
                        <small v-if="errors.duration" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.duration}}</small>
                      </div>
                    </div>
                    <div class="col-sm-6">
                      <div class="form-group">
                        <label>Duration Unit</label>
                        <select @change="updateErrorStatusOnInput('durationUnit')" class="form-control select" v-model="form.durationUnit">
                          <option value="">Select Duration Unit</option>
                          <option value="Hours">Hours</option>
                          <option value="Weeks">Weeks</option>
                          <option value="Months">Months</option>
                        </select>
                        <small v-if="errors.durationUnit" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.durationUnit}}</small>
                      </div>                      
                    </div>
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Course Instructor</label>
                        <select @change="updateErrorStatusOnInput('instructor')" class="form-control select" v-model="form.instructor">
                          <option value="">Select Course Instructor</option>
                          <option v-for="employee in employees" :key="employee.userID" :value="employee.userID">{{employee.fullname}}</option>
                        </select>
                        <small v-if="errors.instructor" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.instructor}}</small>
                      </div>                      
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label>Description</label>
                        <textarea @input="updateErrorStatusOnInput('description')" class="form-control" v-model="form.description"></textarea>
                        <small v-if="errors.description" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.description}}</small>
                      </div>
                    </div>
                  </div>
                  <hr class="mb-3">
                  <div class="form-group text-center">
                    <button v-if="!isLoading" @click.prevent="attemptCreatingCourse" class="btn btn-lg btn-success btn-block">Update Course Record</button>
                    <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                      <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                      Loading...      
                    </button>
                  </div>
                </form>
              </div>            
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'

  export default {
    name: "CreateCourse",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        isLoading: false,
        active: 'registered institutions',
        institutionID: null,
        course: {},
        courseID: null,
        institution: {},
        employees: [],
        form: {
          courseID: "",
          title: "",
          code: "",
          duration: "",
          durationUnit: "",
          description: "",
          instructor: "",
          grade: ""
        },
        errors: {
          courseID: "",
          title: "",
          code: "",
          duration: "",
          durationUnit: "",
          description: "",
          instructor: "",
          grade: ""
        }
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      validateForm() {
        if (!this.form.courseID) {
            this.errors.courseID = "Course ID is required!"
        }
        if (!this.form.title) {
            this.errors.title = "Course title is required!"
        }
        if (!this.form.grade) {
            this.errors.grade = "Grade/Year is required!"
        }
        if (!this.form.code) {
            this.errors.code = "Course code is required!"
        }
        if (!this.form.duration) {
            this.errors.duration = "Duration is required!"
        }

        if (!this.form.durationUnit) {
            this.errors.durationUnit = "Duration unit is required!"
        }

        if (!this.form.instructor) {
            this.errors.instructor = "Course instrctor By is required!"
        }

        if (!this.form.description) {
            this.errors.description = "Description is required!"
        }

        if(this.form.courseID && this.form.title && this.form.grade && this.form.code && this.form.duration && this.form.durationUnit && this.form.instructor && this.form.description) {
            return true
        } else {
            return false
        }
      },

      attemptCreatingCourse() {
        this.isLoading = true
        if (!this.validateForm()) {
          this.isLoading = false; 
          return;
        }
        
         if (confirm(`Do you want proceed with ${this.form.title} course record update?`)) {
              db.collection("courses").where('courseID', '==', this.courseID).get()
                .then( querySnapshot => {
                  querySnapshot.forEach( doc => {
                    doc.ref.update({
                        'title': this.form.title,
                        'grade': this.form.grade,
                        'code': this.form.code,
                        'duration': this.form.duration,
                        'durationUnit': this.form.durationUnit,
                        'instructor': this.form.instructor,
                        'description': this.form.description,
                    })

                    this.isLoading = false
                    this.$router.push({
                        name: 'InstitutionCourseDetails',
                        params: {
                            institutionID: this.institutionID,
                            courseID: this.courseID
                        }
                    });
                })
            }).catch(error => {
            this.isLoading = false
            alert(error)
            });
         }
      },

      updateErrorStatusOnInput(fieldName) {
        if(this.form[fieldName].toString().length > 0){
          this.errors[fieldName] = ""
          this.errors.message = ""
        }
      },      
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    created() {
      this.institutionID = this.$route.params.institutionID
      this.courseID = this.$route.params.courseID

      db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

      db.collection("institution_staffs").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              if (doc.data().userRole !== 'institution administrator') {
                const data = {
                  'id': doc.id,
                  'userID': doc.data().userID,
                  'institutionID': doc.data().institutionID,
                  'fullname': doc.data().fullname,
                  'email': doc.data().email,
                  'userRole': doc.data().userRole,
                  'accountType': doc.data().accountType,
                  'address': doc.data().address,
                  'phone': doc.data().phone,
                  'gender': doc.data().gender,
                  'dob': doc.data().dob,
                  'avatar': doc.data().avatar
                }                                                                                                                                               
                this.employees.push(data)
              }
            })
          })

        db.collection("courses").where("courseID", "==", this.courseID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              this.form.courseID = doc.data().courseID
              this.course.courseID = doc.data().courseID
              this.form.institutionID =  doc.data().institutionID
              this.course.institutionID =  doc.data().institutionID
              this.form.title = doc.data().title
              this.course.title = doc.data().title
              this.form.grade = doc.data().grade
              this.course.grade = doc.data().grade
              this.form.code = doc.data().code
              this.course.code = doc.data().code
              this.form.duration = doc.data().duration
              this.course.duration = doc.data().duration
              this.form.durationUnit = doc.data().durationUnit
              this.course.durationUnit = doc.data().durationUnit
              this.form.instructor = doc.data().instructor
              this.course.instructor = doc.data().instructor
              this.form.description = doc.data().description
              this.course.description = doc.data().description                                                                                                                                   
            })
          })
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
