<template>
  <div class="main-wrapper account-wrapper">
    <div class="account-page">
      <div class="account-center">
        <div class="account-box">
          <form class="form-signin">
            <div class="account-logo">
              <router-link 
                :to="{ name: 'AdministratorLogin' }"
              >
                <div class="mb-2"><i class="fas fa-university fa-5x"></i></div>
                <div class="h4">Institution Login</div>
              </router-link>
            </div>
            <div class="form-group">
              <label>Institution</label>
              <select @change="updateErrorStatusOnInput('institutionID')" class="form-control select" v-model="form.institutionID">
                <option value="">Select Institution</option>
                <option 
                  v-for="institution in institutions" 
                  :key="institution.institutionID"
                  :value="institution.institutionID"
                >{{institution.name}}</option>
              </select>
              <small v-if="errors.institutionID" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.institutionID}}</small>
            </div>
            <div class="form-group">
              <label>Email</label>
              <input @input="updateErrorStatusOnInput('email')" type="text" autofocus="" class="form-control" v-model="form.email">
              <small v-if="errors.email" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.email}}</small>
            </div>
            <div class="form-group">
              <label>Password</label>
              <input @input="updateErrorStatusOnInput('password')" type="password" class="form-control" v-model="form.password">
              <small v-if="errors.password" class="form-text text-danger d-flex justify-content-left mb-0">{{errors.password}}</small>
            </div>
            <div class="form-group text-right">
              <!-- <a href="forgot-password.html">Forgot your password?</a> -->
            </div>
            <div class="form-group text-center">
              <button v-if="!isLoading" @click.prevent="attemptLogin" class="btn btn-lg btn-success btn-block">Login</button>
              <button v-else :disabled="isLoading" class="btn btn-lg btn-success btn-block">
                <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                Loading...      
              </button>
            </div>
            <div class="form-group text-center">
              <router-link to="/" class="btn btn-outline-secondary"><span class="mb-2"><i class="fas fa-arrow-circle-left mr-2"></i></span>Back</router-link>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import { db, auth } from '../../firebase/firebaseInit'

    export default {
      name: "Login",

      data() {
        return {
          isLoading: false,
          institutions: [],
          form: {
            institutionID: "",
            email: "",
            password: "",   
          },
          errors: {
            institutionID: "",
            email: "",
            password: "",   
            message: ""
          },

          loggedUserDetails: {},
          expectedAccountType: 'institution staff'
        }
      },

      methods: {
        attemptLogin() {
          this.isLoading = true
          if (!this.validateForm()) {
            this.isLoading = false; 
            return;
          }
          auth.signInWithEmailAndPassword(this.form.email, this.form.password)
            .then( response => {
              console.log(`Login Response: ${response}`);
              db.collection("institution_staffs").where('email', '==', this.form.email).get()
                .then(querySnapshot => {
                    querySnapshot.forEach( doc => {
                      this.loggedUserDetails.userID = doc.data().userID
                      this.loggedUserDetails.institutionID = doc.data().institutionID
                      this.loggedUserDetails.fullname = doc.data().fullname
                      this.loggedUserDetails.email = doc.data().email
                      this.loggedUserDetails.userRole = doc.data().userRole
                      this.loggedUserDetails.accountType = doc.data().accountType
                      this.loggedUserDetails.address = doc.data().address
                      this.loggedUserDetails.gender = doc.data().gender
                      this.loggedUserDetails.dob = doc.data().dob,
                      this.loggedUserDetails.avatar = doc.data().avatar
                    })
                    this.$store.dispatch('UPDATE_LOGIN', {loggedUser: this.loggedUserDetails})
                    .then(() => {
                      if (
                        this.loggedUserDetails.institutionID === this.form.institutionID &&
                        this.loggedUserDetails.accountType === this.expectedAccountType
                      ) {
                        this.$router.push({ name: "InstitutionalDashboard", params: { institutionID: this.loggedUserDetails.institutionID }})
                      } else {
                        this.form.institutionID = ""
                        this.form.email = ""
                        this.form.password = ""
                        alert(`You are NOT a staff of the selected institution. Please login using the valid credentials!!!`);
                        this.attemptLogout()
                      }
                      this.isLoading = false
                    }).catch(err => { console.log('INSTITUTION LOGIN STORE ERROR:', err ); this.isLoading = false })  
                })
                this.isLoading = false
            },
            err => {
                this.isLoading = false
                alert(err.message)
            })
        },

        attemptLogout() {
          auth.signOut().then(() => {
            this.$store.dispatch('ATTEMPT_LOGOUT')
              .then(() => {
              this.$router.push({
                  name: "InstitutionalLogin"
              })
            }).catch((err) => { console.log(err); })
          })
        },

        validateForm() {
          if (!this.form.institutionID) {
              this.errors.institutionID = "Institution is required!"
          }
          if (!this.form.email) {
              this.errors.email = "Email is required!"
          }
          if (!this.form.password) {
              this.errors.password = "Password is required!"
          }
          if(this.form.email && this.form.password) {
              return true
          } else {
              return false;
          }
        },

        updateErrorStatusOnInput(fieldName) {
          if(this.form[fieldName].toString().length > 0){
            this.errors[fieldName] = ""
            this.errors.message = ""
          }
        },
      },

      created() {
        db.collection("institutions").orderBy("name").get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              const data = {
                'id': doc.id,
                'institutionID': doc.data().institutionID,
                'name': doc.data().name,
                'avatar': doc.data().avatar,
                'email': doc.data().email,
                'phone': doc.data().phone,
                'registrationNumber': doc.data().registrationNumber,
                'address': doc.data().address,
                'country': doc.data().country,
                'stateProvince': doc.data().stateProvince,
                'city': doc.data().city,
                'accountType': doc.data().accountType,
                'status': doc.data().status,
                'createdOn': doc.data().createdOn
              }
              this.institutions.push(data)
            })
          })
      }
    }
</script>

<style lang="scss" scoped>
    @import url("../../assets/css/style.css");
    @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

    .account-wrapper {
      min-height: 100vh;

      display: flex;
      align-items: center;
      justify-content: center;
    }
</style>
