<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header :institution="institution"></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
          <div class="row">
              <div class="col-sm-4 col-3">
                  <h4 class="page-title"><strong>{{loggedUserFullname}}</strong>'s Examination Board</h4>
              </div>
              <div class="col-sm-8 col-9 text-right m-b-20">
                  <router-link
                    class="btn btn-success btn-rounded float-right"
                    :to="{
                      name: 'ExaminationLogs',
                      params: {
                        institutionID: institutionID,
                        studentID: studentID
                      }                                                
                    }"
                  ><i class="fas fa-user-edit mr-2"></i>Examination Logs</router-link>
              </div>
          </div>
          <div v-if="isEmptyObject(examinationSchedule)" class="row">
              <div class="col-sm-8 offset-sm-2 mt-5">
                <div class="jumbotron text-center">
                    <h3 class="h3">Examination Time Table is NOT available!</h3> 
                </div>
              </div>
          </div>
          <div v-if="!isEmptyObject(examinationSchedule)" class="row">
            <div class="col-md-8 offset-md-2 mb-4">
              Lorem ipsum dolor, sit amet consectetur adipisicing elit. Aspernatur rerum quibusdam tenetur fugiat praesentium veritatis? Animi veritatis perferendis harum cum, quo nihil quam alias mollitia itaque quidem dolore voluptatem tempore.
            </div>
            <ul style="list-style: none; width: 100%; margin: 0px; padding: 0px;">
              <li v-for="(day, key, index)  in examinationSchedule" :key="`${index} ${key}`">
                <h3 class="h4 text-center" style="text-transform: capitalize;">{{ processExaminationDate(key) }}</h3>
                <ul style="list-style: none; width: 100%; margin: 0px; padding: 0px;">
                  <li 
                    class="col-md-8 offset-md-2"   
                    v-for="(examination, paperIndex) in day" 
                    :key="paperIndex"
                  >
                    <div 
                      class="card"
                      :class="{
                        'can-write' : (isExaminationPaperDay(processExaminationDate(key)) && isActiveExaminationTimeWindow(examination)), 
                        'can-not-write': !(isExaminationPaperDay(processExaminationDate(key)) && isActiveExaminationTimeWindow(examination)),
                        'has-written': !isEmptyObject(studentExaminationGrade[examination.courseID])
                      }" 
                    >
                      <div class="card-body">
                        <h5 class="card-title">{{getCourseRecordByID(examination.courseID).title}} ({{getCourseRecordByID(examination.courseID).code}})</h5>
                        <h6 class="card-subtitle mb-2 text-muted">Start: {{examination.start}}  -  End: {{examination.end}}</h6>
                        <p class="card-text">{{getExaminationStateDescription(examination, processExaminationDate(key))}}</p>
                        <a  
                          class="btn btn-block btn-lg"
                          :class="{
                            'btn-success': (isExaminationPaperDay(processExaminationDate(key)) && isActiveExaminationTimeWindow(examination)),
                            'btn-danger': !(isExaminationPaperDay(processExaminationDate(key)) && isActiveExaminationTimeWindow(examination)),
                            
                          }"
                          @click.prevent="attemptToTakeExamination(examination, (isExaminationPaperDay(processExaminationDate(key)) && isActiveExaminationTimeWindow(examination)))"
                          :style="{
                              'display': !isEmptyObject(studentExaminationGrade[examination.courseID]) ? 'none' : 'block'
                            }"
                        >
                          <strong class="text-white h4">
                            <i v-if="!(isExaminationPaperDay(processExaminationDate(key)) && isActiveExaminationTimeWindow(examination))" class="fa fa-times mr-3"></i>
                            <i v-if="(isExaminationPaperDay(processExaminationDate(key)) && isActiveExaminationTimeWindow(examination))" class="fa fa-check mr-3"></i>
                            {{(isExaminationPaperDay(processExaminationDate(key)) && isActiveExaminationTimeWindow(examination)) ? 'Proceed to Take Examination' : 'Not Available'}}
                          </strong>
                        </a>
                        <button 
                          type="button" 
                          class="btn btn-block"
                          :style="{
                              'display': !isEmptyObject(studentExaminationGrade[examination.courseID]) ? 'block' : 'none'
                            }"
                        ><span class="h3">DONE</span></button>
                      </div>
                    </div>
                  </li>
                </ul>
              </li>
            </ul>
          </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'
  import { isEmptyObject } from '../../helpers/isEmptyObject'
  import { customizeDate, customizeTime } from '../../helpers/customizeDate'

  export default {
    name: "Employees",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'registered institutions',
        institutionID: null,
        studentID: null,
        classroomID: "",
        institution: {},
        classroom: {}, 
        classroomCourses: [],
        timetable: {},
        examination: {},
        courses: [],
        examinationSchedule: {},
        studentExaminationGrade: {}
      }
    },

    computed: {
      loggedUser() {
        return this.$store.getters.loggedUser
      },

      loggedUserFullname() {
        return this.loggedUser.fullname
      },

      getObjectKeys() {
        if (isEmptyObject(this.examinationSchedule)) {
          return []
        } else {
          return Object.keys(this.examinationSchedule)
        }
      },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      }, 

      processExaminationDate(dateString) {
        return dateString.split(".")[1].trim().toUpperCase()
      },

      getCourseRecordByID(courseID) {
        let record = null
        this.courses.map(course => {
          if (course.courseID === courseID) {
            record = course
          }
        })
        return record
      },

      customizeDate(timestamp) {
        return customizeDate(timestamp)
      },

      customizeTime(timestamp) {
        return customizeTime(timestamp)
      },

      isEmptyObject(obj) {
        return isEmptyObject(obj)
      },

      isExaminationPaperDay(processedDate) {
        console.log(processedDate)
        // let processedDateArray = processedDate.split(" "); processedDateArray.splice(0, 1);processedDateArray = processedDateArray.join(" ").trim().toLowerCase()
        // let today = this.customizeDate(new Date().getTime()).toLowerCase()
        // return processedDateArray === today
        return true
      },

      isActiveExaminationTimeWindow(examination) {
        console.log(examination)
        // let currentHr = parseInt(customizeTime(new Date().getTime()).toString().split(":")[0], 10)
        // let startHr = parseInt(examination.start.split(":")[0], 10)
        // let endHr = parseInt(examination.end.split(":")[0], 10)
        // return (currentHr >= startHr) && (currentHr <= endHr)
        return true
      },

      getExaminationStateDescription(examination, processedDate) {
        if (this.isExaminationPaperDay(processedDate)) {
          if (this.isActiveExaminationTimeWindow(examination)) {
            return `${this.getCourseRecordByID(examination.courseID).title} (${this.getCourseRecordByID(examination.courseID).code}) has STARTED, proceed to write your paper.`
          } else {
            return `${this.getCourseRecordByID(examination.courseID).title} (${this.getCourseRecordByID(examination.courseID).code}) is to be Written TODAY. The examination will be open from ${examination.start} to ${examination.end}, please be here on time.`
          }
        } else {
          return `${this.getCourseRecordByID(examination.courseID).title} (${this.getCourseRecordByID(examination.courseID).code}) is NOT available yet.`
        }
      },

      attemptToTakeExamination(examination, isActiveExaminationWindow) {
        if (isActiveExaminationWindow) {
          let isAuthorizedStudent = false
          db.collection("classroom_students").where("studentID", "==", this.studentID).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                if (doc.data().classroomID) {
                  isAuthorizedStudent = true
                }
              })
              if (isAuthorizedStudent) {
                const examinationParams = {
                  examination: examination,
                  firstViewIndex: null,
                  currentViewIdex: null,
                  lastViewIndex: null,
                  examinationStarted: false,
                  examinationEnded: false,
                  examinationStartTime: null,
                  examinationDurationInMinutes: this.getExamationDuration(examination),
                  examinationEndTime: null,
                  examinationStatus: 'Not Started',
                  examinationDetails: null,
                  institutionID: this.institutionID,
                  studentID: this.studentID,
                  classroomID: this.classroomID,
                  examinationID: this.examination.examinationID,
                  timetableID: this.timetable.timetableID,
                  courseID: examination.courseID,
                  questionID: "",
                  countDownDate: `${customizeDate(parseInt(examination.paperID.split("-")[0]))} ${examination.start}`
                }

                this.$store.dispatch('UPDATE_EXAMINATION_STATE', examinationParams)
                .then(() => {
                  this.$router.push({
                    name: "StudentSelectedCourseExaminationPage",
                    params: {
                      institutionID: this.institutionID,
                      studentID: this.studentID,
                      classroomID: this.classroomID,
                      examinationID: this.examination.examinationID,
                      timetableID: this.timetable.timetableID,
                      courseID: examination.courseID
                    }
                  })
                }).catch(() => {
                  alert("Examination configuration failed. Please try again.")
                })
              } else {
                alert("You are NOT eligible to take this examination.")
              }
          })
        } else {
          alert("Examination is NOT available at this time!")
        }
      },

      getExamationDuration(examination) {
        let startTimeArray = examination.start.split(":")
        let endTimeArray = examination.end.split(":")
        let diffMins = 0

        let diffHrs = parseInt(endTimeArray[0]) - parseInt(startTimeArray[0])
        if (parseInt(endTimeArray[1]) < parseInt(startTimeArray[1])) {
          diffHrs -= 1
          diffMins = (60 +parseInt(endTimeArray[1])) - parseInt(startTimeArray[1])
        } else {
          diffMins = parseInt(endTimeArray[1]) - parseInt(startTimeArray[1])
        }

        return (60 * diffHrs) + diffMins
      },

      initialization() {
        db.collection("institutions").where("institutionID", "==", this.$route.params.institutionID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.institution.id = doc.id
            this.institution.institutionID = doc.data().institutionID
            this.institution.name = doc.data().name
            this.institution.avatar = doc.data().avatar
            this.institution.email = doc.data().email
            this.institution.phone = doc.data().phone
            this.institution.registrationNumber = doc.data().registrationNumber
            this.institution.address = doc.data().address
            this.institution.country = doc.data().country
            this.institution.stateProvince = doc.data().stateProvince
            this.institution.city = doc.data().city
            this.institution.accountType = doc.data().accountType
            this.institution.status = doc.data().status
            this.institution.createdOn = doc.data().createdOn
          })
        })

        db.collection("courses").where("institutionID", "==", this.$route.params.institutionID).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                  'id': doc.id,
                  'courseID': doc.data().courseID,
                  'institutionID': doc.data().institutionID,
                  'title': doc.data().title,
                  'grade': doc.data().grade,
                  'code': doc.data().code,
                  'duration': doc.data().duration,
                  'durationUnit': doc.data().durationUnit,
                  'instructor': doc.data().instructor,
                  'description': doc.data().description,
                  'createdOn': doc.data().createdOn
                }                                                                                                                                       
                this.courses.push(data)
              })
            })

        db.collection("classroom_students").where("studentID", "==", this.studentID).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
                this.classroomID = doc.data().classroomID
            })
            if (this.classroomID) {
              db.collection("classrooms").where("classroomID", "==", this.classroomID).get()
              .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.classroom.id = doc.id
                    this.classroom.classroomID = doc.data().classroomID
                    this.classroom.institutionID = doc.data().institutionID
                    this.classroom.name = doc.data().name
                    this.classroom.grade = doc.data().grade
                    this.classroom.capacity = parseInt(doc.data().capacity)
                    this.classroom.numberOfStudentsAssigned = doc.data().numberOfStudentsAssigned
                    this.classroom.overSeenBy = doc.data().overSeenBy
                    this.classroom.createdOn = doc.data().createdOn
                  })
                })

              db.collection("classroom_courses").where("classroomID", "==", this.classroomID).get()
              .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                      'id': doc.id,
                      'classroomID': doc.data().classroomID,
                      'courseID': doc.data().courseID,
                    }                                                                                       
                    this.classroomCourses.push(data)
                  })
                })

              db.collection("examination_timetables").where("classroomID", "==", this.classroomID).get()
              .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.timetable.timetableID = doc.data().timeTableID
                    this.timetable.classroomID = doc.data().classroomID
                    this.timetable.examinationID = doc.data().examinationID
                    this.timetable.details = JSON.parse(doc.data().details)
                    this.timetable.startHour = doc.data().startHour
                    this.timetable.maxNumExaminationPerDay = doc.data().maxNumExaminationPerDay
                    this.timetable.maxExaminationDuration = doc.data().maxExaminationDuration
                    this.timetable.firstBreak = doc.data().firstBreak
                    this.timetable.secondBreak = doc.data().secondBreak
                    this.timetable.timeTableDescription = doc.data().timeTableDescription
                    this.timetable.createdOn = doc.data().createdOn                                                        
                  })
                    if (this.timetable.examinationID) {
                      db.collection("examinations").where("examinationID", "==", this.timetable.examinationID).get()
                      .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            this.examination.examinationID = doc.data().examinationID
                            this.examination.institutionID = doc.data().institutionID
                            this.examination.title = doc.data().title
                            this.examination.academicSession = doc.data().academicSession
                            this.examination.examinationStatus = doc.data().examinationStatus
                            this.examination.startDate = doc.data().startDate
                            this.examination.endDate = doc.data().endDate
                            this.examination.examinationDescription = doc.data().examinationDescription
                            this.examination.createdOn = doc.data().createdOn
                          })
                        })

                      this.timetable.details.map((day, dayIndex) => {
                        let keyStringHolder = ""
                        day.map((examination, index) => {
                          if (index <= 0) {
                            keyStringHolder = `${(dayIndex+1)}. ${examination.weekday} ${examination.date}`
                            this.examinationSchedule[keyStringHolder] = []
                          } else {
                            if (examination.courseID) {
                              this.examinationSchedule[keyStringHolder].push(examination)

                              db.collection("student_examination_grades").where("studentID", "==", this.studentID).get()
                              .then(querySnapshot => {
                                querySnapshot.forEach(doc => {
                                  if (doc.data().institutionID === this.institutionID && doc.data().classroomID === this.classroomID && doc.data().examinationID === this.examination.examinationID  && doc.data().timetableID === this.timetable.timetableID && doc.data().courseID === examination.courseID) {
                                    const data = {
                                      'studentExaminationGradeID' : doc.data().studentExaminationGradeID,
                                      'institutionID' : doc.data().institutionID,
                                      'studentID' : doc.data().studentID,
                                      'classroomID' : doc.data().classroomID,
                                      'examinationID' : doc.data().examinationID,
                                      'timetableID' : doc.data().timetableID,
                                      'courseID' : doc.data().courseID,
                                      'questionID' : doc.data().questionID,
                                      'examinationDetails' : JSON.parse(doc.data().examinationDetails),
                                      'correctAnswer' : doc.data().correctAnswer,
                                      'wrongAnswe' :  doc.data().wrongAnswer,
                                      'score' : doc.data().score,
                                      'percentageScore' : doc.data().percentageScore,
                                      'grade' : doc.data().grade,
                                      'createdOn' : doc.data().createdOn
                                    }
                                    this.studentExaminationGrade[examination.courseID] = data
                                  }
                                })
                              })
                            }
                          }
                        })
                      })
                    }
                })
            }
          })   
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
    },

    async created() {
      this.institutionID = this.$route.params.institutionID
      this.studentID = this.$route.params.studentID

        await this.initialization()
    },
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }

  .can-write { background: #d4edda; }
  .can-not-write { background: #f8d7da; }
  .has-written { background: #d6d8db; }
</style>
