<template>
  <div class="notification-box">
    <div class="msg-sidebar notifications msg-noti">
        <div class="topnav-dropdown-header">
            <span>Messages</span>
        </div>
        <div class="drop-scroll msg-list-scroll" id="msg_list">
            <ul class="list-box">
                <li
                    v-for="(message, index) in messages"
                    :key="index"
                >
                    <router-link
                        :to="{
                            name: 'InstitutionChats',
                            params: {
                                institutionID: institutionID,
                                chatMateID: message.author.userID
                            }
                        }"
                    >
                        <div class="list-item">
                            <div class="list-left">
                                <span class="avatar">{{getAvatar(message.author.fullname)}}</span>
                            </div>
                            <div class="list-body">
                                <span class="message-author">{{message.author.fullname}}</span>
                                <span class="message-time">{{message.time}}</span>
                                <div class="clearfix"></div>
                                <span class="message-content">{{message.content}}</span>
                            </div>
                        </div>
                    </router-link>
                </li>
            </ul>
        </div>
        <div class="topnav-dropdown-footer">
            <a href="chat.html">See all messages</a>
        </div>
    </div>
  </div>
</template>

<script>
    import { getAbbreviatedAppName } from '../../../helpers/getAbbreviatedAppName'
    export default {
      name: "PageMessages",

      data() {
          return {
              institutionID: null,
          }
      },

      computed: {
          messages() {
              return this.$store.getters.messages
          }
      },

      methods: {
          getAvatar(author) {
              return getAbbreviatedAppName(author)
          }
      },

      created() {
          this.institutionID = this.$route.params.institutionID
      }
    }
</script>

