<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper">
        <div class="content">
            <div class="row">
                <div class="col-sm-12">
                    <h4 class="page-title">Activities</h4>
                </div>
            </div>
            <div class="row">
                <div class="col-md-12">
                    <div class="activity">
                        <div class="activity-box">
                            <ul class="activity-list">
                                <li v-for="activity in activities" :key="activity.id">
                                    <div class="activity-user">
                                        <a href="#" :title="getUserFullnameByID(activity.userID)" data-toggle="tooltip" class="avatar">
                                            <img :alt="getUserFullnameByID(activity.userID)" src="assets/img/user.jpg" class="img-fluid rounded-circle">
                                        </a>
                                    </div>
                                    <div class="activity-content">
                                        <div class="timeline-content">
                                            <a href="#" class="name">{{getUserFullnameByID(activity.userID)}}</a> {{activity.connectorPhrase}} <a href="#">{{activity.subject}}</a>
                                            <span class="time"><span class="mr-5">{{customizeDate(activity.createdAt)}}</span>  {{customizeTime(activity.createdAt)}}</span>
                                        </div>
                                    </div>
                                    <!-- <a class="activity-delete" href="#" title="Delete">&times;</a> -->
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import PageMenu from './common/PageMenu'
  import PageMessages from './common/PageMessages'

  import { db } from '../../firebase/firebaseInit'
  import { customizeDate, customizeTime } from '../../helpers/customizeDate'
  export default {
    name: "AdministratorActivity",
    components: {
      'page-header': PageHeader,
      'page-menu': PageMenu,
      'page-messages': PageMessages
    },

    data() {
      return {
        active: 'activities',
        administratorID: '',
        activities: [],
        users: []
      }
    },

    computed: {
      appName() {
        return this.$store.getters.appName
      },

      appNameAbbr() {
        return this.$store.getters.appNameAbbr
      },

      loggedUserDetails() {
        return this.$store.getters.loggedUser;
      },

      isSuperUser() {
        return this.loggedUserDetails.userRole === "super user"
      }
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      getUserFullnameByID(userID) {
        let name = "Unknown"
        this.users.map(user => {
          if (user.userID === userID) {
            name = user.fullname
          }
        })
        return name
      },

      customizeDate(timestamp) {
        return customizeDate(timestamp)
      }, 
      
      customizeTime(timestamp) {
        return customizeTime(timestamp)
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);

      if (!this.isSuperUser) {
        alert("Access denied. You are not permitted to view the requested resource.")
        this.$router.push({
          name: 'AdministratorDashboard',
          params: {
            administratorID: this.administratorID
          }
        })
      }
    },

    created() {
      this.administratorID = this.$route.params.administratorID

      db.collection("administrator_activities").orderBy("createdAt").get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'userID': doc.data().userID,
              'connectorPhrase': doc.data().connectorPhrase,
              'subject': doc.data().subject,
              'action': doc.data().action,
              'createdAt': doc.data().createdAt
            }
            this.activities.push(data)
          })
        })


      db.collection("system_administrators").orderBy("fullname").get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'userID': doc.data().userID,
              'fullname': doc.data().fullname,
              'email': doc.data().email,
              'userRole': doc.data().userRole,
              'accountType': doc.data().accountType
            }
            this.users.push(data)
          })
        })
    }
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
