<template>
<div class="main-viewer">
  <div class="main-wrapper">
    <page-header></page-header>
    <page-menu :active="active"></page-menu>
    <div class="page-wrapper" style="min-height: 100vh;">
<div class="chat-main-row">
    <div class="chat-main-wrapper">
        <div class="col-lg-9 message-view chat-view">
            <chat-window :chatMateID="$route.params.chatMateID"></chat-window>
        </div>
        <chat-profiletab></chat-profiletab>
    </div>
</div>
<div v-if="showAddUserDialog" id="add_chat_user" class="modal fade" :class="{ 'show': showAddUserDialog }" :style="{ 'display': displayOption }" role="dialog">
    <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
            <div class="modal-header">
                <h3 class="modal-title">Find Chat Mate</h3>
				<button @click.prevent="togglePrivateChatSearchView" type="button" class="close" data-dismiss="modal">&times;</button>
            </div>
            <div class="modal-body">
                <div class="input-group m-b-30">
                    <input @input.prevent="generateProspectiveChatMateList" v-model="searchQuery" placeholder="Search to start a chat" class="form-control search-input" type="text">
                </div>
                <div>
                    <h5 v-if="showProspectiveChatMate">Prospective Chat Mate</h5>
                    <ul class="chat-user-list">
                        <li v-if="!showProspectiveChatMate">
                            <div class="jumbotron">
                                <h4 class="h4 text-center">Enter user name to find a chat mate.</h4>
                            </div>
                        </li>
                        <li v-for="user in generatedSearchOutput" :key="user.accountID">
                            <a
                                @click.prevent="chatWithSelectedProspectiveChatMate(user.accountID)"
                                style="cursor: pointer;"
                            >
                                <div class="media">
									<span class="avatar align-self-center">J</span>
                                <div class="media-body align-self-center text-nowrap">
                                    <div class="user-name">{{getUserDatials(user.userID).fullname}}</div>
                                    <span class="designation" style="text-transform: capitalize;">{{getUserDatials(user.userID).userRole}}</span>
                                </div>
                                <!-- <div class="text-nowrap align-self-center"> -->
                                    <!-- <div class="online-date">1 day ago</div> -->
                                <!-- </div> -->
                                </div>
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</div>
        <page-messages></page-messages>
    </div>
  </div>
  <div class="sidebar-overlay" data-reff=""></div>
  <div v-if="showAddUserDialog" class="modal-backdrop fade" :class="{ 'show': showAddUserDialog }"></div>
</div>
</template>

<script>
  import PageHeader from './common/PageHeader'
  import ChatMenu from './common/ChatMenu'
  import PageMessages from './common/PageMessages'
  import ChatProfileTab from './ChatProfileTab'
  import ChatWindow from './ChatWindow'
  

  import { db } from '../../firebase/firebaseInit'

  export default {
    name: "AdministratorChats",
    components: {
      'page-header': PageHeader,
      'page-menu': ChatMenu,
      'page-messages': PageMessages,
      'chat-profiletab': ChatProfileTab,
      'chat-window': ChatWindow
    },

    data() {
      return {
        active: 'chats',
        administratorID: "",
        showAddUserDialog: false,
        displayOption: 'none',
        users: [],
        generatedSearchOutput: [],
        searchQuery: ""
      }
    },

    computed: {
      appName() {
        return this.$store.getters.appName
      },

      appNameAbbr() {
        return this.$store.getters.appNameAbbr
      },

      showProspectiveChatMate() {
          return this.generatedSearchOutput.length > 0 ? true : false
      },

        prospectChatChannels() {
            return this.$store.getters.prospectChatChannels
        },
    },

    methods: {
      toggleMessageBox() {
        if (document.querySelector(".main-wrapper").classList.contains('open-msg-box')) {
          document.querySelector(".main-wrapper").classList.remove('open-msg-box')
        } else {
          document.querySelector(".main-wrapper").classList.add('open-msg-box')
        }
      },

      toggleMobileMenu() {
        if (document.querySelector(".main-wrapper").classList.contains('slide-nav')) {
          document.querySelector(".main-wrapper").classList.remove('slide-nav')
          document.querySelector(".sidebar-overlay").classList.remove('opened')
        } else {
          document.querySelector(".main-wrapper").classList.add('slide-nav')
          document.querySelector(".sidebar-overlay").classList.add('opened')
        }
      },

      togglePrivateChatSearchView() {
            this.showAddUserDialog = !this.showAddUserDialog
            if (this.showAddUserDialog) {
                this.displayOption = 'block'
            } else {
                this.displayOption = 'none'
                this.generatedSearchOutput = []
                this.searchQuery = ""
            }
      },

      generateProspectiveChatMateList() {
          const searches = []

          this.prospectChatChannels.map(user => {
            const targetUser = this.getUserDatials(user.userID)
            if (targetUser) {
                if (targetUser.fullname.toLowerCase().search(this.searchQuery.toLowerCase()) >= 0) {
                    searches.push(user)
                }
            }
          })

           if (!this.searchQuery) {
               this.generatedSearchOutput = []
           } else {
               this.generatedSearchOutput = searches
           }
      },

      chatWithSelectedProspectiveChatMate(accountID) {
          this.togglePrivateChatSearchView()
          this.$router.push({
            name: 'AdministratorChats',
            params: {
              administratorID: this.administratorID,
              chatMateID: accountID
            }                                   
          })
      },

      getUserDatials(userID) {
          let selectedUser = null

          this.users.map(user => {
              if (user.userID === userID) {
                  selectedUser = user
              }
          })

          return selectedUser
      }
    },

    mounted() {
      window.EventBus.on('page-header-message-box', this.toggleMessageBox);
      window.EventBus.on('page-header-mobile-menu', this.toggleMobileMenu);
      window.EventBus.on('toggle-private-chat-search-view', this.togglePrivateChatSearchView);
    },

    created() {
        this.administratorID = this.$route.params.administratorID

        db.collection("institution_staffs").get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id': doc.id,
              'userID': doc.data().userID,
              'institutionID': doc.data().institutionID,
              'fullname': doc.data().fullname,
              'email': doc.data().email,
              'userRole': doc.data().userRole,
              'accountType': doc.data().accountType,
              'address': doc.data().address,
              'phone': doc.data().phone,
              'gender': doc.data().gender,
              'dob': doc.data().dob,
              'avatar': doc.data().avatar
            }                                                                                                                                         
              this.users.push(data)
            })
          })

        db.collection("system_administrators").get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
                'id': doc.id,
                'userID': doc.data().userID,
                'fullname': doc.data().fullname,
                'email': doc.data().email,
                'userRole': doc.data().userRole,
                'accountType': doc.data().accountType,
                'createdOn': doc.data().createdOn
            }                                                                                                        
              this.users.push(data)
            })

            console.log(this.users)
          })
    }
  }
</script>

<style lang="scss" scoped>
  @import url("../../assets/css/style.css");
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

  .main-viewer {
    width: 100%;
    min-height: 100vh;
  }
</style>
